IG$.mgr_dashboard = $s.extend(IG$.v$pnl, {
	scroll: false,
	initialized: false,
	closable: true,
	layout: "fit",
	bodyPadding: 5,
	iconCls: "icon-ing-docdef",
	
	curnode: null,
	cursop: null,
	
	init: function() {
		var me = this;
		
		if (me._cmode == 2)
		{
			me.init_f("BusinessLogic", 0);
		}
		else
		{
			me.init_f("BusinessProcess", 0);
			me.init_f("DashboardResources", 1);
		}
	},
	
	init_f: function(mname, ptype) {
		var me = this,
			mtree,
			m_navi = this.down("[name=m_navi]"),
			req = new IG$._rpc$();
		
		req.init(me, 
			{
				ack: "helpcontent",
				payload: {uid: "/" + mname, name: mname, type: "Folder", post: "create", duty: "everyone", mode: "s"},
				mbody: {option: "translate"}
			}, me, function(jdoc) {
				var me = this,
					tuid = jdoc && jdoc.length ? jdoc[0].uid : null,
					m_1;
					
				if (tuid)
				{
					if (ptype == 0)
					{
						mtree = new IG$.n$navTreeCom({
							name: "mtree",
							rootuid: tuid,
							showCustomMenu: function(rec) {
								var me = this,
									menu = this.customMenu,
									typename = rec.get("type").toLowerCase(),
									itemname = rec.get("name"),
									itemaddr = rec.get("nodepath"),
									itemuid = rec.get("uid"),
									itemtype = rec.get("type"),
									writable = (rec.get("writable") || rec.get("manage")) ? true : false,
									manageable = (rec.get("manage")) ? true : false,
									r = false;
									
								menu.removeAll();
																
								if (writable || manageable)
								{
									r = true;
									menu.add(
										{
											text: IRm$.r1("B_FOLDER"),
											hidden: ig$.features["ig_n_f_f"] !== false,
											handler: function() {
												this._createMetaObject.call(this, "Folder", itemuid, itemaddr, rec);
												  },
												  scope: this
										}
									);
									
									if (typename == "folder")
									{
										menu.add(
											{
												text: IRm$.r1("L_REMOVE"),
												hidden: ig$.features["ig_n_f_f"] !== false,
												handler: function() {
													this.deleteMetaObject.call(this, itemuid, rec);
													  },
													  scope: this
											}
										);
									}
								}
								
								return r;
							},
							custom_expand_func: new IG$.callBackObj(me, me.rs_custom_click_func),
							custom_click_func: new IG$.callBackObj(me, me.rs_custom_click_func)
						});
					
						m_navi.add(mtree);
					}
					else if (ptype == 1)
					{
						m_1 = me.down("[name=m_1]");
						m_1.uid = tuid;
						m_1.init_f.call(m_1);
					}
				}
				
			});
		req.send();
	},
	
	rs_custom_click_func: function(node) {
		var me = this,
			t_btn = me.down("[name=t_btn]");
					
		t_btn.setDisabled(true);
		
		me._s1 = node;
		
		if (node.get("writable") || node.get("manageable"))
		{
			t_btn.setDisabled(false);
		}
		
		me.load_content(node);
	},
	
	load_content: function(node) {
		var panel = this,
			req = new IG$._rpc$();
			
		req.init(panel, 
			{
				ack: "content",
				payload: {uid: node.get("uid")},
				mbody: {option: ''}
			}, panel, function(jdoc) {
				var tnodes = (jdoc) ? jdoc.children : null,
					dp = [],
					g1 = this.down("[name=g1]"),
					i, p;
				
				if (tnodes)
				{
					for (i=0; i < tnodes.length; i++)
					{
						p = tnodes[i];
						
						if ((panel._cmode == 2 && p.type == "JavaClass") || (panel._cmode !=2 && p.type == "ClassModule"))
						{
							dp.push(p);
						}
					}
				}
				g1.store.loadData(dp);
			}, false);
		req.send();
	},
	
	createItem: function(itemtype) {
		var me = this,
			mtree = me.down("[name=mtree]");
		
		if (me._s1)
		{
			mtree._createMetaObject.call(mtree, itemtype, me._s1.get("uid"), me._s1.get("nodepath"), me._s1);
		}
	},
	
	initComponent: function(){
		var me = this;
		
		me.title = IRm$.r1("I_MGR_DBD");
		
		me.items = [
			{
				xtype: "panel",
				layout: "card",
				border: 0,
				defaults: {
					deferredRender: false
				},
				name: "n_c",
				dockedItems: [
					{
						xtype: "toolbar",
						dock: "top",
						hidden: me._cmode == 2,
						items: [
							{
								xtype: "displayfield",
								value: IRm$.r1("L_SEL_MODE")
							},
							"-",
							{
								xtype: "button",
								text: IRm$.r1("L_CLASS_MOD"),
								name: "b_0",
								pressed: 1,
								handler: function() {
									var me = this,
										l = me.down("[name=n_c]").getLayout();
									me.down("[name=b_0]").toggle(true);
									me.down("[name=b_1]").toggle(false);
									me.down("[name=b_2]").toggle(false);
									l.setActiveItem(0);
								},
								scope: this
							},
							{
								xtype: "button",
								name: "b_1",
								text: IRm$.r1("L_RESOURCE"),
								handler: function() {
									var me = this,
										l = me.down("[name=n_c]").getLayout();
									me.down("[name=b_0]").toggle(false);
									me.down("[name=b_1]").toggle(true);
									me.down("[name=b_2]").toggle(false);
									l.setActiveItem(1);
								},
								scope: this
							},
							{
								xtype: "button",
								name: "b_2",
								text: IRm$.r1("L_MENU"),
								handler: function() {
									var me = this,
										l = me.down("[name=n_c]").getLayout();
									me.down("[name=b_0]").toggle(false);
									me.down("[name=b_1]").toggle(false);
									me.down("[name=b_2]").toggle(true);
									l.setActiveItem(2);
								},
								scope: this
							}
						]
					}
				],
				items: [
					{
						xtype: "panel",
						layout: "border",
						name: "m_0",
						border: false,
						title: IRm$.r1("L_CLASS_MOD"),
						items: [
							{
								xtype: "panel",
								layout: "fit",
								region: "west",
								collapsed: false,
								width: 200,
								title: IRm$.r1("L_NAVIGATOR"),
								name: "m_navi",
								tbar: [
									{
										xtype: "button",
										name: "t_btn",
										text: IRm$.r1("L_CREATE_ITEM"),
										disabled: true,
										menu: {
											xtype: "menu",
											items: 
												me._cmode == 2 ? 
												[
													{
														text: IRm$.r1("D_JAVAPACKAGE"),
														hidden: ig$.features["ig_n_f_f"] !== false, 
														handler: function() {
															this.createItem("JavaPackage");
														},
														scope: this
													},
													"-",
													{
														text: IRm$.r1("D_JAVACLASS"),
														hidden: ig$.features["ig_n_jcm"] !== false, 
														handler: function() {
															this.createItem("JavaClass");
														},
														scope: this
													}
												]
												:
												[
													{
														text: IRm$.r1("B_FOLDER"),
														hidden: ig$.features["ig_n_f_f"] !== false, 
														handler: function() {
															this.createItem("Folder");
														},
														scope: this
													},
													"-",
													{
														text: IRm$.r1("D_CLASSMODULE"),
														hidden: ig$.features["ig_n_jcm"] !== false, 
														handler: function() {
															this.createItem("ClassModule");
														},
														scope: this
													}
												]
										}
									},
									{
										xtype: "button",
										iconCls: "icon-refresh",
										tooltip: IRm$.r1("L_REFRESH"),
										handler: function() {
											var me = this,
												mtree = me.down("[name=mtree]"),
												tnode = mtree.getRootNode();
												
											mtree.refreshNode.call(mtree, tnode);
										},
										scope: this
									}
								],
								items: [
									
								]
							},
							{
								xtype: "panel",
								region: "center",
								flex: 1,
								layout: "fit",
								tbar: [
									{
										text: IRm$.r1("L_COMPILE"), // "Compile",
										handler: function() {
											var panel = this,
												g1 = panel.down("[name=g1]"),
												address = {
													items: []
												},
												checked = g1.getSelectionModel().selected,
												rec,
												itemcnt = 0,
												req = new IG$._rpc$();
											
											IG$.confirmMessages(ig$.appname, IRm$.r1("M_CONFIRM_COMPILE"), function(e) {
												if (e == "yes")
												{
													if (checked.length > 0)
													{
														for (i=0; i < checked.length; i++)
														{
															rec = checked.items[i];
															address.items.push({
																uid: rec.get("uid"),
																option: 'compile'
															});
															itemcnt++;
														}
														
														if (itemcnt > 0)
														{
															req.init(panel, 
																{
																	ack: "plugin",
																	payload: address,
																	mbody: {option: "compile"}
																}, panel, function(jdoc) {
																	var i, j,
																		g1 = panel.down("[name=g1]"),
																		rec, uid, st,
																		recmap = {};
																	
																	if (jdoc && jdoc.length)
																	{
																		for (i=0; i < g1.store.data.items.length; i++)
																		{
																			rec = g1.store.data.items[i];
																			
																			recmap[rec.get("uid")] = rec;
																		}
																		
																		for (i=0; i < jdoc.length; i++)
																		{
																			uid = jdoc[i].uid;
																			st = jdoc[i].status;
																			rec = recmap[uid];
																			
																			if (rec)
																			{
																				rec.set("msg", st == "S" ? "Success" : "Error");
																			}
																		}
																	}
																}, null);
															req.send();
														}
													}
												}
											});
										},
										scope: this
									},
									{
										text: IRm$.r1("B_REMOVE"),
										handler: function() {
											var panel = this,
												g1 = panel.down("[name=g1]"),
												address = {items: []},
												checked = g1.getSelectionModel().selected,
												rec,
												itemcnt = 0,
												req = new IG$._rpc$();
											
											IG$.confirmMessages(ig$.appname, IRm$.r1("M_CONFIRM_DELETE"), function(e) {
												if (e == "yes")
												{
													if (checked.length > 0)
													{
														for (i=0; i < checked.length; i++)
														{
															rec = checked.items[i];
															address.items.push({
																uid: rec.get("uid")
															});
															itemcnt++;
														}
														
														if (itemcnt > 0)
														{
															req.init(panel, 
																{
																	ack: "batchcontent",
																	payload: address,
																	mbody: {option: "delete"}
																}, panel, function(xdoc) {
																	var i;
																	for (i=checked.length-1; i>=0; i--)
																	{
																		g1.store.remove(checked.items[i]);
																	}
																}, null);
															req.send();
														}
													}
												}
											});
										},
										scope: this
									}
								],
								items: [
									{
										xtype: "gridpanel",
										name: "g1",
										selType: "checkboxmodel",
										selModel: {
											checkSelector: ".x-grid-cell",
											mode: "MULTI"
										},
										store: {
											xtype: "store",
											fields: [
												"uid", "name", "description", "nodepath", "type", "writable", "msg"
											]
										},
										columns: [
											{
												text: IRm$.r1("B_NAME"),
												dataIndex: "name",
												tdCls: "igc-td-link",
												width: 250
											},
											{
												text: IRm$.r1("B_DESC"),
												dataIndex: "description",
												flex: 1
											},
											{
												text: IRm$.r1("L_MESSAGE"), // "Message",
												dataIndex: "msg",
												width: 80
											},
											{
												xtype: "actioncolumn",
												width: 50,
												menuDisabled: true,
												items: [
													{
														iconCls: "icon-grid-config",
														tooltip: IRm$.r1('L_EDIT'),
														handler: function (grid, rowIndex, colIndex) {
															var grd = grid,
																store = grd.store,
																rec = store.getAt(rowIndex),
																mp = IG$.mainPanel;
																
															if (mp)
															{
																mp.navigateApp.call(mp, rec.get("uid"), rec.get("type").toLowerCase(), rec.get("name"), rec.get("nodepath"), true, rec.get("writable"));
															}
														},
														scope: this
													},
													{
														iconCls: "icon-grid-delete",
														tooltip: IRm$.r1("L_REMOVE"),
														handler: function (grid, rowIndex, colIndex) {
															var me = this,
																store = grid.store,
																rec = store.getAt(rowIndex);
		
															IG$.deleteMetaObject(rec.get("uid"), rec, IG$.callBackObj(me, function() {
																store.removeAt(rowIndex);
															}));
														},
														scope: this
													}
												]
											}
										],
										listeners: {
											cellclick: function(grid, node, c, rec, rindex, e) {
												var mp = IG$.mainPanel;
													
												if (mp && c == 1)
												{
													mp.navigateApp.call(mp, rec.get("uid"), rec.get("type").toLowerCase(), rec.get("name"), rec.get("nodepath"), true, rec.get("writable"));
												}
											},
											scope: this
										}
									}
								]
							}
						]
					},
					new IG$.dashboardRcsMgr({
						cmode: "global",
						title: IRm$.r1("L_RESOURCE"),
						name: "m_1"
					}),
					new IG$.dashboardmenu({
						title : IRm$.r1("L_MENU"),
						name: "m_2"
					})
				]
			}
		];
		
		this.listeners = {
			afterrender: function(tobj) {
				tobj.init.call(tobj);
			}
		}

		IG$.mgr_dashboard.superclass.initComponent.call(this);
	}
});
