IG$.dashboardRcsMgrPreview = $s.extend($s.window, {
	title: IRm$.r1("L_PRV_DATA"),
	modal: true,
	width: 400,
	height: 350,
	layout: "fit",
	
	loadContent: function() {
		var me = this,
			req = new IG$._rpc$()
			
		req.init(me, 
			{ 
				ack: "content",
				payload: {uid: me.uid},
				mbody: {}
			}, me, function(jdoc) {
			}, false);
		req.send();
	},
	
	initComponent: function() {
		var me = this;
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					bodyPadding: 10,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: [
						{
							html: "<div style='height: 200px;overflow: auto;'><img src='" + ig$.servlet + "?sreq=resource&_rcs_=" + me.uid + "&_mts_=" + IG$._g$a + "'></img></div>",
							name: "img_prev"
						}
					]
				}
			],
			buttons: [
				{
					text: IRm$.r1('B_CLOSE'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			]
		});
		IG$.dashboardRcsMgrPreview.superclass.initComponent.call(this);
	},
	listeners: {
		afterrender: function(tobj) {
			if (tobj.uid)
			{
				tobj.loadContent.call(tobj);
			}
		}
	}
});

IG$.dashboardRcsEdit = $s.extend($s.window, {
	title: "Dashboard Resource Config",
	modal: true,
	width: 400,
	height: 350,
	layout: "fit",
	
	loadContent: function() {
		var me = this,
			req = new IG$._rpc$()
			
		req.init(me, 
			{ 
				ack: "content",
				payload: {uid: me.rec.get("uid")},
				mbody: {}
			}, me, function(jdoc) {
				var citem = jdoc.content;
				
				me._citem = citem;
				
				me.down("[name=itemname]").setValue(citem.name);
				me.down("[name=description]").setValue(citem.description);
			}, false);
		req.send();
	},
	
	_confirm: function() {
		var me = this,
			mform = me.down('[name=mform]'),
			orig_filename = me.down("[name=orig_filename]"),
			photo = me.down("[name=photo]"),
			req;

		if (mform.getForm().isValid()) {
			var f = mform.getForm();
			document.charset = "UTF-8";
			var fname = photo.getRawValue();
			if (fname.indexOf("\\") > -1)
			{
				fname = fname.substring(fname.lastIndexOf("\\")+1);
			}
			
			orig_filename.setValue(fname);
			
			mform.submit({
				url: ig$.servlet,
				waitMsg: 'Uploading your data file',
				success: function(fp, o) {
					var node = fp.errorReader.jsonData,
						uid = node && node.result ? node.result.uid : null;
					
					me.processUploadedFile(uid);
					me.__cprog = 0;
				}
			})
		}
		else
		{
			req = new IG$._rpc$();
			req.init(me, 
				{
					ack: "writecontent",
					payload: {
						uid: me.rec.get("uid"),
						name: me.down("[name=itemname]").getValue(),
						description: me.down("[name=description]").getValue()
					},
					mbody: {option: "rename"}
				}, me, function(jdoc) {
					me.rec.set("name", jdoc.name);
					me.close();
				}, function() {
					me._proc = 0;
				});
			req.send();
		}
	},
	
	processUploadedFile: function(uid) {
		var me = this,
			req = new IG$._rpc$(),
			addr = [],
			rec = me.rec;
			
		addr.push({
			uid: uid,
			name: me.down("[name=itemname]").getValue(),
			ouid : rec.get("uid")
		});;
			
		req.init(me, 
			{
				ack: "helpcontent",
				payload: addr,
				mbody: {option: 'reg_rcs', buid: me.buid}
			}, me, function(jdoc) {
				rec.set("name", addr[0].name);
				me.close();
			}, false);
		req.send();
	},
	
	initComponent: function() {
		var me = this;
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					bodyPadding: 10,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: [
						{
							xtype: "textfield",
							name: "itemname",
							_fasl: 1,
							fieldLabel: IRm$.r1("B_NAME")
						},
						{
							xtype: "textarea",
							name: "description",
							fieldLabel: IRm$.r1("B_DESC"),
							height: 50
						},
						{
							xtype: "form",
							name: 'mform',
							layout: "anchor",
							items: [
								{
									xtype: "hiddenfield",
									name: "_mts_",
									value: IG$._g$a
								},
								{
									xtype: "textfield",
									name: "orig_filename",
									hidden: true,
									fieldLabel: IRm$.r1("L_ORIG_NAME"), // "Original name"
								},
								{
									xtype: 'fileuploadfield',
									name: 'photo',
									fieldLabel: IRm$.r1("L_DATA"), // 'Data',
									// labelWidth: 100,
									msgTarget: 'side',
									allowBlank: false,
									anchor: '100%',
									// width: 320,
									buttonText: IRm$.r1("L_SELECT_FILE"),
									buttonConfig: {
										margin: "0 2 0"
									}
								}
							]
						}
					]
				}
			],
			buttons: [
				{
					text: IRm$.r1('B_CONFIRM'),
					handler:function() {
						this._confirm();
					},
					scope: this
				},
				{
					text: IRm$.r1('B_CLOSE'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			]
		});
		
		IG$.dashboardRcsEdit.superclass.initComponent.call(this);
	},
	listeners: {
		afterrender: function(tobj) {
			var mform = tobj.down('[name=mform]');
			mform.getForm().errorReader = new IG$.m2ER();
			tobj.loadContent.call(tobj);
		}
	}
});

IG$.dashboardRcsMgr = $s.extend(IG$.v$pnl, {
	layout: "fit",
	framecontent: null,
	
	confirmSettings: function() {
		var me = this;
		
		if (me.updateSetting())
		{
			me.callback && me.callback.execute();
			
			me.close();
		}
	},
	
	initF: function() {
	},
	
	commitCode: function() {
		var me = this,
			r = 1,
			framecontent = me.framecontent,
			rcs = framecontent ? framecontent.rcs : null,
			i,
			dgrid = me.down("[name=dgrid]"),
			p, rec;
		
		if (framecontent)
		{
			if (rcs.length)
			{	
				rcs.splice(0, rcs.length);
			}
			
			for (i=0; i < dgrid.store.data.items.length; i++)
			{
				rec = dgrid.store.data.items[i];
				
				p = {
					uid: rec.get("uid"),
					name: rec.get("name")
				};
				
				rcs.push(p);
			}
		}
		
		return r;
	},
	
	refreshCode: function() {
	},
		
	init_f: function() {
		var me = this;
		// me.loadResources(9);
	},
	
	toolbarHandler: function(cmd) {
		var me = this;
		
		switch (cmd)
		{
		case "cmd_import":
			me.down("[name=d_file]").show();
			break;
		case "cmd_reload":
			me.loadResources();
			break;
		case "cmd_mgr_rcs":
			break;
		case "cmd_c_files":
			me.registerFiles();
			break;
		}
	},
	
	initApp: function() {
		var me = this,
			d_file = me.down("[name=d_file]"),
			d_file_el = d_file.el.dom,
			m_fileupload = $(".m_fileupload", d_file_el),
			d_upfiles = me.down("[name=d_upfiles]"),
			fileupload, dropzone, d_progress;
			
		m_fileupload.empty();
		
		fileupload = $("<input type='file' name='files[]' data-url='upload' multiple></input>").appendTo(m_fileupload);
		dropzone = $("<div class='filedropzone fade well'>Drop files here</div>").appendTo(m_fileupload);
		d_progress = $("<div class='file-progress'><div class='bar' style='width: 0%;'></div></div>").appendTo(m_fileupload);
		
		fileupload.fileupload({
			url: ig$.servlet,
			dataType: "json",
			done: function (e, data) {
				var jdoc = data.result || {errorcode: "0xffff", errormsg: "Server incorrect responding"},
					errcode = jdoc.errorcode,
					tnodes,
					i, dp = [], p;
				
				if (errcode)
				{
					IG$.ShowErrorMessage(jdoc, me, null);
				}
				else
				{
					if (jdoc.result)
					{
						tnodes = jdoc.result.length ? jdoc.result : [jdoc.result];
						
						for (i=0; i < tnodes.length; i++)
						{
							p = tnodes[i];
							dp.push(p);
						}
					}
				}
				
				if (dp && dp.length)
				{
					d_upfiles.show();
					
					for (i=0; i < dp.length; i++)
					{
						d_upfiles.store.add(dp[i]);
					}
				}
			},
			progressall: function (e, data) {
				var progress = parseInt(data.loaded / data.total * 100, 10),
					bar = $(".bar", d_progress);
				bar.css(
					"width",
					progress + "%"
				);
				bar.text("Loaded " + data.loaded + " / " + data.total);
			},
	 
			dropZone: dropzone
		}).bind('fileuploadsubmit', function (e, data) {
		});
		
		fileupload.fileupload("option", "url", ig$.servlet);
		
		// me.loadResources();
	},
	
	registerFiles: function() {
		var me = this,
			i,
			d_upfiles = me.down("[name=d_upfiles]"),
			dgrid = me.down("[name=dgrid]"),
			store = d_upfiles.store,
			rec, addr = [],
			req = new IG$._rpc$(),
			ds = dgrid.store,
			dmap = {};
		
		for (i=0; i < ds.data.items.length; i++)
		{
			rec = ds.data.items[i];
			dmap[rec.get("name")] = rec.get("uid");
		}
		
		for (i=0; i < store.data.items.length; i++)
		{
			rec = store.data.items[i];
			addr.push({
				uid: rec.get("uid"),
				name: rec.get("filename"),
				ouid : dmap[rec.get("filename")]
			});
		}
		
		if (addr.length)
		{
			req.init(me, 
				{
					ack: "helpcontent",
					payload: addr,
					mbody: {option: 'reg_rcs', buid: me.uid}
				}, me, function(jdoc) {
					me.loadResources(1);
				}, false);
			req.send();
		}
	},
	
	loadResources: function(bmode) {
		var me = this,
			dgrid = me.down("[name=dgrid]"),
			req_g = new IG$._rpc$(),
			mname = "DashboardResources";
		
		if (bmode == 1)
		{
			d_upfiles = me.down("[name=d_upfiles]");
			d_upfiles.store.removeAll();
			d_upfiles.hide();
			me.down("[name=d_file]").hide();
		}
		
		if (!me.gobj)
		{
			req_g.init(me, 
				{ 
					ack: "helpcontent",
					payload: {uid: "/" + mname, name: mname, type: "Folder", post: "create", duty: "everyone", mode: "s"},
					mbody: {option: 'translate'}
				}, me, function(jdoc) {
					if (jdoc && jdoc.length)
					{
						me.gobj = jdoc[0];
						
						me.loadResource(me.gobj.uid, bmode, "global", new IG$.callBackObj(me, function() {
							var me = this;
							if (me.uid && me.cmode == "page")
							{
								me.loadResource(me.gobj.uid, bmode, "page");
							}
						}));
					}
				}, false);
			req_g.send();
		}
		else
		{
			me.loadResource(me.gobj.uid, bmode, "global", new IG$.callBackObj(me, function() {
				var me = this;
				if (me.uid && me.cmode == "page")
				{
					me.loadResource(me.gobj.uid, bmode, "page");
				}
			}));
		}
	},
	
	loadResource: function(muid, bmode, ctype, callback) {
		var me = this,
			req = new IG$._rpc$(),
			dgrid = me.down("[name=dgrid]");
			
		req.init(me, 
			{ 
				ack: "content",
				payload: {uid: muid},
				mbody: {option: 'folder'}
			}, me, function(jdoc) {
				var tnodes = jdoc ? jdoc.children : null,
					i, dp = [], p, rec;
				
				if (tnodes)
				{
					for (i=0; i < tnodes.length; i++)
					{
						p = tnodes[i];
						p.btype = ctype;
						if (p.name.indexOf(".") > -1)
						{
							p.fileext = p.name.substring(p.name.lastIndexOf(".") + 1);
						}
						dp.push(p);
					}
				}
				
				for (i=dgrid.store.data.items.length-1; i>=0; i--)
				{
					rec = dgrid.store.data.items[i];
					
					if (rec.get("btype") == ctype)
					{
						dgrid.store.remove(rec);
					}
				}
				
				for (i=0; i < dp.length; i++)
				{
					dgrid.store.add(dp[i]);
				}
				
				if (bmode == 9)
				{
					me.commitCode();
				}
				
				callback && callback.execute();
				
			}, false);
		req.send();
	},
	
	removeItem: function(rec) {
		var me = this,
			req = new IG$._rpc$(),
			address = {uid: rec.get("uid")},
			dgrid = me.down("[name=dgrid]");
			
		req.init(me, 
			{
				ack: "batchcontent",
				payload: address,
				mbody: {option: "delete"}
			}, me, function(jdoc) {
				dgrid.store.remove(rec);
			}, null);
		req.send();
	},
		
	initComponent : function() {
		var me = this;
		
		$s.apply(this, {
			tbar: [
				{
					iconCls: "icon-toolbar-import",
					tooltip: IRm$.r1("L_IMPORT_FILE"),
					text: IRm$.r1("L_IMPORT_FILE"),
					handler: function() {this.toolbarHandler("cmd_import"); },
					scope: this
				},
				{
					iconCls: "icon-refresh",
					tooltip: "Reload",
					handler: function() {
						this.toolbarHandler("cmd_reload"); 
					},
					scope: this
				}
			],
			items: [
				
				{
					xtype: "container",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: [
						{
							html: "<div class='m_fileupload'></div>",
							name: "d_file",
							height: 120,
							hidden: true,
							bodyPadding: 10
						},
						{
							name: "d_upfiles",
							xtype: "gridpanel",
							height: 120,
							hidden: true,
							selType: "checkboxmodel",
							selModel:  {
								mode: "MULTI"
							},
							store: {
								fields: [
									"uid", "filename", "fileext"
								]
							},
							columns: [
								{
									text: IRm$.r1("L_FILENAME"),
									dataIndex: "filename",
									flex: 1
								},
								{
									text: IRm$.r1("L_FILEEXT"),
									dataIndex: "fileext",
									flex: 1
								},
								{
									xtype: 'actioncolumn',
									width: 20,
									items: [
										{
											iconCls: "icon-grid-delete",
											tooltip: 'Delete item',
											handler: function (grid, rowIndex, colIndex) {
												var rec = grid.store.getAt(rowIndex);
												
												grid.store.remove(rec);
											}
										}
									]
								}
							],
							bbar: [
								"->",
								{
									xtype: "button",
									text: IRm$.r1("L_COMMIT_ALL_FILES"), // "Commit All Files",
									handler: function() {
										this.toolbarHandler("cmd_c_files");
									},
									scope: this
								},
								{
									xtype: "button",
									text: IRm$.r1("L_CANCEL_ALL_FILES"), // "Cancel All Files",
									handler: function() {
										var me = this,
											d_upfiles = me.down("[name=d_upfiles]");
											
										d_upfiles.store.removeAll();
										d_upfiles.hide();
										
										me.down("[name=d_file]").hide();
									},
									scope: this
								}
							]
						},
						{
							xtype: "gridpanel",
							name: "dgrid",
							flex: 1,
							selType: "checkboxmodel",
							selModel:  {
								mode: "MULTI"
							},
							store: {
								fields: [
									"uid", "name", "type", "filename", "btype", "memo", "imgwidth", "imgheight", "fileext"
								]
							},
							columns: [
								{
									text: IRm$.r1("B_TYPE"),
									width: 100,
									dataIndex: "btype"
								},
								{
									text: IRm$.r1("B_NAME"),
									flex: 1,
									dataIndex: "name"
								},
								{
									text: IRm$.r1("L_FILEEXT"),
									width: 120,
									dataIndex: "fileext"
								},
								{
									text: IRm$.r1("B_DOWNLOAD"), // "Download",
									width: 80,
									renderer: function(rec) {
										return "<span class='igc-download'>Download</span>";
									}
								},
								{
									xtype: 'actioncolumn',
									width: 40,
									items: [
										{
											iconCls: "icon-grid-config",
											tooltip: 'Config item',
											handler: function (grid, rowIndex, colIndex) {
												var me = this,
													rec = grid.store.getAt(rowIndex),
													dlg = new IG$.dashboardRcsEdit({
														rec: rec,
														buid: me.uid
													});
												IG$.checkLogin(me, dlg);
											},
											scope: this
										},
										{
											iconCls: "icon-grid-delete",
											tooltip: 'Delete item',
											handler: function (grid, rowIndex, colIndex) {
												var me = this,
													rec = grid.store.getAt(rowIndex);
												IG$.confirmMessages(IRm$.r1("B_CONFIRM"), IRm$.r1("B_CD_MHS", rec.get("name")), function(dlg) {
													if (dlg == "yes")
													{
														rec && me.removeItem(rec);
													}
												});
											},
											scope: this
										}
									]
								}
							],
							listeners: {
								cellclick: function(tobj, node, cellIndex, record, rowIndex, e) {
									var me = this,
										fext = record.get("fileext"),
										ftype = (/png|gif|jpg|jpeg/).test(fext),
										req;
									
									if (cellIndex == 2 && ftype)
									{
										var dlg = new IG$.dashboardRcsMgrPreview({
											uid: record.get("uid")
										});
										IG$.checkLogin(me, dlg);
									}
									else if (cellIndex == 4)
									{
										req = new IG$._rpc$();
										req.init(me, 
											{
												ack: "content",
												payload: {uid: record.get("uid")},
												mbody: {}
											}, me, function(jdoc) {
												if (fext)
												{
													$.download(ig$.servlet, [
														{name: "ack", value: "download"},
														{name: "payload", value: record.get("uid")},
														{name: "mtype", value: "resource"},
														{name: "mbody", value: record.get("name")},
														{name: "_mts_", value: (IG$._g$a || "")}
													], "POST");
												}
											}, false);
										req.send();
									}
								},
								scope: this
							}
						}
					]
				}
			]
		});
		
		IG$.dashboardRcsMgr.superclass.initComponent.apply(this, arguments);
	},
	listeners: {
		afterrender: function(tobj) {
			tobj.initApp();
		}
	}
});
