IG$.sch_prog = $s.extend(IG$.v$pnl, {
	padding: 10,
	closable: true,
	iconCls: "icon-schedule",
	layout: "fit",
	
	init_f: function() {
		var me = this;
		me.load_cnt();		
	},
	
	load_cnt: function() {
		var me = this,
			req = new IG$._rpc$(),
			params;
			
		if (me.mod_type == "chart_dsgn")
		{
			params = {
				ack: "content",
				payload: {address: "/SYS_Config/chart_dsgn"},
				mbody: {subfolder: "T"}
			};
		}
		else
		{
			params = {
				ack: "appoption",
				payload: {address: "schedule_config"},
				mbody: {}
			};
		}
		
		req.init(me, 
			params, me, function(jdoc) {
				var me = this,
					grdprog = me.down("[name=grdprog]");
					
				if (me.mod_type == "chart_dsgn")
				{
					
				}
				else
				{
					me._mc = jdoc || {};
					me._mc.meta_type = "schedule_config";
					grdprog.store.loadData(me._mc.programs);
				}
			}, null);
		req.send();
	},
	
	_editProg: function(rec) {
		var me = this,
			grdprog = me.down("[name=grdprog]"),
			mcard = me.down("[name=mcard]"),
			w, h;
		
		mcard.getLayout().setActiveItem(1);
		
		me._editor_rec = rec ? rec : null;
		me.mdata = rec ? rec.data : {};
		
		if (!me.__tabinit_)
		{
			var jdom = $(me.down("[name=script_0]").el.dom),
				jdomeditor = $(".idv-jcl-edtr", jdom),
				jview,
				editor;
			
			jdomeditor.empty();
			
			w = Math.max(IG$.j$ext._w(jdom) - 4, w || 380);
			h = Math.max(IG$.j$ext._h(jdom) - 4, h || 380);
			
			jview = $("<div class='idv-jcl-view'></div>").appendTo(jdomeditor);
			IG$.j$ext._w(jview, w);
			IG$.j$ext._h(jview, h);
			
			editor = ace.edit(jview[0]);
			editor.getSession().setMode("ace/mode/java");
			
			me.scriptview_0 = jview;
			me.scripteditor_0 = editor;
			
			me.__tabinit_ = 1;
			
			setTimeout(function() {
				me._editor_init_f();
			}, 500);
		}
		else
		{
			me._editor_init_f();
		}
	},
	
	_update_content: function() {
		var me = this,
			r = 1,
			grdprog = me.down("[name=grdprog]"),
			i,
			rec,
			_mc = {
				meta_type: "schedule_config",
				programs: []
			}, p;
		
		for (i=0; i < grdprog.store.data.items.length; i++)
		{
			rec = grdprog.store.data.items[i];
			
			p = {
				mod_type: rec.get("mod_type"),
				name: rec.get("name"),
				java_class: rec.get("java_class"),
				java_source: rec.get("java_source"),
				java_import: rec.get("java_import"),
				java_extra: rec.get("java_extra"),
				ui_layout: rec.get("ui_layout"),
				ui_script: rec.get("ui_script"),
				ui_screen: rec.get("ui_screen"),
				code_parts: rec.get("code_parts")
			};
			
			_mc.programs.push(p);
		}
		
		
		me._mc = _mc;
		
		return r;
	},
	
	_save: function(b_confirm) {
		var me = this,
			req = new IG$._rpc$();
		
		if (!me._update_content())
			return;
		
		if (b_confirm)
		{
			req.init(me, 
				{
					ack: "appoption",
					payload: {address: "schedule_config", option: "updatecontent"},
					mbody: me._mc,
					meta_type: "schedule_config"
				}, me, function(jdoc) {
					IG$.alertmsg(ig$.appname, IRm$.r1("M_SAVED"), null, null, 0, "success");
				}, false);
			req.send();
		}
		else
		{
			IG$.confirmMessages(ig$.appname, IRm$.r1("L_SAVE_CHANGES"), function(e) {
				if (e == "yes")
				{
					req.init(me, 
						{
							ack: "appoption",
							payload: {address: "schedule_config", option: "updatecontent"},
							mbody: me._mc,
							meta_type: "schedule_config"
						}, me, function(jdoc) {
							IG$.alertmsg(ig$.appname, IRm$.r1("M_SAVED"), null, null, 0, "success");
						}, false);
					req.send();
				}
			});
		}
	},
	
	_exp: function() {
		var me = this,
			dlg;
		
		if (!me._update_content())
			return;
		
		dlg = new IG$.dlg_syscon({
			meta_content: me._mc,
			callback: new IG$.callBackObj(me, function(jdoc) {
				var me = this,
					grdprog = me.down("[name=grdprog]");
					
				if (me.mod_type == "chart_dsgn")
				{
					
				}
				else
				{
					me._mc = jdoc || {};
					me._mc.meta_type = "schedule_config";
					grdprog.store.loadData(me._mc.programs);
				}
			})
		});
		dlg.show();
	},
	
	// program editor functions
	pname: "com.amplix.scheduler.job.",
	
	_editor_init_f: function() {
		var me = this,
			mdata = me.mdata || {};
		
		me.__prog_start = 0;
		
		mdata.mod_type = mdata.mod_type || me.mod_type || "scheduler_quartz";
		me.java_import = mdata.java_import;
		me.down("[name=mod_type]").setValue(mdata.mod_type);
		me._editor_updmodtype();
				
		me.down("[name=m_name]").setValue(mdata.name);
		me.down("[name=m_java_class]").setValue(mdata.java_class ?  mdata.java_class : (mdata.name ? me.pname + mdata.name : me.pname));
		me.down("[name=prog_part]").setValue("");
		
		if (me.scripteditor_0)
		{
			me.scripteditor_0.setValue("");
		}
		
		me.__prog_start = 1;
	},
	
	_editor_upcheck: function() {
		var me = this,
			mdata = me.mdata,
			m_name = me.down("[name=m_name]"),
			m_java_class = me.down("[name=m_java_class]"),
			mod_type = me.down("[name=mod_type]");
		
		$s.apply(mdata, {
			mod_type: mod_type.getValue(),
			name: m_name.getValue(),
			java_class: m_java_class.getValue(),
			java_import: me.java_import
		});
		
		m_name.clearInvalid();
		m_java_class.clearInvalid();
		
		if (!mdata.name)
		{
			m_name.setInvalid(IRm$.r1("B_REQ"));
			return;
		}
		
		if (!mdata.java_class)
		{
			m_java_class.setInvalid(IRm$.r1("B_REQ"));
			return;
		}
		
		return mdata;
	},
	
	_apply_prog_part: function(cvalue) {
		var me = this,
			prog_part = me.down("[name=prog_part]"),
			prog_part_val = cvalue ? cvalue : prog_part.getValue(),
			mdata = me.mdata,
			scripteditor_0 = me.scripteditor_0;
			
		if (!me.__prog_start)
			return;
		
		if (mdata && prog_part_val)
		{
			mdata.code_parts = mdata.code_parts || {};
			
			if (prog_part_val.substring(0, 5) == "code_")
			{
				mdata.code_parts[prog_part_val] = scripteditor_0.getValue() ? scripteditor_0.getValue().split("\n") : null; 
			}
			else
			{
				mdata[prog_part_val] = scripteditor_0.getValue() ? scripteditor_0.getValue().split("\n") : null;
			}
		}
	},
	
	_editor_updmodtype: function() {
		var me = this,
			mod_type = me.down("[name=mod_type]"),
			prog_part = me.down("[name=prog_part]"),
			mod_type_val = mod_type.getValue(),
			dp = [
				{name: IRm$.r1("B_SELECT"), value: ""}
			],
			pname,
			i;
		
		for (i=0; i < mod_type.store.data.items.length; i++)
		{
			if (mod_type.store.data.items[i].get("value") == mod_type_val)
			{
				pname = mod_type.store.data.items[i].get("pname");
				break;
			}
		}
		
		if (pname)
		{
			me.pname = pname;
		}
		
		if (mod_type_val == "scheduler_quartz")
		{
			dp = dp.concat([
				{name: "Java Source", value: "java_source"},
				{name: "Extra Java", value: "java_extra"},
				{name: "UI Layout", value: "ui_layout"},
				{name: "UI Script", value: "ui_script"},
				{name: "UI Screen", value: "ui_screen"}
			]);
		}
		else if (mod_type_val == "external_dataloader")
		{
			dp = dp.concat([
				{name: "Java Source", value: "java_source"},
				{name: "Extra Java", value: "java_extra"}
			]);
		}
		else if (mod_type_val == "single_sign_on")
		{
			dp = dp.concat([
				{name: "Login for Authentication", value: "code_before_login"},
				{name: "After Internal Login Failed", value: "code_try_login"},
				{name: "Get User Lists", value: "code_get_user_list"},
				{name: "Get Role Lists", value: "code_get_role_list"},
				{name: "Get Group Lists", value: "code_get_group_list"},
				{name: "Optional Fields", value: "code_get_option_fields"}
			]);
		}
		else if (mod_type_val == "chart_dsgn")
		{
			dp = dp.concat([
				{name: "drawChart: function(owner, results)", value: "drawChart"},
				{name: "updatedisplay: function(owner, w, h)", value: "updatedisplay"},
				{name: "dispose: function()", value: "dispose"},
				{name: "other codes", value: "othercode"}
			]);
		}
		
		prog_part.store.loadData(dp);
	},
		
	_editor_doCompile: function() {
		var me = this,
			req = new IG$._rpc$(),
			address = {option: "compile"},
			data = me._editor_upcheck();
		
		if (!data)
			return;
		
		req.init(me, 
			{
				ack: "plugin",
				payload: address,
				mbody: {
					meta_type: "schedule_config",
					programs: [
						data
					]
				},
				meta_type: "schedule_config"
			}, me, function(jdoc) {
				var me = this,
					txtoutput = me.down("[name=txtoutput]"),
					jmsg = jdoc && jdoc.length ? jdoc[0] : null,
					error_details,
					t = [];
					
				if (jmsg && jmsg.results)
				{
					error_details = jmsg.results.error_details;
					if (error_details)
					{
						$.each(error_details, function(i, s) {
							t.push(s.message || "");
						});
					}
				}
				
				txtoutput.setValue(t.join("\n") || "success!");
			}
		);
		req.send();
	},
	
	_editor_commit_f: function(justsave) {
		var me = this,
			data = me._editor_upcheck(),
			rec = me._editor_rec,
			grdprog = me.down("[name=grdprog]"),
			mcard = me.down("[name=mcard]");
		
		if (!data)
			return;
				
		var k;
		
		if (rec)
		{
			for (k in data)
			{
				rec.data[k] = data[k];
			}
			rec.refresh();
		}
		else
		{
			me._editor_rec = grdprog.store.add(data);
		}
		
		me._save(1);
		
		if (justsave === true)
			return;
		
		mcard.getLayout().setActiveItem(0);
	},
	
	_update_prog_part: function() {
		var me = this,
			prog_part = me.down("[name=prog_part]"),
			pval = prog_part.getValue(),
			tval = "",
			mdata = me.mdata,
			mvalue = "";
		
		if (pval && mdata)
		{
			if (mdata[pval])
			{
				mvalue = mdata[pval].join("\n");
			}
			else if (pval.substring(0, 5) == "code_" && mdata.code_parts && mdata.code_parts[pval])
			{
				mvalue = mdata.code_parts[pval].join("\n");
			}
		}
		
		me.scripteditor_0.setValue(mvalue);
	},
	
	// end of program editor functions
	
	initComponent: function() {
		var me = this;
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: "card",
					name: "mcard",
					items: [
						// card1 -- list of schedule
						{
							xtype: "gridpanel",
							flex: 1,
							name: "grdprog",
							store: {
								xtype: "store"
							},
							columns: [
								{
									text: IRm$.r1("B_NAME"),
									width: 200,
									dataIndex: "name"
								},
								{
									text: IRm$.r1("L_PKG_NAME"), // "Package name",
									flex: 1,
									dataIndex: "java_class"
								},
								{
									xtype: "actioncolumn",
									width: 60,
									items: [
										{
											iconCls: "icon-grid-delete",
											tooltip: IRm$.r1('L_REMOVE'),
											handler: function (grid, rowIndex, colIndex) {
												var me = this,
													grd = grid,
													store = grd.store,
													rec = store.getAt(rowIndex);
												
												IG$.confirmMessages(ig$.appname, IRm$.r1("M_CONFIRM"), function(e) {
													store.removeAt(rowIndex);
												});
											},
											scope: this
										}
									]
								}
							],
							listeners: {
								itemclick: function(tobj, record, item, index, e, eopts) {
									this._editProg(record);
								},
								scope: this
							},
							tbar: [
								{
									text: IRm$.r1("B_SAVE"),
									handler: function() {
										this._save();
									},
									scope: this
								},
								{
									text: IRm$.r1("B_REFRESH"),
									handler: function() {
										this.init_f();
									},
									scope: this
								},
								{
									text: IRm$.r1("L_ADD_NEW_PROGRAM"),
									handler: function() {
										this._editProg();
									},
									scope: this
								},
								"->",
								{
									text: IRm$.r1("L_EXPORT_META"),
									tooltip: IRm$.r1("L_EXPORT"),
									handler: function() {
										this._exp();
									},
									scope: this
								}
							]
						},
						// end of card1 -- list of schedule
						// card2 -- program editor
						{
							xtype: "panel",
							layout: "fit",
							tbar: [
								{
									text: IRm$.r1('B_CONFIRM'),
									handler: function() {
										this._apply_prog_part();
										this._editor_commit_f();
									},
									scope: this
								},
								{
									text: IRm$.r1('B_CANCEL'),
									handler:function() {
										var me = this,
											mcard = me.down("[name=mcard]");
										me.__prog_start = false;
										mcard.getLayout().setActiveItem(0);
									},
									scope: this
								},
								"-",
								{
									text: IRm$.r1("L_PKG_LST"), // "Package Lists",
									handler: function() {
										var me = this,
											item = {
												cls: me.java_import ? me.java_import.join("\n") : ""
											},
											dlg = new IG$.classModuleClsImport({
												litem: item,
												callback: new IG$.callBackObj(me, function(data) {
													me.java_import = data.cls ? data.cls.split("\n") : null;
												})
											});
										dlg.show();
									},
									scope: this
								}
							],
							items: [
								{
									xtype: "panel",
									padding: 10,
									layout: {
										type: "vbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "container",
											layout: {
												type: "hbox",
												align: "stretch"
											},
											items: [
												{
													xtype: "combobox",
													name: "mod_type",
													fieldLabel: IRm$.r1("L_MODULE_TYPE"), // "Module Type",
													required: true,
													displayField: "name",
													valueField: "value",
													store: {
														data: [
															{name: "Scheduler Class", value: "scheduler_quartz", pname: "com.amplix.scheduler.job."},
															{name: "External DataLoader", value: "external_dataloader", pname: "com.amplix.dataloader.custom."},
															{name: "Single Sign On", value: "single_sign_on", pname: "com.amplix.sso.custom."},
															{name: "Chart Code", value: "chart_dsgn", pname: ""}
														]
													},
													listeners: {
														change: function() {
															this._editor_updmodtype();
														},
														scope: this
													}
												},
												{
													xtype: "container",
													flex: 1
												}
											]
										},
										{
											xtype: "container",
											height: 10
										},
										{
											xtype: "panel",
											padding: 10,
											layout: {
												type: "vbox",
												align: "stretch"
											},
											autoScroll: true,
											title: "Java Execution",
											items: [
												{
													xtype: "container",
													layout: {
														type: "hbox",
														align: "stretch"
													},
													items: [
														{
															xtype: "textfield",
															fieldLabel: IRm$.r1("B_NAME"),
															name: "m_name"
														},
														{
															xtype: "container",
															flex: 1
														}
													]
												},
												{
													xtype: "fieldset",
													paddingY: 20,
													title: "Java Codes",
													layout: {
														type: "vbox",
														align: "stretch"
													},
													items: [
														{
															xtype: "container",
															layout: {
																type: "hbox",
																align: "stretch"
															},
															items: [
																{
																	xtype: "textfield",
																	fieldLabel: IRm$.r1("L_JAVA_CLASS"), // "JavaClass",
																	name: "m_java_class"
																},
																{
																	xtype: "container",
																	flex: 1
																}
															]
														},
														{
															xtype: "panel",
															layout: {
																type: "vbox",
																align: "stretch"
															},
															height: 600,
															border: 0,
															items: [
																{
																	xtype: "panel",
																	layout: {
																		type: "hbox",
																		align: "stretch"
																	},
																	border: 0,
																	items: [
																		{
																			xtype: "combobox",
																			name: "prog_part",
																			fieldLabel: IRm$.r1("L_PROGRAM_PARTS"), // "Program Parts",
																			required: true,
																			displayField: "name",
																			valueField: "value",
																			store: {
																			},
																			listeners: {
																				change: function(tobj, nv, ov) {
																					var me = this;
																					if (ov)
																					{
																						me._apply_prog_part(ov);
																					}
																					me._update_prog_part();
																				},
																				scope: this
																			}
																		},
																		{
																			xtype: "container",
																			flex: 1
																		},
																		{
																			xtype: "button",
																			text: IRm$.r1("L_APPLY_CODES"),
																			handler: function() {
																				this._apply_prog_part();
																				this._editor_commit_f(true);
																			},
																			scope: this
																		}
																	]
																},
																{
																	name: "script_0",
																	flex: 1,
																	region: "center",
																	html: "<div class='idv-jcl-edtr'></div>",
																	split: true,
																	border: 1,
																	listeners: {
																		resize: function(tobj, w, h, ow, oh, opts) {
																			if (me.scriptview_0 && me.scripteditor_0)
																			{
																				IG$.j$ext._w(me.scriptview_0, w - 4);
																				IG$.j$ext._h(me.scriptview_0, h - 4);
																				me.scripteditor_0.resize(true);
																			}
																		}
																	}
																}
															]
														},
														{
															xtype: "container",
															layout: {
																type: "hbox",
																align: "stretch"
															},
															items: [
																{
																	xtype: "container",
																	flex: 1
																},
																{
																	xtype: "button",
																	text: IRm$.r1("B_COMPILE"),
																	handler: function() {
																		var me = this;
																		me._apply_prog_part();
																		me._editor_doCompile();
																	},
																	scope: this
																}
															]
														},
														{
															xtype: "textarea",
															fieldLabel: IRm$.r1("L_OUTPUT_MSG"), // "Output Message",
															name: "txtoutput",
															height: 120,
															labelPosition: "top"
														}
													]
												}
											]
										}
									]
								}
							]
						}
					]
				}
				
			]
		});
		
		IG$.sch_prog.superclass.initComponent.call(this);
	},
	listeners: {
		afterrender: function(tobj) {
			tobj.init_f();
		}
	}
});
