IG$.folderManager = $s.extend($s.gridpanel, {
	loadRoot: function(root) {
		var me = this,
			req, meta;
		
		me.root = root;
		me.mpath = [me.root];
		meta = me.root;
		
		req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "content",
				payload: {uid: meta.uid, nodepath: meta.nodepath, name: meta.name, type: meta.type},
				mbody: {}
			}, me, me.rs_loadRoot, null);
		req.send();
	},
	
	topWS: function() {
		this.loadRoot({
			uid: "/",
			nodepath: "/",
			name: "Workspace",
			type: "Folder"
		});
	},
	
	naviFolder: function(record) {
		var me = this,
			type = record.get("type").toLowerCase(),
			req;
		
		if (/(folder|cube|parent|table)/.test(type))
		{
			meta = {
				uid: record.get("uid"),
				type: record.get("type"),
				nodepath: record.get("nodepath"),
				name: record.get("oname") || record.get("name"),
				manage: record.get("manage"),
				writable: record.get("writable")
			};
			
			if (record.get("name") == "..")
			{
				delete meta["type"];
				delete meta["name"];
				delete meta["nodepath"];
				
				if (meta.uid == null)
				{
					meta.uid = "/";
				}
			}
			
			req = new IG$._rpc$();
			req.init(me, 
				{
					ack: "content",
					payload: {uid: meta.uid, nodepath: meta.nodepath, name: meta.name, type: meta.type},
					mbody: {subfolder: "T"}
				}, me, me.rs_loadRoot, null);
			req.send();
		}
	},
	
	rs_loadRoot: function(jdoc) {
		var me = this,
			tval = jdoc,
			snodes = jdoc.children || [],
			i, items = [], item, updatedate,
			parent = {},
			nodepath = tval.nodepath.split("/");
		
		me.cpath = {
			uid: tval.uid,
			name: tval.name,
			nodepath: tval.nodepath,
			type: tval.type,
			writable: tval.writable,
			manage: tval.manage
		};
		
		if (nodepath.length > 1 && nodepath[1])
		{
			parent.oname = tval.name;
			parent.name = "..";
			parent.uid = (tval.uid == tval.pid) ? null : tval.pid;
			parent.type = "parent";
			items.push(parent);
		}
		
		if (snodes)
		{
			for (i=0; i < snodes.length; i++)
			{
				item = snodes[i];
				item.iconcls = IG$.get_metatype_icon(item.type.toLowerCase(), item);
				items.push(item);
			}
			
			IG$.sortMeta(items);
		}
		
		me.store.loadData(items);
	},
	
	refresh: function() {
		var me = this,
			req,
			meta = me.cpath;
		
		req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "content",
				payload: {uid: meta.uid, nodepath: meta.nodepath, name: meta.name, type: meta.type},
				mbody: {}
			}, me, me.rs_loadRoot, null);
		req.send();
	},
	
	_createMetaObject: function(objecttype, parentuid, parentfullpath, node) {
		var dlgpop = new IG$.makeItem({
			parentnodepath: parentfullpath,
			itemtype: objecttype,
			parentuid: parentuid
		});
		dlgpop.callback = new IG$.callBackObj(this, this.r_createMetaObject, node);
		
		IG$.checkLogin(this, dlgpop);
	},
	
	r_createMetaObject: function(jdoc) {
		var me = this;
		me.refresh();
	},
	
	_renameMetaObject: function(itemname, uid, nodepath, nodetype) {
		var me = this,
			dlgpop = new IG$.makeItemEditor(
				{
					itemname: itemname,
					uid: uid,
					nodepath: nodepath,
					itemtype: nodetype,
					callback: new IG$.callBackObj(me, me.r_renameMetaObject, null)
				}
			);
		
		IG$.checkLogin(me, dlgpop);
	},
	
	r_renameMetaObject: function(jdoc) {
		var me = this;
		me.refresh();
	},
	
	toolbarHandler: function(cmd) {
		var me = this;
		switch (cmd)
		{
		case "cmd_new_folder":
			me._createMetaObject("Folder", me.cpath.uid, me.cpath.nodepath, null);
			break;
		case "cmd_refresh":
			me.refresh();
			break;
		case "cmd_gotop":
			me.loadRoot(me.root);
			break;
		case "cmd_top_ws":
			me.topWS();
			break;
		case "cmd_delete":
			me.deleteMetaObject();
			break;
		case "cmd_rename":
			var selmodel = me.getSelectionModel(),
				sel;
			if (selmodel.selected.items.length > 0)
			{
				sel = selmodel.selected.items[0];
				if (sel.get("name") != ".." && (sel.get("writable") || sel.get("manage")))
				{
					me._renameMetaObject(sel.get("name"), sel.get("uid"), sel.get("nodepath"), sel.get("type"));
				}
			}
			break;
		}
	},
	
	deleteMetaObject: function() {
		var me = this,
			i, checked,
			address = [],
			itemcnt = 0;
		
		checked = me.getChecked();
		
		if (checked.length > 0)
		{
			for (i=0; i < checked.length; i++)
			{
				if (checked[i].name != ".." && (checked[i].manage || checked[i].writable))
				{
					address.push({
						uid: checked[i].uid,
						option: "delete"
					});
					itemcnt++;
				}
				else if (checked[i].name != "..")
				{
					itemcnt = 0;
					IG$.ShowError(IRm$.r1("E_PRIV_ERR") + checked[i].name, me); // "Privilege error on content: " + checked[i].name, me);
					break;
				}
			}
			
			if (itemcnt > 0)
			{
				req = new IG$._rpc$();
				req.init(me, 
					{
						ack: "batchcontent",
						payload: address,
						mbody: {option: "delete"}
					}, me, me.rdeleteMetaObject, null);
				req.send();
			}
		}
	},
	
	rdeleteMetaObject: function(xdoc) {
		var me = this;
		me.refresh();
	},
	
	getChecked: function() {
		var me = this,
			checked=[], i, item;
		for (i=0; i < me.store.data.items.length; i++)
		{
			if (me.store.data.items[i].get("selected"))
			{
				item = me.store.data.items[i];
				checked.push({
					uid: item.get("uid"),
					type: item.get("type"),
					nodepath: item.get("nodepath"),
					name: item.get("name"),
					manage: item.get("manage"),
					writable: item.get("writable")
				});
			}
		}
		
		return checked;
	},
	
	initComponent : function() {
		var me = this;
		
		$s.apply(this, {
			name: "grd",
			store: {
				xtype: "store",
				fields: [
					"uid", "name", "oname", "nodepath", "type", "writable", "manage", "updatedate", "updatedatafm", "selected", "iconcls"
				]
			},
			selType: "checkboxmodel",
			selModel: {
				mode: "MULTI",
				checkSelector: "cell"
			},
			columns: [
				{
					xtype: "templatecolumn",
					text: "",
					dataIndex: "iconcls",
					width: 28,
					renderer: function(value) {
						return "<div class='igc_g_icon " + value + "'></div>";
					}
				},
				{
					text: IRm$.r1("B_NAME"),
					dataIndex: "name",
					flex: 1,
					tdCls: "igc-td-link",
					minWidth: 200
				},
				{
					text: IRm$.r1("B_TYPE"),
					dataIndex: "type",
					width: 60
				},
				{
					text: IRm$.r1("L_LAST_UPD"),
					dataIndex: "updatedatefm",
					width: 140
				}
			],
			dockedItems: [
				{
					xtype: "toolbar",
					dock: "top",
					items: [
						{
							xtype: "textfield",
							flex: 1,
							name: "_filter",
							emptyText: IRm$.r1("L_SEARCH_KWD"),
							enableKeyEvents: true,
							listeners: {
								keyup: function(tobj, e, eopts) {
									e.keyCode == 13 && IG$._dofilter(this, "grd", ["name"]);
								},
								scope: this
							}
						},
						{
							xtype: "button",
							// text: "..",
							iconCls: "icon-toolbar-search",
							tooltip: IRm$.r1("B_SEARCH"),
							handler: function() {
								IG$._dofilter(this, "grd", ["name"]);
							},
							scope: this
						}
					]
				},
				{
					xtype: "toolbar",
					dock: "top",
					items: [
						{
							xtype: 'splitbutton',
							text: IRm$.r1("B_NEW"),
							menu: [
								{
									text: IRm$.r1("B_FOLDER"),
									handler: function() {
										this.toolbarHandler("cmd_new_folder");
									},
									scope: this
								}
							]
						},
						{
							text: IRm$.r1("B_RENAME"),
							handler: function() {
								this.toolbarHandler("cmd_rename"); 
							},
							scope: this
						},
						{
							iconCls: "icon-toolbar-remove",
							handler: function() {
								var me = this;
								
								IG$.confirmMessages(IRm$.r1("B_CONFIRM"), IRm$.r1("B_CD_CHS"), function(dlg) {
									if (dlg == "yes")
									{
										me.toolbarHandler("cmd_delete");
									}
								});
							},
							scope: this
						},
						"->",
						{
							iconCls: "icon-tb-refresh",
							handler: function() {
								this.toolbarHandler("cmd_refresh"); 
							},
							scope: this
						},
						{
							xtype: "button",
							name: "t_btn",
							text: IRm$.r1("B_SHORTCUR"),
							disabled: true,
							menu: {
								xtype: "menu",
								items: [
									{
										text: IRm$.r1("L_GO_TOP"),
										handler: function() {
											this.toolbarHandler("cmd_gotop"); 
										},
										scope: this
									},
									{
										text: IRm$.r1("L_WORKSPACE"),
										handler: function() {
											this.toolbarHandler("cmd_top_ws");
										},
										scope: this
									}
								]
							}
						}
					]
				}
			],
			listeners: {
				cellclick: function(tobj, td, cellIndex, record, rowIndex, e) {
					if (cellIndex == 2)
					{
						me.naviFolder.call(me, record);
					}
				}
			}
		});
		IG$.folderManager.superclass.initComponent.apply(this, arguments);
	}
});

IG$.dlgFolderMgr = $s.extend($s.window, {
	
	modal: true,
	region:"center",
	
	layout: "fit",
	
	closable: false,
	resizable:false,
	
	width: 950,
	autoHeight: true,
	dbscan: 1,
	
	_init: function() {
		var me = this,
			grd_l = me.grd_l,
			grd_r = me.grd_r;
		
		grd_l.loadRoot.call(grd_l, me.root);
		grd_r.loadRoot.call(grd_r, me.root);
	},
	
	moveChecked: function(isleft, iscopy) {
		var me = this,
			grd_l = me.grd_l,
			grd_r = me.grd_r,
			checked;
		
		checked = (isleft) ? grd_l.getChecked.call(grd_l) : grd_r.getChecked.call(grd_r);
		
		if (grd_l.cpath.uid == grd_r.cpath.uid)
		{
			IG$.alertmsg(ig$.appname, IRm$.r1("E_DUP_FOLDER"), null, null, 1, "error");
			return;
		}
		
		if (checked && checked.length > 0)
		{
			var req,
				address = [],
				panel = this,
				itemcnt = 0,
				i;
			
			for (i=0; i < checked.length; i++)
			{
				if (checked[i].name != ".." && (checked[i].writable || checked[i].manage))
				{
					address.push({
						uid: checked[i].uid
					});
					itemcnt++;
				}
			}
			
			if (itemcnt > 0)
			{
				req = new IG$._rpc$();
				req.init(panel, 
					{
						ack: "helpcontent",
						payload: address,
						mbody: {option: (iscopy == true ? "copy" : "move"), target: (isleft ? grd_r.cpath.uid : grd_l.cpath.uid)}
					}, panel, panel.r_moveSelItems, null, null);
				req.send();
			}
		}
	},
	
	r_moveSelItems: function(jdoc) {
		var me = this,
			grd_l = me.grd_l,
			grd_r = me.grd_r;
		
		IG$.alertmsg(ig$.appname, IRm$.r1("M_SUCCESS"), null, me, 0, "success");
		
		grd_l.refresh.call(grd_l);
		grd_r.refresh.call(grd_r);
	},
	
	initComponent : function() {
		var me = this;
		
		me.title = IRm$.r1("T_MGR_FOLDER");
		
		me.grd_l = new IG$.folderManager({
			name: "grd_l",
			height: 500,
			flex: 1
			
		});
		
		me.grd_r = new IG$.folderManager({
			name: "grd_r",
			height: 500,
			flex: 1
			
		});
		
		var bwidth = 60;
		
		$s.apply(this, {
			
			layout: "fit",
			
			items: [
				{
					xtype: "panel",
					layout: {
						type: "hbox",
						align: "stretch"
					},
					border: 0,
					padding: 10,
					items: [
						me.grd_l,
						{
							width: 60,
							layout: {
								type: 'vbox',
								align: 'stretch',
								pack: 'center'
							},
							items: [
								{
									xtype: "container",
									flex: 1
								},
								{
									xtype: "displayfield",
									value: "Move"
								},
								{
									xtype: 'button',
									text: '>>',
									$w: bwidth,
									tooltip: "Move items",
									handler:function() {
										this.moveChecked(true, false);
									},
									scope: this
								},
								{
									xtype: 'button',
									text: '<<',
									$w: bwidth,
									tooltip: "Move items",
									handler:function() {
										this.moveChecked(false, false);
									},
									scope: this
								},
								{
									xtype: "container",
									height: 10
								},
								{
									xtype: "displayfield",
									value: "Copy"
								},
								{
									xtype: 'button',
									text: '>',
									$w: bwidth,
									tooltip: "Copy items",
									handler:function() {
										this.moveChecked(true, true);
									},
									scope: this
								},
								{
									xtype: 'button',
									text: '<',
									$w: bwidth,
									tooltip: "Copy items",
									handler:function() {
										this.moveChecked(false, true);
									},
									scope: this
								},
								{
									xtype: "container",
									flex: 1
								}
							]
						},
						me.grd_r
					]
				}
			],
			
			buttons:[
				{
					text: IRm$.r1("B_CLOSE"),
					handler: function() {
						this.close();
					},
					scope: this
				}
			],
			
			listeners: {
				afterrender: function(ui) {
					var me = this;
					me._init();
				}
			}
		});
		
		IG$.dlgFolderMgr.superclass.initComponent.apply(this, arguments);
	}
});