if (!IG$.dlg_select_ds)
{
IG$.dlg_select_ds = $s.extend($s.window, {

	width: 500,
	height: 520,
	layout: "fit",
	padding: 5,
	
	_init: function() {
		var me = this,
			req = new IG$._rpc$(),
			datasource = me.datasource,
			sel;
			
		if (datasource)
		{
			me.down("[name=opt_dbtype]").setValue("db");
		}

		req.init(me, 
			{
				ack: "userdb",
				payload: {"address": "/Connections", option: "list"},
				mbody: {}
			}, me, function(jdoc) {
				var me = this,
					dbs = jdoc.children,
					dsource = me.down("[name=dsource]"),
					dp = [],
					i;
					
				if (dbs && dbs.length)
				{
					for (i=0; i < dbs.length; i++)
					{
						if (me.sel_mode == 1 || dbs[i].attributes && dbs[i].attributes.attrb1 == "T")
						{
							if (datasource && datasource.uid == dbs[i].uid)
							{
								sel = dbs[i];
							}
							dp.push(dbs[i]);
						}
					}
				}
				
				dsource.store.loadData(dp);
				
				if (sel)
				{
					var rec = dsource.store._pull[sel.id];
					rec && dsource.getSelectionModel().select(rec, true);
				}
			}, false);
		req.send();
	},
	
	_confirm: function(rec) {
		var me = this,
			dsource = me.down("[name=dsource]"),
			opt_dbtype = me.down("[name=opt_dbtype]"),
			dbtype_val = opt_dbtype.getValue(),
			dbtype = dbtype_val["cb_dbtype"],
			sel = !rec ? dsource.getSelectionModel().selected : null;
		
		if (dbtype == "db" || me.sel_mode == 1)
		{
			if (rec)
			{
				me.callback && me.callback.execute(rec);
				me.close();
			}
			else if (sel && sel.length)
			{
				me.callback && me.callback.execute(sel.items[0]);
				me.close();
			}
			else
			{
				IG$.ShowError(IRm$.r1("E_SEL_DC"), me); // "Select data connector to continue!", me);
			}
		}
		else
		{
			me.callback && me.callback.execute();
			me.close();
		}
	},
	
	initComponent: function(){
		var me = this;
		
		$s.apply(me, {
			title: IRm$.r1("T_SEL_DB"),
			items: [
				{
					xtype: "panel",
					padding: 10,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: [
						{
							xtype: "fieldset",
							title: IRm$.r1("L_STORAGE_OPTIONS"), // "Storage Options",
							hidden: me.sel_mode == 1,
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "radiogroup",
									name: "opt_dbtype",
									fieldLabel: IRm$.r1("L_DATA_STORAGE"),
									padding: "0 5",
									plain: true,
									items: [
										{
											boxLabel: IRm$.r1("L_USE_TEMP"), // "Use Temporary", 
											name: "cb_dbtype", 
											inputValue: "h2", 
											checked: true
										},
										{
											boxLabel: IRm$.r1("L_USE_STG"), // "Staging Database", 
											name: "cb_dbtype", 
											inputValue: "db"
										}
									],
									listeners: {
										change: function(tobj, nvalue, ovalue) {
											var me = this,
												mview = me.down("[name=mview]");
												
											mview.setDisabled(nvalue && nvalue.cb_dbtype && nvalue.cb_dbtype == "db" ? false : true);
										},
										scope: this
									}
								}
							]
						},
						{
							xtype: "fieldset",
							name: "mview",
							title: IRm$.r1("L_STAGING_DB"), // "Staging Databases",
							flex: 1,
							layout: {
								type: "vbox",
								align: "stretch"
							},
							paddingY: 10,
							disabled: me.sel_mode == 1 ? false : true,
							items: [
								{
									xtype: "gridpanel",
									name: "dsource",
									flex: 1,
									selType: "checkboxmodel",
									selModel: {
										mode: "SINGLE",
										checkSelector: ".x-grid-cell"
									},
									store: {
										fields: []
									},
									columns: [
										{
											text: IRm$.r1("B_NAME"),
											dataIndex: "name",
											tdCls: "igc-td-link"
										},
										{
											text: IRm$.r1("B_DESC"),
											dataIndex: "description"
										}
									],
									listeners: {
										cellclick: function(view, node, c, rec, rindex, e) {
											var me = this,
												opt_dbtype = me.down("[name=opt_dbtype]"),
												dbtype_val = opt_dbtype.getValue();
											
											if (me.sel_mode == 1)
												return;
											
											if (c == 1)
											{
												if (dbtype_val && dbtype_val["cb_dbtype"] == "db")
												{
													me._confirm(rec);
												}
												else
												{
													IG$.ShowError(IRm$.r1("E_CHECK_DTYPE"), me); // "Check datatype to staging database!", me);
												}
											}
										},
										scope: this
									}
								}
							]
						}
					]
				}
			],
			buttons: [
				"->",
				{
					xtype: "button",
					text: IRm$.r1("B_CONFIRM"),
					handler: function() {
						this._confirm();
					},
					scope: this
				},
				{
					xtype: "button",
					text: IRm$.r1("B_CANCEL"),
					handler: function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(tobj) {
					tobj._init();
				}
			}
		});
		
		IG$.dlg_select_ds.superclass.initComponent.call(this);
	}
});
}