IG$.mgr_deploy = $s.extend(IG$.v$pnl, {
	closable: true,
	layout: "fit",
	bodyPadding: 0,
	padding: 0,
	iconCls: "icon-ing-docdef",
	
	_init: function() {
		// load server lists
		var me = this,
			req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "content",
				payload: {address: "/SYS_Config/extra_sys_config"},
				mbody: {}
			}, me, function(jdoc) {
				var dpsvr = me.down("[name=dpsvr]"),
					dp = [],
					i,
					deploy = jdoc.deploy || [],
					rec;
				
				me.dp_svr = {};
				
				for (i=0; i < deploy.length; i++)
				{
					rec = deploy[i];
					dp.push(rec);
					me.dp_svr[rec.name] = rec;
				}
				
				dpsvr.store.loadData(dp);
			}, false);
		req.send();
	},
	
	testConnection: function() {
		var me = this,
			c_info = me.down("[name=c_info]"),
			req = new IG$._rpc$(),
			mbody = {};
		
		if (c_info.getAllValues(mbody))
			return;
		
		req.init(me, 
			{
				ack: "deploy",
				payload: {option: "testconnection"},
				mbody: mbody
			}, me, function(jdoc) {
				if (jdoc && jdoc.uid)
				{
					IG$.alertmsg(ig$.appname, IRm$.r1("M_SUCCESS"), null, null, 0, "success");
					
					var mexplorer = me.down("[name=mexplorer]"),
						navtree = mexplorer.mtree;
						
					navtree.refreshTree();
				}
			}, false);
		req.send();
	},
	
	_move_sel: function(add) {
		var me = this,
			grd_ditem = me.down("[name=grd_ditem]"),
			mexplorer = me.down("[name=mexplorer]"),
			navtree,
			sel,
			i,
			rec,
			p;
		
		if (!add)
		{
			sel = grd_ditem.getSelectionModel().selected;
			
			if (sel && sel.length)
			{
				for (i=sel.length-1; i>=0; i--)
				{
					rec = sel.items[i];
					grd_ditem.store.remove(rec); 
				}
			}
		}
		else
		{
			navtree = mexplorer.mtree;
			sel = navtree.getSelectionModel().selected;
			
			if (sel && sel.length)
			{
				for (i=0; i < sel.length; i++)
				{
					rec = sel.items[i];
					p = {
						name: rec.get("name"),
						uid: rec.get("uid"),
						nodepath: rec.get("nodepath"),
						type: rec.get("type"),
						icon: rec.get("icon"),
						iconCls: rec.get("iconCls"),
						updatedate: rec.get("updatedate")
					};
					grd_ditem.store.add(p);
				}
			}
		}
	},
	
	_do_deploy: function(validate) {
		var me = this,
			grd_ditem = me.down("[name=grd_ditem]"),
			c_info = me.down("[name=c_info]"),
			req = new IG$._rpc$(),
			mbody = {},
			metrics = {children: []},
			i;
			
		if (c_info.getAllValues(mbody))
			return;
			
		mbody.option = (validate) ? "validate" : "execute";
		
		var do_proc = function() {
			var store = grd_ditem.store,
				rec,
				i;
				
			for (i=0; i < store.data.items.length; i++)
			{
				rec = store.data.items[i];
				
				metrics.children.push(rec.data);
			}
			req.init(me, 
				{
					ack: "deploy",
					payload: mbody,
					mbody: metrics,
					meta_type: "metrics"
				}, me, function(jdoc) {
					
				}, false);
			req.send();
		}
		
		if (!validate)
		{
			IG$.confirmMessages(ig$.appname, IRm$.r1("M_CONFIRM_OVERWRITE"), function(e) {
				if (e == "yes")
				{
					do_proc();
				}
			});
		}
		else
		{
			do_proc();
		}
	},
	
	initComponent: function(){
		var me = this;
		
		$s.apply(me, {
			items: [
				{
					xtype: "panel",
					bodyPadding: 0,
					border: 1,
					layout: {
						type: "hbox",
						align: "stretch"
					},
					items: [
						{
							xtype: "panel",
							flex: 1,
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "container",
									layout: {
										type: "hbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "textfield",
											fieldLabel: IRm$.r1("B_SEARCH"),
											flex: 1,
											emptyText: IRm$.r1("M_SEL_ITEM"),
											labelWidth: 60
										},
										{
											xtype: "button",
											// text: "..",
											iconCls: "icon-toolbar-search",
											tooltip: IRm$.r1("B_SEARCH")
										},
										{
											xtype: "container",
											flex: 1
										}
									]
								},
								new IG$.explorer({
									name: "mexplorer",
									vmode: "tree",
									border: 0,
									flex: 1,
									split: false,
									_meta_move: false,
									nav_options: {
										context_menu: false
									},
									request_override: new IG$.callBackObj(me, function(reqparam, callback) {
										var me = this,
											c_info = me.down("[name=c_info]"),
											req = new IG$._rpc$(),
											mbody = {};
										
										if (c_info.getAllValues(mbody))
											return;
										
										mbody.option = "bridge";
										
										req.init(me, 
											{
												ack: "deploy",
												payload: mbody,
												mbody: reqparam,
												meta_type: "deploy_request"
											}, me, function(jdoc) {
												callback.execute(jdoc);
											}, false);
										req.send();
									}),
									custom_click_func: new IG$.callBackObj(me, function(record, node) {
										var ltype = record.get("type").toLowerCase(),
											isfolder = ltype == "root" || IG$.isFolder(ltype) < 5 ? true : false;
										
										if (isfolder)
										{
											if (!record.isExpanded())
											{
												record.expand();
											}
										}
										return false;
									}),
									show_tree_items: true
								})
							]
						},
						{
							xtype: "container",
							layout: {
								type: "vbox",
								align: "stretch",
								pack: "center"
							},
							padding: 5,
							width: 50,
							items: [
								{
									xtype: "container",
									flex: 1,
									border: 0
								},
								{
									xtype: "button",
									text: ">",
									width: 50,
									handler: function() {
										this._move_sel(1);
									},
									scope: this
								},
								{
									xtype: "button",
									text: "<",
									width: 50,
									handler: function() {
										this._move_sel(0);
									},
									scope: this
								},
								{
									xtype: "container",
									flex: 1,
									border: 0
								}
							]
						},
						{
							xtype: "panel",
							flex: 1,
							text: IRm$.r1("L_TGT_SVR"), // "Target Server",
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "fieldset",
									title: "Connection Information",
									name: "c_info",
									layout: "anchor",
									items: [
										{
											xtype: "combobox",
											name: "dpsvr",
											fieldLabel: IRm$.r1("L_DEPLOY_SVR"),
											displayField: "name",
											valueField: "name",
											store: {
												fields: ["name"]
											},
											listeners: {
												change: function(tobj) {
													var me = this,
														c_info = me.down("[name=c_info]"),
														dp_svr = me.dp_svr;
													
													if (dp_svr && dp_svr[tobj.getValue()])
													{
														c_info.setAllValues(dp_svr[tobj.getValue()].properties);
													}
												},
												scope: this
											}
										},
										{
											xtype: "textfield",
											name: "server_url",
											placeholder: "Select " + IRm$.r1("L_DEPLOY_SVR") + " ex) http://localhost:8280",
											required: true,
											readOnly: true,
											fieldLabel: IRm$.r1("L_SERVER_HOST")
										},
										{
											xtype: "textfield",
											name: "endpoint",
											placeholder: "Select " + IRm$.r1("L_DEPLOY_SVR") + " ex) /servlet/krcp",
											required: true,
											readOnly: true,
											fieldLabel: IRm$.r1("L_ENDPOINT_URL")
										},
										{
											xtype: "textfield",
											name: "tenant_name",
											required: true,
											placeholder: "type teant such as ROOT",
											fieldLabel: IRm$.r1("L_TENANT")
										},
										{
											xtype: "textfield",
											name: "tsvr_userid",
											required: true,
											fieldLabel: IRm$.r1("L_USERID")
										},
										{
											xtype: "textfield",
											name: "tsvr_passwd",
											inputType: "password",
											required: true,
											fieldLabel: IRm$.r1("L_PASSWORD")
										},
										{
											xtype: "container",
											layout: {
												type: "hbox",
												align: "stretch"
											},
											items: [
												{
													xtype: "container",
													flex: 1
												},
												{
													xtype: "button",
													text: IRm$.r1("L_TST_CON"), // "Test Connection",
													handler: function() {
														this.testConnection();
													},
													scope: this
												}
											]
										}
									]
								},
								{
									xtype: "gridpanel",
									title: "Deploy Items",
									name: "grd_ditem",
									tbar: [
										{
											xtype: "button",
											text: IRm$.r1("L_VALID_STATUS"),
											handler: function() {
												this._do_deploy(true);
											},
											scope: this
										},
										{
											xtype: "button",
											text: IRm$.r1("L_EXECUTE"),
											handler: function() {
												this._do_deploy();
											},
											scope: this
										}
									],
									selType: "checkboxmodel",
									selModel: {
										mode: "MULTI"
									},
									viewConfig: {
										plugins: [
											{
												ptype: "gridviewdragdrop",
												ddGroup: "_I$RD_G_",
												enableDrop: false,
												enableDrag: true
											}
										],
										listeners: {
										}
									},
									store: {},
									columns: [
										{
											text: IRm$.r1("B_NAME"),
											dataIndex: "name",
											flex: 1
										},
										{
											text: IRm$.r1("L_FULL_PATH"), // "Full path",
											dataIndex: "nodepath",
											flex: 1
										},
										{
											text: IRm$.r1("B_STATUS"),
											dataIndex: "status"
										},
										{
											text: IRm$.r1("L_EXE_RSLTS"), // "Execution Results",
											dataIndex: "execution_results"
										}
									]
								}
							]
						}
					]
				}
			],
			listeners: {
				afterrender: function(tobj) {
					this._init();
				}
			}
		});
		
		IG$.mgr_deploy.superclass.initComponent.call(this);
	}
});