IG$.makeStyle = $s.extend($s.window, {
	
	modal: true,
	region:"center",
	layout: "fit",
	closable: false,
	resizable:false,
	width: 320,
	autoHeight: true,
	
	parentnodepath: null,
	
	callback: null,
	
	processMakeStyleItem: function(){
		var me = this,
			i,
			m_theme = me.m_theme,
			fitemname = me.down("[name=fitemname]"),
			fitemname_v = fitemname.getValue(),
			basetypeselect = me.down("[name=basetypeselect]"),
			basetypeselect_v = basetypeselect.getValue();
			
		fitemname.clearInvalid();
		basetypeselect.clearInvalid();
		
		if (!fitemname_v)
		{
			fitemname.setInvalid("Required Field");
			return;
		}
			
		if (!m_theme && !basetypeselect_v)
		{
			basetypeselect.setInvalid("Required Field");
			return;
		}
		
		if (m_theme && me.themes[fitemname_v])
		{
			fitemname.setInvalid("Duplicated");
			return;
		}
		else if (!m_theme)
		{
			for (i=0; i < me.styleinfo.length; i++)
			{
				if (me.styleinfo[i].name == fitemname_v)
				{
					fitemname.setInvalid("Duplicated");
					return false;
				}
			}
		}
		
		me.callback.execute([fitemname_v, basetypeselect_v]);
		
		me.close();
	},
	
	_init: function() {
		var me = this,
			i,
			dp = [],
			m_theme = me.m_theme,
			basetypeselect = me.down("[name=basetypeselect]");
		
		if (!m_theme)
		{
			for (i=0; i < me.styleinfo.length; i++)
			{
				if (!me.styleinfo[i].basestylename)
				{
					dp.push({name: me.styleinfo[i].name});
				}
			}
			
			basetypeselect.store.loadData(dp);
		}
	},
	
	initComponent : function() {

		var me = this;

		me.title = IRm$.r1("L_MAKESTYLE");
		
		$s.apply(this, {
			defaults:{bodyStyle:"padding:10px"},
			
			items: [
					{
						html: IRm$.r1("L_MAKESTYLE"),
						flex: 1,
						border: 0
					},
					{
						xtype: "panel",
						region:"center",
						flex: 3,
						border:true,
						region:"center",
						height: 120,
						defaultType: "textfield",
						layout: "anchor",
						defaults: {
							anchor: "100%"
						},
						items: [
							{
								fieldLabel: IRm$.r1("L_STYLENAME"),
								labelWidth: 130,
								name: "fitemname",
								value: "",
								allowBlank: false,
								blankText: IRm$.r1("B_REQ"),
								enableKeyEvents: true,
								listeners: {
									"keyup": function(item, e) {
										if (e.keyCode == 13)
										{
											this.processMakeStyleItem.call(this);
										}
									},
									scope: this
								}
							},
							{
								xtype: "combobox",
								name: "basetypeselect",
								hidden: me.m_theme ? true : false,
								fieldLabel: IRm$.r1("L_INHERIT_STYLE_FROM"),
								labelWidth: 130,
								store: {
									fields: [
										{name: "name"}
									]
								},
								displayField: "name",
								valueField: "name",
								editable: false,
								queryMode: "local",
								selectOnTab: false
							}
						],
						listeners: {
							afterrender: function() {
								this._init();
							},
							scope: this
						}
					}
				],
			buttons:[
				{
					text: IRm$.r1("B_CONFIRM"),
					handler: function() {
						this.processMakeStyleItem();
					},
					scope: this
				}, 
				{
					text: IRm$.r1("B_CANCEL"),
					handler:function() {
						this.close();
					},
					scope: this
				}
			]
		});
		
		IG$.makeStyle.superclass.initComponent.apply(this, arguments);
	}
});
