IG$.dlg_check_email = $s.extend($s.window, {
	width: 400,
	layout: "fit",
	modal: true,
	
	bodyPadding: 5,
	
	initComponent: function() {
		var me = this;
		
		$s.apply(me, {
			title: IRm$.r1("L_CHECK_EMAIL"), // "Select items",
			
			items: [
				{
					xtype: "panel",
					flex: 1,
					layout: {
						type: "fit"
					},
					defaults:{
						padding: 5
					},
					items: [
						{
							xtype: "panel",
							padding: 10,
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "fieldset",
									title: IRm$.r1("L_RCPTS"), // "Recipients",
									layout: {
										type: "vbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "textarea",
											name: "recipients"
										}
									]
								}
							]
						}
					]
				}
			],
			buttons: [
				{
					xtype: "button",
					text: IRm$.r1("B_CONFIRM"),
					handler: function() {
						var me = this,
							rcp = me.down("[name=recipients]");
						
						rcp.clearInvalid();
						
						if (!rcp.getValue())
						{
							rcp.markInvalid("Required");
							return false;
						}
						
						me.callback && me.callback.execute(this, rcp.getValue());
						me.close();
					},
					scope: this
				},
				{
					xtype: "button",
					text: IRm$.r1("B_CANCEL"),
					handler: function() {
						this.close();
					},
					scope: this
				}
			]
		});
		
		IG$.dlg_check_email.superclass.initComponent.call(this);
	}
});

IG$.dlg_esri_basemap = $s.extend($s.window, {
	width: 400,
	layout: "fit",
	modal: true,
	
	bodyPadding: 5,
	
	_init: function() {
		var me = this,
			mtype = me.mtype,
			rec = me.rec;
			
		if (rec)
		{
			me.setAllValues(me.rec.data);
		}
	},
	
	_confirm: function() {
		var me = this,
			data = {};
			
		if (me.getAllValues(data))
		{
			return;
		}
		
		if (me.rec)
		{
			$.each(data, function(f, val) {
				me.rec.set(f, val);
			});
		}
		else
		{
			me.grid.store.add(data);
		}
		
		me.callback && me.callback.execute(this, data);
		me.close();
	},
	
	initComponent: function() {
		var me = this;
		
		$s.apply(me, {
			items: [
				{
					xtype: "panel",
					flex: 1,
					layout: {
						type: "fit"
					},
					defaults:{
						padding: 5
					},
					items: [
						{
							xtype: "panel",
							padding: 10,
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "textfield",
									name: "name",
									fieldLabel: IRm$.r1("B_NAME"),
									required: true
								},
								{
									xtype: "textarea",
									height: 80,
									name: "url",
									hidden: me.mtype == "deploy",
									fieldLabel: IRm$.r1("B_URL")
								},
								{
									xtype: "combobox",
									fieldLabel: IRm$.r1("B_LOADER"),
									name: "loader",
									queryMode: 'local',
									displayField: 'name',
									valueField: 'value',
									editable: false,
									autoSelect: true,
									required: me.mtype == "restapi",
									hidden: me.mtype != "restapi",
									store: {
										xtype: 'store',
										fields: [
											"name", "value"
										],
										data: [
											{name: IRm$.r1("B_SELECT"), value: ""},
											{name: "FeatureLayer", value: "FeatureLayer"},
											{name: "ArcGISDynamicMapServiceLayer", value: "ArcGISDynamicMapServiceLayer"}
										]
									}
								},
								{
									xtype: "textarea",
									height: 120,
									name: "option",
									hidden: me.mtype != "restapi",
									fieldLabel: IRm$.r1("B_OPTION")
								},
								{
									xtype: "textarea",
									height: 80,
									name: "server_url",
									placeholder: "http://localhost:8280",
									required: me.mtype == "deploy",
									hidden: me.mtype != "deploy",
									fieldLabel: IRm$.r1("L_SERVER_HOST")
								},
								{
									xtype: "textarea",
									height: 80,
									name: "endpoint",
									required: me.mtype == "deploy",
									hidden: me.mtype != "deploy",
									placeholder: "/servlet/krcp",
									fieldLabel: IRm$.r1("L_ENDPOINT_URL")
								}
							]
						}
					]
				}
			],
			buttons: [
				{
					xtype: "button",
					text: IRm$.r1("B_CONFIRM"),
					handler: function() {
						var me = this;
						me._confirm();
					},
					scope: this
				},
				{
					xtype: "button",
					text: IRm$.r1("B_CANCEL"),
					handler: function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(tobj) {
					tobj._init();
				}
			}
		});
		
		IG$.dlg_esri_basemap.superclass.initComponent.call(this);
	}
});

