IG$.mgrUserEditor = $s.extend($s.window, {
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 580,
	height: 550,
	plain: true,
	bodyPadding: 10,
	
	initapp: function() {
		var panel = this,
			gid = panel.down("[name=gid]");
			
		gid.store.loadData(this.groupinfo);
		
		panel.buildCustomForm();
		panel.updateUserDetail();
		
		panel.loadTemplates();
		
		panel.loadAuthModule();
	},
	
	buildCustomForm: function() {
		var me = this,
			c_area = me.down("[name=c_area]"),
			uform = IG$.uform,
			i, uf, mf;
			
		for (i=0; i < uform.length; i++)
		{
			uf = uform[i];
			
			mf = {
				xtype: uf.type,
				fieldLabel: uf.displayname,
				name: uf.fieldname
			};
			
			c_area.add(mf);
		}
	},
	
	updateUserDetail: function() {
		var p = this,
			u = p.userinfo,
			l = IG$.dlgLogin.logininfo,
			userid = p.down("[name=userid]"),
			username = p.down("[name=username]"),
			email = p.down("[name=email]"),
			password = p.down("[name=password]"),
			password_plain = p.down("[name=password_plain]"),
			gid = p.down("[name=gid]"),
			pupdatedate = p.down("[name=pupdatedate]"),
			updatedate = IRm$.r1("L_LAST_UPD"),
			duties = p.down("[name=duties]"),
			auth = (u && u.auth) ? u.auth : [],
			btnaddduty = p.down("[name=btnaddduty]"),
			fieldstatus = p.down("[name=fieldstatus]"),
			i, uf, ufname;

		btnaddduty.setVisible((u && l.userid == u.id) ? false : true);
		userid.setReadOnly((u) ? true : false);
		userid.setValue(u ? u.id || "" : "");
		username.setValue(u ? u.name || "" : "");
		password.setValue(u ? "" : IG$.generateUniqueTest(10));
		password_plain.setValue(password.getValue());
		email.setValue(u ? u.email || "" : "");
		gid.setValue(u ? u.gid || "" : (p.currentGroup || ""));
		fieldstatus.setVisible(u ? true : false);
		updatedate = (u && u.updatedate) ? updatedate + u.updatedatefm : updatedate;
		pupdatedate.setValue(updatedate);
		duties.store.loadData(auth);
		
		if (IG$.uform)
		{
			for (i=0; i < IG$.uform.length; i++)
			{
				ufname = IG$.uform[i];
				
				uf = p.down("[name=" + ufname.fieldname + "]");
				
				if (uf)
				{
					uf.setValue(u && u.additional_info ? u.additional_info[ufname.fieldname] : "");
				}
			}
		}
	},
	
	confirmChanges: function(cmd) {
		var me = this,
			purpose,
			address,
			content,
			iserror = false,
			p = this,
			l = IG$.dlgLogin.logininfo, mduty,
			userid = p.down("[name=userid]"),
			username = p.down("[name=username]"),
			email = p.down("[name=email]"),
			password = p.down("[name=password]"),
			password_plain = p.down("[name=password_plain]"),
			gid = p.down("[name=gid]"),
			duties = p.down("[name=duties]"),
			i, fname, fobj,
			pwd = (me._password_mode ? password_plain.getValue() : password.getValue()),
			isfailed = false,
			userinfo = me.userinfo,
			uf,
			meta_type;
			
		if (cmd == "confirm")
		{
			if (!IG$.rsaPublicKeyModules)
			{
				var req = new IG$._rpc$();
				req.init(me, 
					{
						ack: "security",
						payload: {},
						mbody: {}
					}, me, function(jdoc) {
						var p1 = (jdoc ? jdoc.p1 : null),
							p2 = (jdoc ? jdoc.p2 : null);
						if (p1 && p2)
						{
							IG$.rsaPublicKeyModules = p1;
							IG$.rsaPublicKeyExpoenent = p2;
							IG$.rsa_kinfo = jdoc.p3;
							this.confirmChanges("confirm");
						}
					}, null);
				req.send();
				return;
			}
			
			purpose = "usercontent";
			address = {address: "/user/" + userid.getValue(), updatemode: "change"};
			content = {};
			
			$.each([userid, username, gid], function(i, pobj) {
				pobj.clearInvalid();
				
				if (!pobj.getValue())
				{
					pobj.markInvalid("Required");
					isfailed = true;
				}
			});
			
			if (isfailed)
			{
				return;
			}
			
			if (pwd)
			{
				pwd = IG$.encryptKey([pwd])[0];
				address.keyvar = IG$.rsa_kinfo;
			}
			
			content = {
				sid: (userinfo ? userinfo.sid : null),
				wid: (userinfo ? userinfo.wid : null),
				id: userid.getValue(),
				name: username.getValue(),
				password: pwd,
				email: email.getValue(),
				gid: gid.getValue(),
				a1: me.down("[name=a1]").getValue(),
				ssomod: me.down("[name=ssomod]").getValue(),
				additional_info: {}
			};
			
			if (IG$.uform)
			{
				for (i=0; i < IG$.uform.length; i++)
				{
					uf = IG$.uform[i];
					fname = uf.fieldname;
					fobj = me.down("[name=" + fname + "]");
					
					if (fobj)
					{
						if (!uf.optional)
						{
							fobj.clearInvalid();
							
							if (!fobj.getValue())
							{
								fobj.markInvalid("Required");
								
								return;
							}
						}
						
						content.additional_info[fname] = fobj.getValue();
					}
				}
			}
			
			content.duties = [];
			
			for (i=0; i < duties.store.data.items.length; i++)
			{
				mduty = duties.store.data.items[i];
				
				content.duties.push({
					dutytype: mduty.get("dutytype"),
					name: mduty.get("name"),
					sid: mduty.get("sid"),
					status: mduty.get("status")
				});
			}
			
			meta_type = "user";
		}
		else if (cmd == "delete")
		{
			if (userid.getValue() == l.userid)
			{
				IG$.ShowError(IRm$.r1("E_REMOVE_SELF"), p); // "You cannot remove yourself.", p);
				return;
			}
			
			purpose = "usercontent";
			address = {address: "/user/" + userid.getValue(), updatemode: "delete"};
			content = {sid: (me.userinfo ? me.userinfo.sid : "")};
			meta_type = "user";
		}
		else
		{
			return;
		}
		
		var panel = this,
			req = new IG$._rpc$();
		
		req.init(panel, 
			{
				ack: purpose,
				payload: address,
				mbody: content,
				meta_type: meta_type
			}, panel, panel.rs_confirmChanges, null);
		req.send();
	},
	
	rs_confirmChanges: function(xdoc) {
		var panel = this;
		
		if (panel.callback)
		{
			panel.callback.execute();
		}
		
		IG$.alertmsg(ig$.appname, IRm$.r1('M_SAVED'), null, null, 0, "success");
		
		panel.close();
	},
	
	setUserActive: function(user, status) {
		var panel = this,
			req = new IG$._rpc$(),
			content;
		
		if (status != "activate" && (user.name == "admin"))
		{
			IG$.alertmsg(ig$.appname, IRm$.r1('M_NA'), null, null, 0, "error");
			return;
		}
		
		content = {option: "changestatus", status: status, uid: user.sid};
		
		req.init(panel, 
			{
				ack: "usercontent",
				payload: {address: "/user/" + user.name},
				mbody: content
			}, panel, panel.rs_setUserActive, false, status);
		req.send();
	},
	
	rs_setUserActive: function(jdoc, status) {
		if (this.drow)
		{
			this.drow.set("status", (status == "1" ? "Active" : "Blocked"));
		}
		
		IG$.alertmsg(ig$.appname, IRm$.r1("M_SAVED"), null, null, 0, "success");
	},
	
	toolbarHandler: function(cmd) {
		var me = this,
			dlg,
			userinfo = me.userinfo;
		
		switch (cmd)
		{
		case "cmd_activate_user":
			if (userinfo)
			{
				me.setUserActive(userinfo, "1");
			}
			break;
		case "cmd_deprivate_user":
			if (userinfo)
			{
				me.setUserActive(userinfo, "2");
			}
			break;
		case "cmd_add_duty":
			var dp = [],
				rec,
				duties = me.down("[name=duties]").store.data.items,
				i;
			
			for (i=0; i < duties.length; i++)
			{
				rec = duties[i];
				dp.push({
					dutytype: rec.get("dutytype"),
					name: rec.get("name"),
					sid: rec.get("sid"),
					type: rec.get("type"),
					status: rec.get("status")
				});
			}
			dlg = new IG$.mgrUserDuty({
				sel: dp,
				callback: new IG$.callBackObj(me, me.rs_addDuty)
			});
			IG$.checkLogin(me, dlg);
			break;
		}
	},
	
	rs_addDuty: function(sel) {
		var me = this,
			duties = me.down("[name=duties]"),
			dp = [],
			rec,
			i;
		
		for (i=0; i < sel.length; i++)
		{
			rec = sel[i];
			dp.push({
				dutytype: rec.get("dutytype"),
				name: rec.get("name"),
				sid: rec.get("sid"),
				type: rec.get("type"),
				status: rec.get("status")
			});
		}
		duties.store.loadData(dp);
	},
	
	loadTemplates: function() {
		var me = this,
			req = new IG$._rpc$();
			
		req.init(me, 
			{
				ack: "helpcontent",
				payload: {},
				mbody: {option: "timezone"}
			}, me, function(jdoc) {
				var me = this,
					tnode,
					i, dp_tmpl = [],
					p,
					a1 = me.down("[name=a1]"),
					userinfo = me.userinfo;
					
				tnode = jdoc.app_tmpl;
				
				dp_tmpl.push({name: IRm$.r1("B_SELECT"), value: ""});
				
				for (i=0; i < tnode.length; i++)
				{
					p = tnode[i];
					p.name = p.value;
					dp_tmpl.push(p);
				}
				
				a1.store.loadData(dp_tmpl);
				a1.setValue(userinfo ? userinfo.a1 || "" : "");
			});
		req.send();
	},
	
	loadAuthModule: function() {
		var me = this,
			req = new IG$._rpc$(),
			ssomod = me.down("[name=ssomod]"),
			sdmodules,
			userinfo = me.userinfo ? me.userinfo : null;
		
		if (!IG$._ssomod)
		{
			req.init(me, 
				{
					ack: "usercontent",
					payload: {address: "/Features"},
					mbody: {option: "auth_module"}
				}, me, 
				function(jdoc) {
					var tnodes = jdoc ? jdoc.authorization : {},
						i,
						dp = [{name: "Select Module", value: ""}],
						dmodule = me.down("[name=dmodule]");
					
					for (i in tnodes)
					{
						dp.push({
							name: i,
							value: i
						});
					}
					
					IG$._ssomod = tnodes;
					ssomod.store.loadData(dp);
					ssomod.setValue(userinfo ? userinfo.ssomod || "" : "");
				}
			);
			req.send();
		}
		else
		{
			var i,
				tnodes = IG$._ssomod,
				dp = [{name: "Select Module", value: ""}];
			
			for (i in tnodes)
			{
				dp.push({
					name: i,
					value: i
				});
				
				ssomod.store.loadData(dp);
				ssomod.setValue(userinfo ? userinfo.ssomod || "" : "");
			}
		}
	},
	
	initComponent: function(){
		this.title = IRm$.r1("T_REG_USER");
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: "anchor",
					bodyPadding: 4,
					autoScroll: true,
					
					defaults: {
						anchor: "100%"
					},
					items: [
						{
							xtype: "textfield",
							fieldLabel: IRm$.r1("L_USERID"),
							name: "userid"
						},
						{
							xtype: "textfield",
							fieldLabel: IRm$.r1("L_USERNAME"),
							name: "username"
						},
						{
							xtype: "textfield",
							fieldLabel: IRm$.r1("L_EMAIL"),
							name: "email"
						},
						{
							xtype: "fieldcontainer",
							fieldLabel: IRm$.r1("L_INPUT_PASSWORD"),
							layout: "hbox",
							items: [
								{
									xtype: "textfield",
									name: "password",
									readOnly: false,
									inputType: "password",
									flex: 1
								},
								{
									xtype: "textfield",
									name: "password_plain",
									hidden: true,
									readOnly: false,
									// inputType: "password",
									flex: 1
								},
								{
									xtype: "button",
									name: "pgetpwd",
									text: IRm$.r1("B_GENERATE"),
									handler: function() {
										var me = this,
											password = me.down("[name=password]"),
											password_plain = me.down("[name=password_plain]"),
											temppwd = IG$.generateUniqueTest(10);
										
										password.setValue(temppwd);
										password_plain.setValue(temppwd);
									},
									scope: this
								},
								{
									xtype: "button",
									name: "btn_pwd",
									text: IRm$.r1("B_SHOW"),
									handler: function() {
										var me = this,
											btn_pwd = me.down("[name=btn_pwd]"),
											password = me.down("[name=password]"),
											password_plain = me.down("[name=password_plain]");
											
										me._password_mode = me._password_mode || 0;
										
										if (me._password_mode == 0)
										{
											me._password_mode = 1;
											password_plain.setValue(password.getValue());
										}
										else
										{
											me._password_mode = 0;
											password.setValue(password_plain.getValue());
										}
										
										btn_pwd.setText(me._password_mode == 1 ? IRm$.r1("B_HIDE") : IRm$.r1("B_SHOW"));
										password.setVisible(me._password_mode == 0);
										password_plain.setVisible(me._password_mode == 1);
									},
									scope: this
								}
							]
						},
						{
							xtype: "combobox",
							fieldLabel: IRm$.r1("L_GROUP_NAME"),
							labelField: "name",
							valueField: "gid",
							name: "gid",
							queryMode: 'local',
							displayField: 'name',
							valueField: 'gid',
							editable: false,
							autoSelect: true,
							store: {
								
								fields: [
									"name", "gid"
								]
							}
						},
						{
							xtype: "container",
							name: "c_area",
							layout: {
								type: "vbox",
								align: "stretch"
							}
						},
						{
							xtype: "combobox",
							name: "a1",
							fieldLabel: IRm$.r1("L_APPL_TMPL"),
							queryMode: "local",
							hidden: ig$.$l_ > 5,
							editable: false,
							valueField: "value",
							displayField: "name",
							store: {
								xtype: "store",
								fields: ["name", "value"]
							}
						},
						{
							xtype: "displayfield",
							value: IRm$.r1("L_LAST_UPD"),
							name: "pupdatedate"
						},
						{
							xtype: "fieldcontainer",
							fieldLabel: IRm$.r1("L_USER_DUTY"),
							layout: "hbox",
							items: [
								{
									xtype: "gridpanel",
									fieldLabel: IRm$.r1("L_USER_DUTY"), // User Duty",
									flex: 1,
									height: 160,
									hideLabel: true,
									name: "duties",
									store: {
										xtype: "store",
										fields: [
											"dutytype", "name", "sid", "status"
										]
									},
									columns: [
										{
											header: IRm$.r1("B_NAME"),
											flex: 1,
											sortable: false,
											hideable: false,
											dataIndex: "name"
										}
									]
								},
								{
									xtype: "button",
									iconCls: 'icon-toolbar-add',
									tooltip: "Add Duty",
									name: "btnaddduty",
									hidden: true,
									handler: function() {
										this.toolbarHandler('cmd_add_duty'); 
									},
									scope: this
								}
							]
						},
						{
							xtype: "fieldset",
							title: IRm$.r1("L_SSO"), // "Single Sign On",
							hidden: ig$.$l_ > 5,
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "combobox",
									name: "ssomod",
									fieldLabel: IRm$.r1("L_SSO_MODULE"), // SSO Module",
									displayField: "name",
									valueField: "value",
									store: {
										fields: ["name", "value"]
									},
									readOnly: false // true
								}
							]
						},
						{
							xtype: "fieldcontainer",
							fieldLabel: IRm$.r1("B_STATUS"),
							name: "fieldstatus",
							hidden: true,
							layout: "hbox",
							items: [
								{
									xtype: "button",
									text: IRm$.r1("L_ACTIVATE"),
									name: "btnactuser",
									handler: function() {
										this.toolbarHandler('cmd_activate_user');
									},
									scope: this
								},
								{
									xtype: "button",
									text: IRm$.r1("L_DEPRIVATE"),
									name: "btndeprivuser",
									handler: function() {
										this.toolbarHandler('cmd_deprivate_user');
									},
									scope: this
								}
							]
						}
					]
				}
			],
			buttons: [
				{
					xtype: "button",
					text: IRm$.r1("L_E_DEL"),
					handler: function() {
						this.confirmChanges("delete");
					},
					scope: this
				},
				"->",
				{
					xtype: "button",
					text: IRm$.r1("B_CONFIRM"),
					handler: function() {
						this.confirmChanges("confirm");
					},
					scope: this
				},
				{
					xtype: "button",
					text: IRm$.r1("B_CANCEL"),
					handler: function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(tobj) {
					this.initapp();
				}
			}
		});
		
		IG$.mgrUserEditor.superclass.initComponent.call(this);
	}
});


IG$.dlgSelUsers = $s.extend($s.window, {
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 480,
	height: 550,
	plain: true,
	bodyPadding: 10,
	
	initapp: function() {
		var me = this,
			req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "usercontent",
				payload: {address: "/user"},
				mbody: {option: "get"}
			}, me, function(jdoc) {
				var me = this,
					dt = jdoc || [],
					cusers = me.cusers,
					cmap = {},
					i;
				
				if (cusers)
				{
					for (i=0; i < cusers.length; i++)
					{
						cmap[cusers[i].get("sid")] = 1;
					}
					
					for (i=0; i < dt.length; i++)
					{
						dt[i].selected = cmap[dt[i].sid] ? true : false;
					}
				}
				
				me.down("[name=grdusers]").store.loadData(dt);
			});
		req.send();
	},

	initComponent: function(){
		this.title = IRm$.r1("T_USERS");
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: "anchor",
					bodyPadding: 4,
					autoScroll: true,
					
					defaults: {
						anchor: "100%"
					},
					items: [
						{
							xtype: "gridpanel",
							name: "grdusers",
							store: {
								fields: []
							},
							selType: "checkboxmodel",
							selModel: {
								mode: "MULTI"
							},
							columns: [
								{
									text: IRm$.r1("B_ID"),
									flex: 1,
									dataIndex: "id"
								},
								{
									text: IRm$.r1("B_NAME"),
									flex: 1,
									dataIndex: "name"
								},
								{
									text: IRm$.r1("B_EMAIL"),
									flex: 1,
									dataIndex: "email"
								},
								{
									text: IRm$.r1("L_GROUP_NAME"),
									flex: 1,
									dataIndex: "groupname"
								},
								{
									text: IRm$.r1("B_ACTIVE"),
									width: 40,
									dataIndex: "active"
								}
							]
						}
					]
				}
			],
			buttons: [
				"->",
				{
					xtype: "button",
					text: IRm$.r1("B_CONFIRM"),
					handler: function() {
						var me = this,
							selections = [],
							grdusers = me.down("[name=grdusers]"),
							selections = grdusers.getSelectionModel().selected;
						
						me.callback && me.callback.execute(me, selections);
						me.close();
					},
					scope: this
				},
				{
					xtype: "button",
					text: IRm$.r1("B_CANCEL"),
					handler: function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(tobj) {
					this.initapp();
				}
			}
		});
		
		IG$.dlgSelUsers.superclass.initComponent.call(this);
	}
});