IG$.sys_resource = $s.extend(IG$.v$pnl, {
	region:'center',
	
	layout: 'fit',
	
	closable: true,
	resizable:false,
	
	autoHeight: true,
	
	callback: null,
	
	bodyPadding: 10,
	
	iconCls: "icon-ing-docdef",
	
	in$t: function() {
		this.getSvrInfo();
	},
	
	getRcs: function() {
		var me = this,
			req = new IG$._rpc$();
						
		req.init(me, 
			{
				ack: "infosvr",
				payload: {address: "/resources"},
				mbody: {option: "getrs"}
			}, me, me.rs_getRcs, false);
		req.send();
	},
	
	getSvrInfo: function() {
		var me = this,
			req = new IG$._rpc$();
						
		req.init(me, 
			{
				ack: "infosvr",
				payload: {address: "/serverinfo"},
				mbody: {option: "getrs"}
			}, me, me.rs_getSvrInfo, false);
		req.send();
	},
	
	rs_getRcs: function(jdoc) {
		var me = this;
		
		me.setMemory(jdoc.meminfo);
		me.setDisk(jdoc.diskinfo);
		me.setOS(jdoc.osinfo);
	},
	
	rs_getSvrInfo: function(jdoc) {
		var me = this,
			tnode, tnodes,
			i;
			
		me.licinfo = jdoc;
		me.getRcs();
	},
	
	formatMem: function(v) {
		var unit = "b",
			d = 1,
			mv = v;
		
		if (mv > 1000)
		{
			unit = "M";
			mv = mv / 1000;
			
			if (mv > 1000)
			{
				unit = "G";
				mv = mv / 1000;
			}
		}
		
		return mv.toFixed(0) + " " + unit;
	},
	
	setMemory: function(tnode) {
		var me = this,
			m_charts,
			prop,
			g_mem = me.down("[name=g_mem]"),
			dp = [],
			k;
			
		if (!me.m_charts)
		{
			var p = me.down("[name=p_mem]"),
				el = $(".idv-rcs-mem", p.el.dom),
				pbox = [
					{
						name: "free",
						title: "Free Memory"
					},
					{
						name: "allocated",
						title: "Allocated"
					},
					{
						name: "appfree",
						title: "Application Free"
					},
					{
						name: "max",
						title: "Max Memory",
						chart: false
					},
					{
						name: "totalfree",
						title: "Free Total",
						chart: false
					}
				];
				
			el.empty();
			
			me.clen = 0;
			
			$.each(pbox, function(i, pb) {
				if (pb.chart != false)
				{
					var mel = $("<div class='idv-rcs-pnl'><div class='idv-rcs-title'>" + pb.title + "</div><div class='idv-rcs-chart'></div></div>").appendTo(el);
					pb.el = mel;
					pb.dc = $('.idv-rcs-chart', pb.el);
					pb.clabel = $("<div class='idv-rcs-label'></div>").appendTo(pb.dc);
					pb.chart = pb.dc.easyPieChart({
						//your configuration goes here
						size: IG$.j$ext._w(pb.dc)
					});
					me.clen ++;
				}
			});
			
			me.m_charts = pbox;
			
			me.resizeMemChart();
		}
		
		m_charts = me.m_charts;
		
		if (tnode)
		{
			prop = tnode;
			
			var tfree = Number(prop["totalfree"]),
				tmax = Number(prop["max"]);
			
			$.each(m_charts, function(i, pb) {
				var v = Number(prop[pb.name]),
					lbl;
				
				dp.push({
					name: pb.title,
					value: v
				});
				
				switch (pb.name)
				{
				case "free":
				case "appfree":
					pb.value = 100 * v / tfree;
					lbl = me.formatMem(v) + "<br>" + me.formatMem(tfree)
					break;
				case "allocated":
					pb.value = 100 * v / tmax;
					lbl = me.formatMem(v) + "<br>" + me.formatMem(tmax)
					break;
				}
				
				if (pb.chart)
				{
					pb.chart.data('easyPieChart').update(pb.value);
					pb.clabel.html(lbl);
				}
			});
		}
		
		g_mem.store.loadData(dp);
	},
	
	resizeMemChart: function() {
		var me = this,
			p_mem = me.down("[name=p_mem]"),
			p_el = $(p_mem.body.dom),
			rmem = $(".idv-rcs-mem", p_el),
			w = IG$.j$ext._w(p_el),
			h = IG$.j$ext._h(p_el),
			m_charts = me.m_charts,
			i, n,
			x = 0,
			pb;
		
		IG$.j$ext._w(rmem, w);
		IG$.j$ext._h(rmem, h);
		
		if (m_charts)
		{
			n = w / me.clen;
			
			for (i=0; i < m_charts.length; i++)
			{
				pb = m_charts[i];
				
				if (pb.chart)
				{
					IG$.j$ext._w(pb.el, n);
					IG$.j$ext._h(pb.el, h);
					pb.el.css("left", x);
					pb.dc.css({
						left: (n - 90) / 2,
						top: (h - 90) / 2 + 20
					});
					x += n;
				}
			}
		}
	},
	
	setDisk: function(tnode) {
		var me = this,
			g_disk = me.down("[name=g_disk]"),
			dp = [],
			p_dsk = me.down("[name=p_dsk]"),
			el = $(".idv-rcs-dsk", p_dsk.el.dom),
			mel, pb,
			i, v;
		
		me.dlen = 0;
		me.m_dsks = [];
		me.h_dsks = me.h_dsks || {};
		
		if (tnode)
		{
			for (i=0; i < tnode.length; i++)
			{
				p = tnode[i];
				
				if (me.h_dsks[p.name])
				{
					pb = me.h_dsks[p.name];
				}
				else
				{
					mel = $("<div class='idv-rcs-pnl-dsk'><div class='idv-rcs-title'>" + p.name + "</div><div class='idv-rcs-chart'></div></div>").appendTo(el);
					pb = {
						el: mel,
						dc: $('.idv-rcs-chart', mel)
					};
					pb.clabel = $("<div class='idv-rcs-label'></div>").appendTo(pb.dc);
					pb.chart = pb.dc.easyPieChart({
						//your configuration goes here
						size: IG$.j$ext._w(pb.dc)
					});
					
					me.h_dsks[p.name] = pb;
				}
				v = Number(p.total) - Number(p.usable);
				v = (Number(p.total) > 0 ? v * 100 / Number(p.total) : 0);
				pb.chart.data('easyPieChart').update(v);
				pb.clabel.text(p.name + " : " + v.toFixed(0) + "%");
				me.dlen ++;
				
				dp.push(p);
				me.m_dsks.push(pb);
			}
			
			me.resizeDiskChart();
		}
		
		g_disk.store.loadData(dp);
	},
	
	resizeDiskChart: function() {
		var me = this,
			p_dsk = me.down("[name=p_dsk]"),
			p_el = $(p_dsk.body.dom),
			rdsk = $(".idv-rcs-dsk", p_el),
			w = IG$.j$ext._w(p_el),
			h = IG$.j$ext._h(p_el),
			m_dsks = me.m_dsks,
			i, n,
			y = 0,
			pb;
		
		IG$.j$ext._w(rdsk, w);
		IG$.j$ext._h(rdsk, h);
		
		if (m_dsks)
		{
			for (i=0; i < m_dsks.length; i++)
			{
				pb = m_dsks[i];
				
				if (pb.chart)
				{
					IG$.j$ext._w(pb.el, w);
					n = IG$.j$ext._h(pb.el);
					pb.dc.css({
						left: (w - 90) / 2,
						top: (n - 90) / 2 + 20
					});
				}
			}
		}
	},
	
	setOS: function(tnode) {
		var me = this,
			p_os = me.down("[name=p_os]"),
			el = $(".idv-os-pnl", p_os.el.dom),
			ui, li, ap,
			p,
			i,
			licinfo = me.licinfo;
		
		el.empty();
		
		if (tnode)
		{
			ul = $("<ul class='idv-os-ul'></ul>").appendTo(el);
			
			for (i in tnode)
			{
				li = $("<li><span class='idv-os-type'>" + i + "</span> : <span class='idv-os-val'>" + tnode[i] + "</span></li>");
				ul.append(li);
			}
			
			ul = $("<div class='idv-lic-dv'><span>License Information</span></div>").appendTo(el);
			ul = $("<ul class='idv-lic-ul'></ul>").appendTo(ul);
			
			if (licinfo)
			{
				for (p in licinfo)
				{
					if (p != "access_ip" && p != "update_history")
					{
						li = $("<li><span class='idv-os-type'>" + p + "</span> : <span class='idv-os-val'>" + licinfo[p] + "</span></li>");
						ul.append(li);
					}
				}
				
				if (licinfo.access_ip)
				{
					ul = $("<div class='idv-lic-dv'><span>Accessed IP</span></div>").appendTo(el);
					
					for (i=0; i < licinfo.access_ip.length; i++)
					{
						li = $("<li><span class='idv-os-val'>" + licinfo.access_ip[i] + "</span></li>"); 
						ul.append(li);
					}
				}
			}
		}
	},
	
	initComponent : function() {
		var me = this;
		
		me.title = IRm$.r1("T_SYS_RES");
		
		$s.apply(this, {
			tbar: [
				{
					xtype: "button",
					iconCls: "icon-refresh",
					tooltip: IRm$.r1("L_REFRESH"),
					handler: function() {
						this.getSvrInfo();
					},
					scope: this
				}
			],
			items: [
				{
					xtype: "panel",
					layout: "border",
					border: 0,
					items: [
						{
							xtype: "panel",
							region: "north",
							height: 200,
							border: 0,
							layout: {
								type: "hbox",
								align: "stretch"
							},
							items: [
								
								{
									xtype: "panel",
									name: "p_mem",
									title: "Memory Resources",
									layout: "fit",
									flex: 1,
									html: "<div class='idv-rcs-mem'></div>",
									listeners: {
										resize: function() {
											this.resizeMemChart();
										},
										scope: this
									}
								}
							]
						},
						{
							xtype: "panel",
							region: "center",
							border: 1,
							layout: {
								type: "hbox",
								align: "stretch"
							},
							flex: 1,
							items: [
								{
									xtype: "panel",
									flex: 1,
									layout: {
										type: "vbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "gridpanel",
											title: "Memory",
											name: "g_mem",
											flex: 1,
											store: {
												xtype: "store",
												fields: ["name", "value"]
											},
											columns: [
												{
													xtype: "gridcolumn",
													text: IRm$.r1("B_NAME"),
													flex: 1,
													dataIndex: "name"
												},
												{
													xtype: "gridcolumn",
													text: IRm$.r1("B_VALUE"),
													dataIndex: "value",
													flex: 1
												}
											]
										},
										{
											xtype: "gridpanel",
											title: "Disk",
											name: "g_disk",
											flex: 1,
											store: {
												xtype: "store",
												fields: ["name", "total", "free", "usable"]
											},
											columns: [
												{
													xtype: "gridcolumn",
													text: IRm$.r1("B_NAME"),
													flex: 1,
													dataIndex: "name"
												},
												{
													xtype: "gridcolumn",
													text: "Total",
													dataIndex: "total",
													flex: 1
												},
												{
													xtype: "gridcolumn",
													text: "Free",
													dataIndex: "free",
													flex: 1
												},
												{
													xtype: "gridcolumn",
													text: "usable",
													dataIndex: "usable",
													flex: 1
												}
											]
										}
									]
								},
								{
									xtype: "panel",
									name: "p_dsk",
									flex: 1,
									layout: "fit",
									html: "<div class='idv-rcs-dsk'></div>",
									listeners: {
										resize: function() {
											this.resizeDiskChart();
										},
										scope: this
									}
								},
								{
									xtype: "panel",
									title: "System Info",
									name: "p_os",
									layout: "fit",
									flex: 1,
									html: "<div class='idv-os-pnl'></div>"
								}
							]
						}
					]
				}
			],
			
			listeners: {
				afterrender: function(ui) {
					this.in$t();
				}
			}
		});
		
		IG$.sys_resource.superclass.initComponent.apply(this, arguments);
	}
});