	openDSLayer: function(cmd, rec) {
		var me = this,
			mp = IG$.mainPanel;
			
		switch(cmd)
		{
		case "cmd_edit_config":
			if (rec.get("writable"))
			{
				me.loadDBDetail(rec.get("uid"));
			}
			break;
		case "cmd_tables":
			var dlgitemsel = new IG$.metaSelectDlg({
				mode: "newitem",
				initpath: null,
				callback: new IG$.callBackObj(me, function(item) {
					var req = new IG$._rpc$();
					req.init(me, 
						{
							ack: "writecontent",
							payload: {
								address: item.nodepath + "/" + item.name,
								name: item.name,
								type: "Bobject",
								pid: item.uid
							},
							mbody: {
								datasource: {
									uid: rec.get("uid"),
									name: rec.get("name"),
									type: rec.get("type")
								}
							},
							meta_type: "business_object"
						}, me, function(jdoc) {
							mp.navigateApp.call(mp, jdoc.uid, jdoc.type.toLowerCase(), jdoc.name, jdoc.nodepath, true, jdoc.writable);
						});
					req.send();
				})
			});
			IG$.checkLogin(this, dlgitemsel);
			break;
		case "cmd_object_auth":
			if (rec.get("writable") == true)
			{
				var dlgpop;
				dlgpop = new IG$.objectAuth({
					uid: rec.get("uid"), 
					itemtype: rec.get("type")
				});
				
				IG$.checkLogin(this, dlgpop);
			}
			break;
		}
	},
	
