IG$.ExcelFileUploader = $s.extend($s.window, {
	
	modal: true,
	region:'center',
	
	layout: 'fit',
	
	closable: false,
	resizable:false,
	
	ufiles: null,
	
	width: 360,
	autoHeight: true,
	
	processUploadedFile: function(uid) {
		var me = this,
			// fieldvalue = me.down("[name=fieldvalue]"),
			uploadmode = me.down("[name=uploadmode]"),
			delimiter = me.down("[name=delimiter]"),
			deletemode = me.down("[name=deletemode]"),
			targetsheet = me.down("[name=targetsheet]"),
			file_encoding = me.down("[name=file_encoding]");
		
		// me.datalabel = fieldvalue.getValue();
		me.uploadmode = uploadmode.getValue();
		me.deletemode = deletemode.getValue();
		me.delimiter = delimiter.getValue();
		me.targetsheet = targetsheet.getValue();
		me.file_encoding = file_encoding.getValue();
		me.fileuid = uid;
		
		/*
		if (me.mode != "rcs" && fieldvalue.getValue() == "" && me.uploadmode == false)
		{
			IG$.ShowError(IRm$.r1("L_MSG_TABNAME"), me);
			return;
		}
		*/
		
		me.callback && me.callback.execute(me);
		
		me.close();
	},
	
	_confirm: function() {
		var me = this,
			mform = me.down('[name=mform]'),
			orig_filename = me.down("[name=orig_filename]"),
			photo = me.down("[name=photo]"),
			file_encoding = me.down("[name=file_encoding]");

		if (mform.getForm().isValid()) {
			var f = mform.getForm();
			// $(f).attr("accept-charset", "UTF-8");
			document.charset = "UTF-8";
			var fname = photo.getRawValue();
			if (fname.indexOf("\\") > -1)
			{
				fname = fname.substring(fname.lastIndexOf("\\")+1);
			}
			
			orig_filename.setValue(fname);
			
			mform.submit({
				url: ig$.servlet,
				waitMsg: 'Uploading your data file',
				success: function(fp, o) {
					var node = fp.errorReader.jsonData,
						uid = node && node.result ? node.result.uid : null;
					
					me.processUploadedFile(uid);
					me.__cprog = 0;
				}
			});
		}
		else
		{
			me.__cprog = 0;
		}
	},
	
	initComponent : function() {
		var me = this;
		
		me.title = IRm$.r1('T_DATA_UPLOAD');
		
		$s.apply(me, {
			
			layout: 'fit',
			
			items: [
				{
					xtype: "panel",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					bodyPadding: 10,
					items: [
						{
							xtype: "form",
							name: 'mform',
							layout: "anchor",
							items: [
								{
									xtype: "hiddenfield",
									name: "_mts_",
									value: IG$._g$a
								},
								{
									xtype: "textfield",
									name: "orig_filename",
									hidden: true,
									fieldLabel: IRm$.r1("L_ORIG_NAME")
								},
								{
									xtype: 'fileuploadfield',
									name: 'photo',
									fieldLabel: IRm$.r1("L_DATA"),
									// labelWidth: 100,
									msgTarget: 'side',
									allowBlank: false,
									anchor: '100%',
									// width: 320,
									buttonText: IRm$.r1("L_SELECT_FILE"),
									buttonConfig: {
										margin: "0 2 0"
									}
								},
								{
									xtype: "combobox",
									fieldLabel: IRm$.r1("B_DELIMITER"),
									hidden: me.mode == "rcs",
									name: "delimiter",
									queryMode: "local",
									autoSelect: true,
									autoScroll: true,
									editable: false,
									labelField: "name",
									displayField: "name",
									valueField: "value",
									store: {
										fields: [
											"name", "value"
										],
										data: [
											{name: ",", value: ","},
											{name: "Tab", value: "tab"},
											{name: "space", value: "space"},
											{name: "|", value: "|"}
										]
									}
								},
								/*
								{
									xtype: "textfield",
									name: "fieldvalue",
									hidden: me.mode == "rcs",
									fieldLabel: IRm$.r1("L_FIELD_VALUE"),
									// labelWidth: 100,
									allowBlank: me.mode == "rcs" ? true : false
								},
								*/
								{
									xtype: "combobox",
									name: "file_encoding",
									hidden: window.ig$.file_encoding && window.ig$.file_encoding.length > 0 ? false : true,
									fieldLabel: IRm$.r1("L_ENCODING"),
									editable: false,
									queryMode: "local",
									labelField: "name",
									valueField: "value",
									displayField: "name",
									store: {
										xtype: "store",
										fields: [
											"name", "value"
										],
										data: window.ig$.file_encoding || []
									},
									listeners: {
										afterrender: function(ui) {
											if (window.ig$.file_encoding && window.ig$.file_encoding.length > 0)
											{
												ui.select(window.ig$.file_encoding[0].value);
											}
										}
									}
								},
								{
									xtype: "fieldset",
									title: IRm$.r1("B_REPLACE_DATA_OPTIONS"), // "Replace Data Options",
									hidden: (me.mode != "rcs" && me.ufiles && me.ufiles.length > 0 ? false : true),
									layout: "anchor",
									defaults: {
										anchor: "100%"
									},
									items: [
										{
											xtype: "checkbox",
											name: "uploadmode",
											hidden: (me.ufiles && me.ufiles.length > 0 ? false : true),
											hideLabel: true,
											boxLabel: IRm$.r1("L_FILE_APPEND"),
											listeners: {
												change: function(field, newvalue, oldvalue, eOpts) {
													var me = this,
														ctrl = field,
														deletectrl = me.down("[name=deletemode]"),
														targetsheet = me.down("[name=targetsheet]"),
														// fieldvalue = me.down("[name=fieldvalue]"),
														checked = ctrl.getValue();
													  
													// fieldvalue.allowBlank = checked;
													targetsheet.setVisible(checked);
													deletectrl.setVisible(checked);
												},
												scope: this
											}
										},
										{
											xtype: "combobox",
											fieldLabel: IRm$.r1("L_DATA_FILE"),
											name: "targetsheet",
											queryMode: "local",
											hidden: true,
											autoSelect: true,
											autoScroll: true,
											editable: false,
											labelField: "name",
											displayField: "name",
											valueField: "uid",
											store: {
												fields: [
													"name", "uid", "type", "nodepath"
												],
												data: me.ufiles
											}
										},
										{
											xtype: "checkbox",
											name: "deletemode",
											hidden: true,
											fieldLabel: " ",
											hideLabel: true,
											boxLabel: IRm$.r1("L_FILE_DELETE")
										}
									]
								}
							]
						}
					]
				}
			],
			
			buttons:[
				{
					text: IRm$.r1('B_CONFIRM'),
					handler: function() {
						var me = this;
						
						if (me.__cprog)
						{
							IG$.alertmsg(ig$.appname, IRm$.r1("M_PROC_WAIT"), null, null, 0, "warning");
							return;
						}
						
						me.__cprog = 1;
						me._confirm();
					},
					scope: this
				},
				{
					text: IRm$.r1('B_CANCEL'),
					handler: function() {
						this.close();
					},
					scope: this
				}
			],
			
			listeners: {
				afterrender: function(panel) {
					var me = this,
						delimiter = me.down("[name=delimiter]"),
						targetsheet = me.down("[name=targetsheet]"),
						ufiles = me.ufiles,
						i, selindex = 0;
					delimiter.setValue(",");
					
					if (ufiles && ufiles.length > 0)
					{
						for (i=0; i < ufiles.length; i++)
						{
							if (ufiles[i].selected == true)
							{
								selindex = i;
								break;
							}
						}
						
						targetsheet.setValue(ufiles[selindex].uid);
					}
					
					var mform = panel.down('[name=mform]');
					mform.getForm().errorReader = new IG$.m2ER();
				}
			}
		});

		IG$.ExcelFileUploader.superclass.initComponent.apply(this, arguments);
	}
});