IG$.nlpProg = $s.extend($s.window, {
	
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 600,
	height: 500,
	
	title: "NLP Intent Program",
	
	_init: function() {
		var me = this,
			datarec = me.datarec;
			
		if (datarec)
		{
			me.down("[name=intent]").setValue(datarec.get("intent"));
			me.down("[name=name]").setValue(datarec.get("name"));
			me.down("[name=description]").setValue(datarec.get("description"));
			
			if (datarec.get("java_source"))
			{
				me.down("[name=java_source]").setValue(datarec.get("java_source").join("\n"));
			}
		}
	},
	
	_confirm: function() {
		var me = this,
			data = {}, dnames = ["intent", "name", "description", "java_source"],
			is_error = 0;
			
		$.each(dnames, function(i, d) {
			var c = me.down("[name=" + d + "]");
			
			c.clearInvalid();
			if (c.required && !c.getValue())
			{
				c.markInvalid();
				is_error = 1;
				return;
			}
			
			if (d == "java_source")
			{
				data[d] = c.getValue().split("\n");
			}
			else
			{
				data[d] = c.getValue();
			}
		});
		
		if (is_error)
			return;
			
		me.callback && me.callback.execute(data);
		me.close();
	},
	
	initComponent : function() {
		var me = this;
		
		$s.apply(me, {
			padding: 5,
			border: 0,
			items: [
				{
					xtype: "panel",
					layout: "fit",
					padding: 10,
					autoScroll: true,
					items: [
						{
							xtype: "panel",
							layout: {
								type: "vbox",
								align: "stretch"
							},
							padding: 5,
							items: [
								{
									xtype: "fieldset",
									paddingY: 40,
									layout: {
										type: "vbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "textfield",
											fieldLabel: IRm$.r1("L_INTENT_NAME"), // "Intent Name",
											name: "intent",
											required: true
										},
										{
											xtype: "textfield",
											fieldLabel: IRm$.r1("B_NAME"),
											name: "name",
											required: true
										},
										{
											xtype: "textarea",
											height: 50,
											fieldLabel: IRm$.r1("B_DESC"),
											name: "description"
										}
									]
								},
								{
									xtype: "textarea",
									flex: 1,
									name: "java_source",
									fieldLabel: IRm$.r1("L_JAVA_SOURCE"), // "Java Source",
									labelPosition: "top",
									required: true
								}
							]
						}
					]
				}
									
			],
			buttons:[
				"->",
				{
					text: IRm$.r1('B_CONFIRM'),
					handler: function() {
						this._confirm();
					},
					scope: this
				}, 
				{
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(tobj) {
					tobj._init();
				},
				scope: this
			}
		});
		
		IG$.nlpProg.superclass.initComponent.apply(this, arguments);
	}
});