IG$.userdbTemplate = $s.extend($s.window, {
	
	modal: true,
	region:'center',
	closable: false,
	resizable:false,
	width: 320,
	height: 400,
	
	confirm: function(){
		var panel = this,
			fielditems = panel.fielditems,
			fname, fvalue,
			i;
		
		for (i=0; i < fielditems.length; i++)
		{
			fname = fielditems[i].fname;
			fvalue = panel.down("[name=" + fname + "]").getValue();
			
			fielditems[i].uvalue = fvalue;
			
			if (fielditems[i].optional != true)
			{
				if (!fvalue)
				{
					IG$.alertmsg(ig$.appname, IRm$.r1('T_DB_BLANK'), null, panel, null, null, 1, "error");
					return;
				}
			}
		}
		
		var nvalue = panel.applyValue();
			
		if (panel.callback)
		{
			panel.callback.execute(nvalue);
		}
		
		panel.close();
	},
	
	applyValue: function() {
		var me = this,
			fieldvalue = me.fieldvalue,
			fielditems = me.fielditems,
			fname, fvalue, i,
			fieldmap = {},
			rvalue;
		
		for (i=0; i < fielditems.length; i++)
		{
			fieldmap[fielditems[i].fname] = fielditems[i].uvalue;
		}
		
		rvalue = me.processValue(fieldvalue, fieldmap, false);
		
		return rvalue;
	},
	
	processValue: function(fieldvalue, fieldmap, isblock) {
		var me = this,
			rvalue = [],
			i,
			c,
			inblock = false,
			inoptblock = false,
			mval,
			mbval, 
			hasvalue = false;
		
		for (i=0; i < fieldvalue.length; i++)
		{
			c = fieldvalue[i];
			switch (c)
			{
			case "<":
				if (inoptblock == true)
				{
					mbval += c;
				}
				else
				{
					inblock = true;
					mval = "";
				}
				break;
			case "[":
				inoptblock = true;
				inblock = false;
				mbval = "";
				break;
			case ">":
				if (inoptblock == false && inblock == true)
				{
					if (fieldmap[mval])
					{
						rvalue.push(fieldmap[mval]);
						hasvalue = true;
					}
					inblock = false;
				}
				else if (inoptblock == true)
				{
					mbval += c;
				}
				break;
			case "]":
				inoptblock = false;
				var optproc = me.processValue(mbval, fieldmap, true);
				rvalue.push(optproc);
				break;
			default:
				if (inoptblock == true)
				{
					mbval += c;
				}
				else if (inblock == true)
				{
					mval += c;
				}
				else
				{
					rvalue.push(c);
				}
				break;
			}
		}
		
		if (hasvalue == false && isblock == true)
		{
			return "";
		}
		
		return rvalue.join("");
	},
	
	parseValue: function(value) {
		var fielditems = [],
			i, c, item, mval, isoptional = false, inblock = false;
		
		for (i=0; i < value.length; i++)
		{
			c = value[i];
			switch (c)
			{
			case "<":
				inblock = true;
				mval = c;
				break;
			case "[":
				isoptional = true;
				break;
			case ">":
				if (inblock == true)
				{
					inblock = false;
					mval += c;
					fielditems.push({
						field: mval,
						fname: mval.substring(1, mval.length-1),
						optional: isoptional
					});
				}
				break;
			case "]":
				isoptional = false;
				break;
			default:
				if (inblock == true)
				{
					mval += c;
				}
				break;
			}
		}
		
		return fielditems;
	},
	
	initComponent : function() {
		var me = this,
			items = [],
			fieldvalue = me.fieldvalue,
			fielditems,
			i;
			
		me.title = IRm$.r1('T_DB_TMPL');
		
		me.fielditems = fielditems = me.parseValue(fieldvalue);
		
		for (i=0; i < fielditems.length; i++)
		{
			items.push({
				fieldLabel: fielditems[i].fname,
				name: fielditems[i].fname,
				value: '',
				allowBlank: fielditems[i].optional,
				blankText: IRm$.r1("B_REQ")
			});
		}
		
		$s.apply(this, {
			padding: 10,
			layout: {
				type: "vbox",
				align: "stretch"
			},
			items: [
				{
					xtype: "panel",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					autoScroll: true,
					items: [
						{
							region:'center',
							id: 'dlgmakemetaform',
							defaultType: 'textfield',
							layout: {
								type: "vbox",
								align: "stretch"
							},
							defaults: {
								labelWidth: 80
							},
							items: items
						}
					]
				}
			],
			buttons:[
				{
					text: IRm$.r1('B_CONFIRM'),
					handler: function() {
						this.confirm();
					},
					scope: this
				}, 
				{
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			]
		});
		
		IG$.userdbTemplate.superclass.initComponent.apply(this, arguments);
	}
});