IG$.mgrFeatures = $s.extend(IG$.v$pnl, {
	scroll: false,
	closable: true,
	layout: "border",
	
	userinfo: null,
	groupinfo: null,
	
	iconCls: "icon-doc-features",
	
	loadAuth: function() {
		var panel = this,
			req = new IG$._rpc$();
		req.init(panel, 
			{
				ack: "usercontent",
				payload: {address: "/Auth"},
				mbody: {option: 'select'}
			}, panel, panel.r_loadAuth, false);
		req.send();
	},
	
	r_loadAuth: function(jdoc) {
		var i,
			cnodes,
			duty,
			gridduty = this.down("[name=gridduty]"),
			smodel = gridduty.getSelectionModel(),
			dn;
		
		if (jdoc)
		{
			for (i=0; i < jdoc.length; i++)
			{
				duty = jdoc[i];
				duty.active = duty.status == "1" ? "Active" : "Disabled";
				switch (duty.dutytype)
				{
				case "A":
					dn = IRm$.r1("B_DEFAULT");
					break;
				case "G":
					dn = "Group";
					break;
				case "C":
					dn = "Custom";
					break;
				case "U":
					dn = "User";
					break;
				default:
					dn = duty.dutytype;
					break;
				}
				duty.dutytype_d = dn;
			}
		}
		
		gridduty.store.loadData(jdoc);
		
		this.loadFeatures();
	},
	
	loadFeatures: function() {
		var panel = this,
			req = new IG$._rpc$();
		req.init(panel, 
			{
				ack: "usercontent",
				payload: {address: "/Features"},
				mbody: {option: 'get'}
			}, panel, panel.r_loadFeatures, false);
		req.send();
	},
	
	upd_locale: function(nodes) {
		var me = this,
			i,
			node,
			cls;
		
		if (nodes && nodes.length) {
			for (i=0; i < nodes.length; i++)
			{
				node = nodes[i];
				cls = "LR_" + node.cls.toUpperCase();
				
				node.description = IRm$.r1(cls) || "";
				
				if (node.children)
				{
					me.upd_locale(node.children);
				}
			}
		}
	},
	
	r_loadFeatures: function(jdoc) {
		var me = this,
			tf = me.down("[name=tf]"),
			mroot;
			
		if (jdoc)
		{
			mroot = {
				name: "Root",
				children: jdoc
			};
			
			me.upd_locale(mroot.children);
			// this._r3/*formatData*/(tval, mroot, "");
			tf.store.setRootNode(mroot);
			tf.store.getRootNode().expand(true);
			
			me.recordmap = [];
			
			me.getRecordMap(tf.store.getRootNode());
		}
	},
	
	saveFeatures: function() {
		var me = this,
			gridduty = me.down("[name=gridduty]"),
			sel = gridduty.getSelectionModel().selected,
			tf = me.down("[name=tf]"),
			troot = tf.store.getRootNode(),
			feature = {
				features: [] 
			},
			i,
			sid;
		
		if (sel && sel.length > 0)
		{
			sid = sel.items[0].get("sid");
			
			feature.uid = sid;
			me.updateFeature(troot, feature.features);
			
			var panel = this,
				req = new IG$._rpc$();
			req.init(panel, 
				{
					ack: "writecontent",
					payload: {uid: sid, type: "feature"},
					mbody: feature,
					meta_type: "features"
				}, panel, panel.r_saveFeatures, false);
			req.send();
		}
	},
	
	r_saveFeatures: function(xdoc) {
		IG$.alertmsg(ig$.appname, IRm$.r1("M_SAVED"), null, null, 0, "success");
	},
	
	updateFeature: function(tparent, feature) {
		if (tparent.childNodes)
		{
			var me = this,
				i,
				rec,
				f;
			for (i=0; i < tparent.childNodes.length; i++)
			{
				rec = tparent.childNodes[i];
				if (rec.get("mshow"))
				{
					f = {
						name: rec.get("name"),
						cls: rec.get("cls"),
						clspath: rec.get("clspath"),
						show: rec.get("mshow") || false
					}
					feature.push(f);
				}
				me.updateFeature(rec, feature);
			}
		}
	},
	
	loadFeature: function() {
		var me = this,
			dfn = me.down("[name=dfn]"),
			dfn_v = me.down("[name=dfn_v]"),
			btn_save = me.down("[name=btn_save]"),
			gridduty = me.down("[name=gridduty]"),
			sel = gridduty.getSelectionModel().selected,
			sid;
			
		if (sel.length > 0)
		{
			sid = sel.items[0].get("sid");
			dfn.setValue(IRm$.r1("B_ROLE"));
			dfn_v.setValue(sel.items[0].get("name"));
			dfn_v.show();
			btn_save.setDisabled(false);
			var panel = this,
				req = new IG$._rpc$();
			req.init(panel, 
				{
					ack: "usercontent",
					payload: {address: "/Features", sid: sid},
					mbody: {option: 'content'}
				}, panel, panel.r_loadFeature, false);
			req.send();
		}
		else
		{
			dfn.setValue(IRm$.r1("L_SEL_DUTY"));
			dfn_v.setValue("");
			dfn_v.hide();
		}
	},
	
	r_loadFeature: function(jdoc) {
		var me = this,
			recmap = me.recordmap,
			tf = me.down("[name=tf]"),
			i, p, rec, pmap = {},
			ps, ph;
			
		if (jdoc && jdoc.features)
		{	
			for (i=0; i < jdoc.features.length; i++)
			{
				p = jdoc.features[i];
				pmap[p.clspath] = p;
			}
		}
		
		tf.suspendLayouts();
		tf.suspendEvents(false);
		tf.store.suspendEvents(false);
		
		for (i=0; i < recmap.length; i++)
		{
			rec = recmap[i];
			p = pmap[rec.get("clspath")];
			ps = p ? p.show : false;
			rec.get("mshow") != ps && rec.set("mshow", ps);
		}
		tf.store.resumeEvents();
		tf.resumeEvents();
		tf.resumeLayouts();
	},
	
	getRecordMap: function(trec) {
		if (trec.childNodes)
		{
			var i,
				me = this,
				rec;
			
			for (i=0; i < trec.childNodes.length; i++)
			{
				rec = trec.childNodes[i];
				me.recordmap.push(rec);
				
				me.getRecordMap(rec);
			}
		}
	},
	
	updateTreeNode: function(rec, status) {
		var me = this,
			i;
		
		if (rec.childNodes && rec.childNodes.length)
		{
			for (i=0; i < rec.childNodes.length; i++)
			{
				me.updateTreeNode(rec.childNodes[i], status);
				rec.childNodes[i].set("mshow", status);
			}
		}
	},
	
	_exp_meta: function() {
		var dlg = new IG$.metaExpDlg({
				dmode: "security",
				meta: {
					uid: "/features"
				}
			});
		
		IG$.checkLogin(this, dlg);
	},
	
	initComponent: function(){
		var me = this;
		
		me.title = IRm$.r1('T_FEATURES');
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					width: 270,
					region: "west",
					title: IRm$.r1("L_DUTY"), // "Duties",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: [
						{
							xtype: "container",
							layout: {
								type: "hbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "textfield",
									enableKeyEvents: true,
									name: "tkeyword",
									emptyText: IRm$.r1("L_SEARCH_KWD"),
									listeners: {
										keyup: function(tobj, e, eopts) {
											e.keyCode == 13 && IG$._dofilter(this, "gridduty", ["name", "type"], tobj);
										},
										scope: this
									}
								},
								{
									xtype: "button",
									// text: "..",
									iconCls: "icon-toolbar-search",
									tooltip: IRm$.r1("B_SEARCH"),
									handler: function() {
										IG$._dofilter(this, "gridduty", ["name", "type"], this.down("[name=tkeyword]"));
									},
									scope: this
								}
							]
						},
						{
							xtype: "grid",
							name: "gridduty",
							// hideHeaders: true,
							selType: "checkboxmodel",
							flex: 1,
							selModel: {
								checkSelector: ".x-grid-cell",
								mode: "SINGLE"
							},
							store: {
								xtype: "store",
								fields: [
									"sid", "name", "active", "status", "dutytype", "dutytype_d"
								]
							},
							columns: [
								{
									dataIndex: "dutytype_d",
									text: IRm$.r1("B_TYPE"),
									width: 60
								},
								{
									dataIndex: "name",
									text: IRm$.r1("B_NAME"),
									flex: 1
								},
								{
									dataIndex: "active",
									width: 80,
									text: IRm$.r1("B_STATUS")
								}
							],
							tbar: [
								{
									text: IRm$.r1("L_LOAD_AUTH"), // "Load Auth",
									handler: function() {
										this.loadAuth();
									},
									scope: this
								},
								{
									text: IRm$.r1("B_REFRESH"),
									handler: function() {
										this.loadFeatures();
									},
									scope: this
								},
								"->",
								{
									text: IRm$.r1("L_EXPORT_META"),
									hidden: ig$.$l_ > 5,
									handler: function() {
										this._exp_meta();
									},
									scope: this
								}
							],
							listeners: {
								selectionchange: function(tobj, selected, eOpts) {
									this.loadFeature();
								},
								scope: this
							}
						}
					]
				},
				{
					xtype: "panel",
					region: "center",
					flex: 1,
					title: IRm$.r1("L_FEASURES"), // "Features",
					layout: "fit",
					items: [
						{
							xtype: "treepanel",
							name: "tf",
							// hidden: true,
							store: {
								xtype: "treestore",
								fields: [
									"name", "cls", "clspath", "mshow"
								]
							},
							columns: [
								{
									xtype: "treecolumn",
									text: IRm$.r1("B_NAME"),
									dataIndex: "name",
									flex: 1
								},
								{
									dataIndex: "description",
									text: IRm$.r1("B_DESC"),
									flex: 1
								},
								{
									xtype: "gridcolumn",
									dataIndex: "cls"
								},
								{
									xtype: "checkcolumn",
									dataIndex: "mshow",
									text: IRm$.r1("B_SHOW"),
									width: 80
								}
							],
							tbar: [
								{
									xtype: "displayfield",
									name: "dfn",
									value: IRm$.r1("L_SEL_DUTY") // "Click Duty to set features"
								},
								{
									xtype: "textfield",
									flex: 1,
									hidden: true,
									readOnly: true,
									name: "dfn_v"	
								},
								"-",
								{
									text: IRm$.r1("B_SAVE"),
									name: "btn_save",
									xtype: "button",
									disabled: true,
									iconCls: 'icon-toolbar-save',
									handler: function() {
										this.saveFeatures();
									},
									scope: this
								}
							],
							listeners: {
								cellclick: function(tobj, td, cellIndex, record, rowIndex, e) {
									var me = this;
									if (cellIndex == 1)
									{
										record.expand(true);
									}
								},
								checkchange: function(rec, status) {
									this.updateTreeNode(rec, status);
								},
								scope: this
							}
						}
					]
				}
			]
		});
		
		IG$.mgrFeatures.superclass.initComponent.call(this);
	},
	listeners: {
		afterrender: function() {
			this.loadAuth();
		}
	}
});