IG$.cubeStyle = $s.extend($s.window, {
	
	modal: true,
	layout: "border",
	closable: false,
	resizable: true,
	width: 720,
	height: 500,
	
	reportoption: null,
	
	callback: null,
	globalstyle: null,
	globalstylemap: null,
	styleinfo: null,
	
	loadGlobalStyle: function() {
		var panel = this,
			req = new IG$._rpc$();
		
		req.init(panel, 
				{
					ack: "appoption",
					payload: {address: "ReportStyle"},
					mbody: {}
				}, panel, panel.rs_loadGlobalStyle, null);
		req.send();
	},
	
	rs_loadGlobalStyle: function(jdoc) {
		var tnode = jdoc.themes[0],
			tnodes1,
			mnode, cs, key,
			bsds = [{
				name: "Select Item",
				value: ""
			}],
			theme_id = ig$.theme_id,
			unode, child, i, j, bs, nodenames = ["default_styles", "custom_styles"];
			
		this.globalstyle = [];
		this.globalstylemap = {};
		
		if (tnode)
		{
			for (i=0; i < nodenames.length; i++) {
				key = nodenames[i];
				
				mnode = tnode[key];
				
				if (mnode)
				{
					for (j=0; j < mnode.length; j++)
					{
						cs = mnode[j];
						this.globalstyle.push(cs);
						bsds.push({
							name: cs.name,
							value: cs.name
						});
						this.globalstylemap[cs.name] = cs;
					}
				}
			}
			
			for (key in this.globalstylemap)
			{
				cs = this.globalstylemap[key];
				
				if (cs.basestylename && this.globalstylemap[cs.basestylename])
				{
					bs = this.globalstylemap[cs.basestylename];
					// cs.applyBaseStyle.call(cs, bs);
				}
			}
		}
		
		this.down("[name=base_style]").store.loadData(bsds);
		this.loadLocalStyle();
	},
	
	loadLocalStyle: function() {
		var me = this,
			i, stylename,
			cs, bs,
			stylelist = me.down("[name=stylelist]"),
			ltree = [], cstree, unode;
			
		me.down("[name=c_cset]").setValue(me.c_cset || "");
			
		if (me.styleinfo) {
			unode = {name: 'LocalStyle', leaf: false, children: []};
			ltree.push(unode);
			
			for (i=0; i < me.styleinfo.length; i++) {
				cs = me.styleinfo[i];
				cstree = {name: cs.name, leaf: true, custom: false};
				
				if (cs.basestylename && me.globalstylemap[cs.basestylename])
				{
					bs = me.globalstylemap[cs.basestylename];
					// cs.applyBaseStyle.call(cs, bs);
				}
				
				unode.children.push(cstree);
			}
			
			stylelist.store.setRootNode({
				expanded: true,
				text: IRm$.r1("B_STYLE"),
				name: IRm$.r1("B_STYLE"),
				title: IRm$.r1("B_STYLE"),
				children: ltree
			});
		}
	},
	
	editStyleItem: function(stylename) {
		var i,
			me = this,
			form_header = me.down("[name=form_header]"),
			form_data = me.down("[name=form_data]");
					
		me.commitStyleItem();
		
		for (i=0; i < me.styleinfo.length; i++)
		{
			if (me.styleinfo[i].name == stylename)
			{
				me._edtItem = me.styleinfo[i];
				me._valForm = 1;
				me.down("[name=sname]").setValue(me.styleinfo[i].name);
				me.down("[name=base_style]").setValue(me.styleinfo[i].basestylename || "");
				form_header.loadStyleData.call(form_header, me.styleinfo[i].header);
				form_data.loadStyleData.call(form_data, me.styleinfo[i].data);
				me._valForm = 0;
				break;
			}
		}
	},
	
	commitStyleItem: function() {
		var me = this,
			form_header = me.down("[name=form_header]"),
			form_data = me.down("[name=form_data]"),
			nname = me.down("[name=sname]").getValue();
			nvalue = me.down("[name=base_style]").getValue();
			
		if (me._edtItem && nvalue && nname)
		{
			me._edtItem.basestylename = nvalue;
			me._edtItem.name = nname;
			form_data.updateStyleData.call(form_data);
			form_header.updateStyleData.call(form_header);
		}
	},
	
	_confirm: function() {
		var me = this,
			i,
			base,
			cs;
			
		me.commitStyleItem();
		
		for (i=0; i < me.styleinfo.length; i++)
		{
			cs = me.styleinfo[i];
			
			if (cs.basestylename && me.globalstylemap[cs.basestylename])
			{
				base = me.globalstylemap[cs.basestylename];
				// cs.removeBsStyl(base);
			}
		}
		
		me.c_cset = me.down("[name=c_cset]").getValue();
		me.callback && me.callback.execute(me);
		me.close();
	},

	toolbarHandler: function(cmd) {
		var me = this;
		
		switch (cmd)
		{
		case "cmd_add_custom":
			var dlg = new IG$.makeStyle({
				styleinfo: this.globalstyle,
				callback: new IG$.callBackObj(this, this.rs__appendCustomStyle)
			});
			IG$.checkLogin(this, dlg);
			break;
		case "cmd_delete":
			var i,
				stylelist = this.down("[name=stylelist]"),
				rec = stylelist.getSelectionModel().selected,
				srec = rec && rec.length > 0 ? rec.items[0] : null;
			
			if (srec)
			{
				for (i=0; i < me.styleinfo.length; i++)
				{
					if (srec.get("name") == me.styleinfo[i].name)
					{
						me.styleinfo.splice(i, 1);
						srec.remove();
						break;
					}
				}
			}
			break;
		}
	},
	
	rs__appendCustomStyle: function(params) {
		var stylename = params[0], 
			basestylename = params[1];
			
		if (basestylename && stylename)
		{
			var i,
				basestyle;
			
			basestyle = this.globalstylemap[basestylename];
			
			if (basestyle)
			{
				var stylelist = this.down("[name=stylelist]"),
					// proxy = stylelist.store.getProxy(),
					// reader = proxy.getReader(), 
					cs,
					robj, urecord, rootnode = stylelist.store.getRootNode();
					
				urecord = rootnode.childNodes[rootnode.childNodes.length - 1];
				
				robj = {name: stylename, leaf: true, custom: true};
				cs = {}; // new IG$.clStyleObject(null, "item", true);
				cs.name = stylename;
				cs.nodename = "ItemStyle";
				cs.basestylename = basestylename;
				cs.header = {}; //new IG$.clStyleItem(null, "Header");
				cs.data = {}; // new IG$.clStyleItem(null, "Data");
				this.styleinfo.push(cs);
				
				// cs.applyBaseStyle.call(cs, basestyle);
				
				var nrecord = [stylelist.store._record(robj)]; // reader.extractData.call(reader, [robj], true);
				// stylelist.store.fillNode.call(stylelist.store, urecord, nrecord);
				stylelist.store._appendNode(nrecord, urecord);
			}
		}
	},
	
	initComponent : function() {
		var panel = this,
			formHeader = new IG$.styleEditor({title: IRm$.r1('L_STYLE_TITLE'), name: "form_header", htype: "hd"}),
			formData = new IG$.styleEditor({title: IRm$.r1('L_STYLE_DATA'), name: "form_data", htype: "dt"}),
			dp = [{name: IRm$.r1("B_DEFAULT"), value: ""}],
			copt = IG$.__chartoption.chartcolors,
			k;
			
		for (k in copt)
		{
			dp.push({name: k, value: k});
		};
		
		panel.title = IRm$.r1('T_STYLE_WIZARD');
		
		$s.apply(this, {
			defaults:{bodyStyle:'padding:3px'},
			
			items: [
				{
					xtype: "treepanel",
					title: IRm$.r1("B_STYLE"), // "Style",
					name: "stylelist",
					preventHeader: true,
					region: "west",
					width: 160,
					split: true,
					border: false,
					rootVisible: false,
					hideHeaders: true,
					autoScroll: true,
					store: {
						xtype: "treestore",
						fields: [
							"name", "text", "type", "pname", "region", "custom"
						],
						root: {
							expanded: true,
							name: "Style",
							children: []
						},
						folderSort: false
					},
					
					useArrows: true,
					
					dockedItems: [
						{
							xtype: 'toolbar',
							dock: 'top',
							plain: true,
							items: [
								{ 
									iconCls: 'icon-toolbar-add',
									tooltip: "Add Custom Style",
									text: IRm$.r1("L_ADD_NEW"),
									handler: function() {
										this.toolbarHandler('cmd_add_custom'); 
									},
									scope: this
								},
								{
									iconCls: "icon-grid-delete",
									tooltip: IRm$.r1("B_DELETE"),
									handler: function() {
										this.toolbarHandler('cmd_delete'); 
									},
									scope: this
								}
							]
						}
					],
					
					columns: [
						{
							xtype: "treecolumn",
							text: IRm$.r1("B_TEXT"),
							flex: 2,
							sortable: false,
							dataIndex: "name"
						},
						{
							xtype: 'actioncolumn',
							width: 26,
							items: [
								{
									// icon: './images/delete.png',
									iconCls: "icon-grid-delete",
									tooltip: 'Delete item',
									handler: function (grid, rowIndex, colIndex) {
										var rec = grid.store.getAt(rowIndex);
									},
									getClass: function(v, metadata, r, rowIndex, colIndex, store) {
										if (rowIndex == 0 || r.isLeaf() == false || (r.data && r.data.custom == false))
										{
											return "idv-hd-val";
										}
										return "";
									}
								}
							]
						}
					],
					listeners: {
						afterrender: function() {
							var ctrl = this;
						},
						itemclick: function(view, record, item, index, e) {
							var stylename = record.get("name");
							
							if (record.isRoot() == false && stylename)
							{
								panel.editStyleItem.call(panel, stylename);
							}
						}
					}
					
				},
				{
					xtype: "form",
					region: "center",
					layout: {
						type: 'vbox',
						align: 'stretch'
					},
					items: [
						{
							xtype: "textfield",
							name: "sname",
							fieldLabel: IRm$.r1("B_NAME")
						},
						{
							xtype: "combobox",
							fieldLabel: IRm$.r1("L_INHERIT"), // Inherit",
							name: "base_style",
							displayField: "name",
							valueField: "value",
							editable: false,
							queryMode: "local",
							store: {
								fields: ["name", "value"]
							},
							listeners: {
								change: function(tobj, newvalue, oldvalue) {
									var me = this,
										basestylename = me.down("[name=base_style]").getValue(),
										bs,
										form_header = me.down("[name=form_header]"),
										form_data = me.down("[name=form_data]");
									
									if (me._valForm)
										return;
										
									if (basestylename)
									{
										bs = me.globalstylemap[basestylename];
										
										if (bs)
										{
											form_header.loadStyleData.call(form_header, bs.header);
											form_data.loadStyleData.call(form_data, bs.data);
										}
									}
								},
								scope: this
							}
						},
						{
							xtype: "combobox",
							fieldLabel: IRm$.r1("L_CHART_STYLE"), // Chart Style",
							name: "c_cset",
							displayField: "name",
							valueField: "value",
							editable: false,
							queryMode: "local",
							store: {
								fields: [
									"name", "value"
								],
								data: dp
							}
						},
						{
							xtype: 'tabpanel',
							plain: true,
							flex: 1,
							items: [
								formData,
								formHeader
							]
						}
					]
				}
			],
			
			buttons:[
				{
					text: IRm$.r1('B_CONFIRM'),
					handler: function() {
						this._confirm();
					},
					scope: this
				}, {
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function() {
					this.loadGlobalStyle();
				}
			}
		});
		
		IG$.cubeStyle.superclass.initComponent.apply(this, arguments);
	}
});