IG$.mgrdb_editor_soa = $s.extend($s.window, {
	width: 600,
	height: 480,
	layout: "fit",
	
	_init: function() {
		var me = this;
		
		if (me._ditem)
		{
			me.setAllValues(me._ditem);
			
			if (me._ditem.ws_operation)
			{
				me._sel_op = {
					name: me._ditem.ws_operation,
					bind: me._ditem.ws_bindname,
					port: me._ditem.ws_portname
				};
				
				me.down("[name=ws_op]").setValue(me._sel_op.name);
			}
		}
	},
	
	_confirm: function() {
		var me = this,
			t_input = me.down("[name=t_input]");
		
		if (!me._ditem)
		{
			me._ditem = {};
		}
		
		if (me.getAllValues(me._ditem) == false)
		{
			return;
		}
		
		me._ditem.ws_operation = me._sel_op.name;
		me._ditem.ws_bindname = me._sel_op.bind;
		me._ditem.ws_portname = me._sel_op.port;
		
		me._ditem.parameters = {};
		
		$.each(t_input.store.data.items, function(i, rec) {
			if (rec.data.name)
			{
				me._ditem.parameters[rec.data.name] = {
					name: rec.data.name,
					path: rec.data.path,
					namespace: rec.data.namespace,
					optional: rec.data.optional,
					maxoccur: rec.data.maxoccur,
					minoccur: rec.data.minoccur,
					value: rec.data.value,
					isfixed: rec.data.isfixed,
					hasdefault: rec.data.hasdefault
				};
			}
		});
		
		me.callback && me.callback.execute(me);
		me.close();
	},
	
	_req_wsdl_op: function() {
		var me = this,
			ws_wsdl = me.down("[name=ws_wsdl]");
		
		if (!ws_wsdl.getValue())
		{
			ws_wsdl.markInvalid();
			return;
		}
		
		ws_wsdl.clearInvalid();
		
		var req = new IG$._rpc$(),
			mbody = {
				option: "webservice",
				handle: "wsdl_operation",
				wsdl: me.down("[name=ws_wsdl]").getValue()
			};
		req.init(me,
			{
				ack: "helpcontent",
				payload: {},
				mbody: mbody
			}, me, function(jdoc) {
				var data = jdoc && jdoc.length ? jdoc : [];
				me.down("[name=ws_operations]").store.loadData(data);
			}, false);

		req.send();
	},
	
	_get_ws_tmpl: function() {
		var me = this;
		var req = new IG$._rpc$(),
			mbody = {
				option: "webservice",
				handle: "wsdl_template",
				wsdl: me.down("[name=ws_wsdl]").getValue(),
				opname: me._sel_op.name,
				bindname: me._sel_op.bind,
				portname: me._sel_op.port
			};
		req.init(me,
			{
				ack: "helpcontent",
				payload: {},
				mbody: mbody
			}, me, function(jdoc) {
				if (jdoc)
				{
					me.setAllValues(jdoc);
					var d = [];
					if (jdoc.parameters)
					{
						$.each(jdoc.parameters, function(i, param) {
							d.push(param);
						});
					}
					me.down("[name=t_input]").store.loadData(d);
				}
			}, false);
	
		req.send();
	},
	
	_addRow: function(gname) {
		var me = this,
			tgt = me.down("[name=" + gname + "]");
			
		tgt.store.add({});
	},
	
	initComponent: function(){
		var me = this;
		
		$s.apply(me, {
			title: IRm$.r1("T_DB_CON"),
			padding: 10,
			items: [
				{
					xtype: "panel",
					padding: 10,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					autoScroll: true,
					items: [
						{
							xtype: "fieldset",
							title: "WSDL",
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "textfield",
									fieldLabel: IRm$.r1("B_NAME"),
									name: "name",
									required: true
								},
								{
									xtype: "fieldcontainer",
									fieldLabel: "WSDL",
									hidden: me.dbtype == "rest",
									layout: {
										type: "hbox",
										align: "stretch"
									},
									items: [
										
										{
											xtype: "textfield",
											name: "ws_wsdl",
											flex: 1,
											required: me.dbtype == "soap"
										},
										{
											xtype: "button",
											text: "Load",
											handler: function() {
												this._req_wsdl_op();
											},
											scope: this
										}
									]
								},
								{
									xtype: "textfield",
									fieldLabel: "Endpoint",
									name: "ws_endpoint",
									required: true
								},
								{
									xtype: "textfield",
									fieldLabel: "Operation",
									name: "ws_op",
									hidden: me.dbtype == "rest",
									required: me.dbtype == "soap",
									readOnly: true
								},
								{
									xtype: "container",
									layout: {
										type: "hbox",
										align: "stretch"
									},
									hidden: me.dbtype == "soap",
									required: me.dbtype == "rest",
									items: [
										{
											xtype: "combobox",
											fieldLabel: "Method",
											name: "ws_method",
											displayField: "name",
											valueField: "value",
											queryMode: 'local',
											editable: false,
											flex: 1,
											autoSelect: true,
											store: {
												xtype: "store",
												fields: [
													"name", "bind", "port"
												],
												data: [
													{name: "GET", value: "GET"},
													{name: "POST", value: "POST"}
												]
											}
										},
										{
											xtype: "button",
											text: "Test",
											handler: function() {
											},
											scope: this
										}
									]
								}
							]
						},
						{
							xtype: "fieldset",
							title: "Message",
							height: 400,
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "fieldcontainer",
									fieldLabel: "Operations",
									layout: {
										type: "hbox",
										align: "stretch"
									},
									hidden: me.dbtype == "rest",
									items: [
										{
											xtype: "combobox",
											name: "ws_operations",
											displayField: "name",
											valueField: "name",
											queryMode: 'local',
											editable: false,
											autoSelect: true,
											store: {
												xtype: "store",
												fields: [
													"name", "bind", "port"
												]
											},
											listeners: {
												change: function(tobj) {
													var me = this,
														selop,
														sval = tobj.getValue();
													
													$.each(tobj.store.data.items, function(i, item) {
														if (item.data["name"] == sval)
														{
															selop = item;
															return false;
														}
													});
													
													me.down("[name=ws_op]").setValue(selop ? selop.data.name : "");
													me._sel_op = selop.data;
												},
												scope: this
											}
										},
										{
											xtype: "button",
											text: "Template",
											handler: function() {
												this._get_ws_tmpl();
											},
											scope: this
										}
									]
								},
								{
									xtype: "gridpanel",
									name: "t_input",
									flex: 1,
									store: {},
									columns: [
										{
											text: IRm$.r1("B_PATH"),
											dataIndex: "path"
										}
									],
									tbar: [
										"->",
										{
											xtype: "button",
											text: IRm$.r1("B_ADD"),
											handler: function() {
												this._addRow("t_input");
											},
											scope: this
										}
									]
								},
								{
									xtype: "textarea",
									flex: 1,
									readOnly: me.dbtype == "rest",
									name: "txt_advanced"
								}
							]
						},
						{
							xtype: "fieldset",
							title: "Output Mapping",
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "textfield",
									fieldLabel: "Table Data Path",
									name: "ws_table_path"
								},
								{
									xtype: "textfield",
									fieldLabel: IRm$.r1("L_TABLE_NAME"),
									name: "ws_table_name"
								},
								{
									xtype: "gridpanel",
									name: "ws_data_output",
									title: "Table output",
									height: 250,
									store: {},
									columns: [
										{
											text: IRm$.r1("B_PATH"),
											dataIndex: "path"
										},
										{
											text: "DataType",
											dataIndex: "datatype"
										}
									],
									tbar: [
										"->",
										{
											xtype: "button",
											text: IRm$.r1("B_ADD"),
											handler: function() {
												this._addRow("ws_data_output");
											},
											scope: this
										}
									]
								},
								{
									xtype: "gridpanel",
									name: "ws_output",
									height: 250,
									title: "Parameter output",
									store: {},
									columns: [
										{
											text: IRm$.r1("B_PATH"),
											dataIndex: "path"
										},
										{
											text: "Parameter name",
											dataIndex: "parameter_name"
										}
									],
									tbar: [
										"->",
										{
											xtype: "button",
											text: IRm$.r1("B_ADD"),
											handler: function() {
												this._addRow("ws_output");
											},
											scope: this
										}
									]
								}
							]
						}
					]
				}
			],
			buttons: [
				{
					xtype: "button",
					text: IRm$.r1("B_CONFIRM"),
					handler: function() {
						this._confirm();
					},
					scope: this
				},
				{
					xtype: "button",
					text: IRm$.r1("B_CANCEL"),
					handler: function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(tobj) {
					tobj._init();
				}
			}
		});
		
		IG$.mgrdb_editor_soa.superclass.initComponent.call(this);
	}
});