IG$._I7a_/*dashboardmenuconfig*/ = $s.extend($s.window, {
	modal: true,
	title: "Page Config",
	layout: "fit",
	width: 400,
	autoHeight: true,
	
	initApp: function() {
		var me = this,
			options;
		if (me.rec)
		{
			options = me.rec.get("options") || {};
			me.down("[name=ltype]").setValue(options.ltype || "");
			me.down("[name=_h]").setValue(options.html);
			me.down("[name=_t]").setValue(options.tmpl);
			me.down("[name=_ra]").setValue(options.rptl);
			me.rptl_uid = options.rptl_uid;
			me.down("[name=_s]").setValue(options.scrp);
		}
	},
	
	_2/*changeLayout*/: function() {
		var me = this,
			ltype = me.down("[name=ltype]").getValue();
		
		$.each(["_h", "_r", "_t"], function(i, t) {
			me.down("[name=" + t + "]").setVisible(ltype && "_" + ltype.charAt(0) == t ? true : false);
		});
	},
	
	_3/*confirmChanges*/: function() {
		var me = this,
			option,
			rec = me.rec;
		
		if (rec)
		{
			option = rec.get("options") || {};
			option.ltype = me.down("[name=ltype]").getValue();
			option.html = me.down("[name=_h]").getValue();
			option.tmpl = me.down("[name=_t]").getValue();
			option.rptl = me.down("[name=_ra]").getValue();
			option.rptl_uid = me.rptl_uid;
			option.scrp = me.down("[name=_s]").getValue();
			
			rec.set("options", option);
		}
		
		this.close();
	},
	
	initComponent: function() {
		var me = this;
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: "anchor",
					bodyPadding: 10,
					defaults: {
						anchor: "100%"
					},
					items: [
						{
							xtype: "combobox",
							fieldLabel: "Load Type",
							name: "ltype",
							queryMode: "local",
							editable: false,
							valueField: "value",
							displayField: "name",
							store: {
								xtype: "store",
								fields: ["name", "value"],
								data: [
									{name: "No Action", value: ""},
									{name: "Template", value: "template"},
									{name: "Load HTML", value: "html"},
									{name: "Open Report", value: "report"}
								]
							},
							listeners: {
								change: function() {
									this._2/*changeLayout*/();
								},
								scope: this
							}
						},
						{
							xtype: "textfield",
							name: "_h",
							fieldLabel: "Default HTMLPage"
						},
						{
							xtype: "fieldcontainer",
							name: "_r",
							layout: "hbox",
							fieldLabel: "Report",
							items: [
								{
									xtype: "textfield",
									name: "_ra",
									readOnly: true,
									emptyText: IRm$.r1("M_SEL_ITEM"),
									flex: 1
								},
								{
									xtype: "button",
									// text: "..",
									iconCls: "icon-toolbar-search",
									tooltip: IRm$.r1("B_SEARCH"),
									handler: function() {
										var me = this,
											dlgitemsel = new IG$.metaSelectDlg({
											visibleItems: "workspace;folder;report;dashboard",
											tree_options: {
												cubebrowse: false
											},
											callback: new IG$.callBackObj(me, function(item) {
												var _ra = me.down("[name=_ra]");
												_ra.setValue(item.name);
												me.rptl_uid = item.uid;
											})
										});
										IG$.checkLogin(this, dlgitemsel);
									},
									scope: this
								}
							]
						},
						{
							xtype: "combobox",
							name: "_t",
							queryMode: "local",
							fieldLabel: "Template Name",
							editable: false,
							valueField: "value",
							displayField: "name",
							store: {
								xtype: "store",
								fields: ["name", "value"]
							}
						},
						{
							xtype: "textfield",
							fieldLabel: "After Javascript",
							name: "_s"
						}
					]
				}
			],
			buttons: [
				{
					text: IRm$.r1("B_CONFIRM"),
					handler: function() {
						var me = this;
						me._3/*confirmChanges*/();
					},
					scope: this
				},
				{
					text: IRm$.r1('B_CLOSE'),
					handler: function() {
						this.close();
					},
					scope: this
				}
			]
		});
		
		IG$._I7a_/*dashboardmenuconfig*/.superclass.initComponent.call(this);
	},
	listeners: {
		afterrender: function(tobj) {
			this.initApp();
		}
	}
});

IG$.dashboardmenu = $s.extend(IG$.v$pnl, {
	scroll: false,
	initialized: false,
	hideMode: 'offsets',
	layout: "fit",
	bodyPadding: 5,
	border: 0,
	
	load_content: function() {
		var me = this,
			req = new IG$._rpc$(),
			uid = "/SYS_Config/dashboardmenu";
		
		req.showerror = false;
		req.init(me, 
			{
				ack: "content",
				payload: {uid: uid},
				mbody: {}
			}, me, me._l1/*rs_loadContent*/, function(jdoc) {
				var r = true,
					req,
					errcode = jdoc.errorcode;
				
				if (errcode == "0x1400")
				{
					req = new IG$._rpc$();
					req.init(me, 
						{
							ack: "writecontent",
							payload: {address: uid, name: "dashboardmenu", type: "PrivateContent"},
							mbody: {},
							meta_type: "dashboard_menu"
						}, me, me.load_content);
					req.send();
					r = false;
				}
				
				return r
			});
		req.send();
	},
	
	_l1/*rs_loadContent*/: function(jdoc) {
		var me = this,
			menust = me.down("[name=menust]"),
			root;
		
		me.appinfo = jdoc;
		
		root = {
			name: "Menu",
			level: -1,
			option: {},
			description: "Top Menu",
			expanded: true,
			children: me.appinfo.menustructure 
		};
		
		menust.store.setRootNode(root, 1);
	},
	
	_l2/*menusthandler*/: function(act) {
		var me = this,
			menust = me.down("[name=menust]"),
			sel = menust.getSelectionModel().selected,
			appinfo = me.appinfo,
			m = appinfo._a/*menustructure*/,
			sobj,
			suid,
			srec;
			
		switch (act)
		{
		case 0:
			if (sel && sel.length > 0)
			{
				sobj = {
					name: "New Menu"
				};
				
				sel.items[0].appendChild(sobj, false, true);
				sel.items[0].expand();
			}
			break;
		case 1:
			if (sel && sel.length > 0)
			{
				srec = sel.items[0];
				if (!srec.isRoot())
				{
					suid = srec.get("sid");
					delete m.map[suid];
					srec.remove(); // parentNode.remove(srec);
				}
			}
			break;
		case 2:
			me._m3/*moveTreeGridSelection*/(menust, -1, false);
			break;
		case 3:
			me._m3/*moveTreeGridSelection*/(menust, 0, false);
			break;
		case 4:
			me._m3/*moveTreeGridSelection*/(menust, 1, true);
			break;
		}
	},
	
	_l3/*updateContent*/: function() {
		var me = this,
			menust = me.down("[name=menust]"),
			menuroot = menust.store.getRootNode(),
			mobj = {
				children: []
			},
			appinfo = me.appinfo,
			cnt,
			omenu = appinfo.menustructure;
		
		me._l8/*updateCurrent*/();
		me._l5/*updatenode*/(omenu, menuroot, mobj);
		appinfo.menustructure = mobj.children;
	},
	
	_l5/*updatenode*/: function(omenu, mnode, mobj) {
		var i,
			i, pnode,
			mtree = [],
			cobj;
		
		for (i=0; i < mnode.childNodes.length; i++)
		{
			pnode = mnode.childNodes[i];
			cobj = {
				name: pnode.get("name"),
				sid: pnode.get("sid"),
				description: pnode.get("description"),
				items: pnode.get("items"),
				children: null,
				level: mobj.level+1, 
				options: pnode.get("options")
			};
			
			mobj.children = mobj.children || [];
			
			mobj.children.push(cobj);
			
			if (pnode.hasChildNodes())
			{
				this._l5/*updatenode*/(omenu, pnode, cobj);
			}
		}
	},
	
	l4/*maintoolbar*/: function(cmd) {
		var me = this;
		
		switch(cmd)
		{
		case 0: /*savecontent*/
			me._l3/*updateContent*/();
			me._l6/*saveContent*/();
			break;
		}
	},
	
	_l6/*saveContent*/: function() {
		var me = this,
			req = new IG$._rpc$(),
			appinfo = me.appinfo;
		
		req.init(me, 
			{
				ack: "writecontent",
				payload: {uid: appinfo.uid},
				mbody: appinfo,
				meta_type: "dashboard_menu"
			}, me, function(xdoc) {
				IG$.alertmsg(ig$.appname, IRm$.r1("M_SAVED"), null, null, 0, "success");
			});
		req.send();
	},
	
	moveGridSelection: function(grid, direction) {
		var records = grid.getSelectionModel().getSelection(),
			record = records && records.length ? records[0] : null;
			
		if (!record) 
		{
			return;
		}
		
		var index = grid.getStore().indexOf(record);
		
		if (direction < 0) 
		{
			index--;
			if (index < 0) 
			{
				return;
			}
		} 
		else 
		{
			index++;
			if (index >= grid.getStore().getCount()) 
			{
				return;
			}
		}
		grid.getStore().remove(record);
		record = grid.getStore().insert(index, record);
		grid.getSelectionModel().select(record, true);
	},
	
	_m3/*moveTreeGridSelection*/: function(grid, direction, move_left) {
		var records = grid.getSelectionModel().getSelection(),
			record = records && records.length ? records[0] : null;
			
		if (!record || (record && !record.parentNode)) 
		{
			return;
		}
		
		var pnode = record.parentNode,
			index = pnode.indexOf(record),
			ppnode;
		
		if (move_left)
		{
			ppnode = pnode.parentNode;
			
			if (!ppnode)
			{
				return;
			}
			
			pnode.removeChild(record);
			record = ppnode.insertChild(ppnode.childNodes.length, record);
			grid.getSelectionModel().select(record, true);
		}
		else
		{
			if (direction < 0) 
			{
				index--;
				if (index < 0) 
				{
					return;
				}
			} 
			else 
			{
				index++;
				if (index >= pnode.childNodes.length) 
				{
					return;
				}
			}
			
			pnode.removeChild(record);
			record = pnode.insertChild(index, record);
			grid.getSelectionModel().select(record, true);
		}
	},
	
	_l7/*toolbar_content*/: function(act) {
		var me = this,
			mgrid = me.down("[name=mgrid]"),
			sel, rec,
			appinfo = me.appinfo,
			m = appinfo._a/*menustructure*/,
			i, j, sobj;
		
		switch(act)
		{
		case 0: // add item
			break;
		case 1: // remove item
			sel = mgrid.getSelectionModel().selected;
			for (i=0; i < sel.length; i++)
			{
				rec = sel.items[i];
				mgrid.store.remove(rec);
				
				sobj = m.map[rec.get("sid")];
				
				if (sobj)
				{
					for (j=0; j < sobj.items.length; j++)
					{
						if (sobj.items[j].uid == rec.get("uid"))
						{
							sobj.items.splice(j, 1);
							break;
						}
					}
				}
			}
			break;
		case 2:	// move up
			me.moveGridSelection(mgrid, -1);
			break;
		case 3: // move down
			me.moveGridSelection(mgrid, 1);
			break;
		}
	},
	
	updateGridContent: function(item) {
		var me = this,
			mg = me.down("[name=mg]"),
			req = new IG$._rpc$();
			
		if (!item.get("uid"))
		{
			mg.store.loadData([]);
			return;
		}
		
		req.init(me, 
			{
				ack: "content",
				payload: {uid: item.get("uid")},
				mbody: {subfolder: "T"}
			}, me, function(jdoc) {
				var i,
					ndata = [],
					t;
				
				if (jdoc.children)
				{
					for (i=0; i < jdoc.children.length; i++)
					{
						t = jdoc.children[i].type.toLowerCase();
						if ((/report|folder/).test(t))
						{
							ndata.push(jdoc.children[i]);
						}
					}
				}								
				mg.store.loadData(ndata);
			});
		req.send();
	},
	
	_l8/*updateCurrent*/: function() {
		var me = this,
			menust = me.down("[name=menust]"),
			sel = menust.getSelectionModel().selected,
			mgrid = me.down("[name=mgrid]"),
			appinfo = me.appinfo,
			m = appinfo._a/*menustructure*/,
			p, i, rec, mrec,
			sid, items = [];
			
		if (sel && sel.length > 0)
		{
			mrec = sel.items[0];
			sid = mrec.get("sid");
			
			for (i=0; i < mgrid.store.data.items.length; i++)
			{
				rec = mgrid.store.data.items[i];
				items.push({
					uid: rec.get("uid"),
					name: rec.get("name"),
					type: rec.get("type"),
					sid: sid,
					nodepath: rec.get("nodepath")
				});
			}
			
			mrec.set("items", items);
		}
	},
	
	_1/*configPage*/: function(rec) {
		var me = this,
			cinfo = me.appinfo,
			sid = rec.get("sid");
			
		var dlg = new IG$._I7a_/*dashboardmenuconfig*/({
			rec: rec
		});
		IG$.checkLogin(this, dlg);
	},
	
	initComponent: function(){
		var me = this;
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					border: 0,
					defaults: {
						anchor: "100%"
					},
					items: [
						{
							xtype: "panel",
							border: 0,
							flex: 1,
							layout: {
								type: "border"
							},
							tbar: [
								{
									text: IRm$.r1("B_SAVE"),
									handler: function() {
										this.l4/*maintoolbar*/(0);
									},
									scope: this
								},
								{
									text: IRm$.r1("L_REFRESH"),
									handler: function() {
										this.load_content();
									},
									scope: this
								}
							],
							items: [
								{
									xtype: "panel",
									region: "center",
									flex: 2,
									border: 0,
									layout: "border",
									items: [
										{
											xtype: "treepanel",
											region: "center",
											flex: 1,
											title: "Menu Structures",
											name: "menust",
											
											rootVisible: true,
											plugins: [
												{
													ptype: "cellediting"
												}
											],
											store: {
												xtype: "treestore",
												proxy: {
													type: "memory"
												},
												fields: [
													"name", "description", "sid"
												]
											},
											columns: [
												{
													xtype: "treecolumn",
													text: IRm$.r1("B_TITLE"),
													dataIndex: "name",
													editor: "textfield",
													minWidth: 200
												},
												{
													text: IRm$.r1("B_DESC"),
													dataIndex: "description",
													editor: "textfield",
													flex: 1,
													minWidth: 200
												},
												{
													xtype: "actioncolumn",
													width: 50,
													items: [
														{
															iconCls: "icon-grid-config",
															tooltip: IRm$.r1("B_CONFIG_ITEM"),
															handler: function (grid, rowIndex, colIndex, item, e, record) {
																this._1/*configPage*/(record);
															},
															scope: this
														}
													]
												}
											],
											listeners: {
												beforeselect: function(tobj, record, index, eopts) {
													this._l8/*updateCurrent*/();
												},
												cellclick: function(tobj, td, cellIndex, record, rowIndex, e) {
													var me = this,
														sid = record.get("sid"),
														items = record.get("items") || [],
														mgrid = me.down("[name=mgrid]"),
														appinfo = me.appinfo;
													
													mgrid.store.loadData(items);
												},
												scope: this
											},
											tbar: [	
												{
													text: IRm$.r1("L_ADD_NEW"),
													handler: function() {
														me._l2/*menusthandler*/.call(me, 0);
													}
												},
												{
													text: IRm$.r1("L_REMOVE_SELECTED"),
													handler: function() {
														me._l2/*menusthandler*/.call(me, 1);
													}
												},
												{
													text: IRm$.r1("B_MOVE_UP"),
													handler: function() {
														me._l2/*menusthandler*/.call(me, 2);
													}
												},
												{
													text: IRm$.r1("B_MOVE_DOWN"),
													handler: function() {
														me._l2/*menusthandler*/.call(me, 3);
													}
												},
												{
													text: IRm$.r1("B_MOVE_LEFT"), // "Move Left",
													hidden: true,
													handler: function() {
														me._l2/*menusthandler*/.call(me, 4);
													}
												}
											]
										},
										
										{
											xtype: "gridpanel",
											region: "south",
											name: "mgrid",
											selModel: {
												selection: "checkboxmodel",
												checkSelector: ".x-grid-cell"
											},

											viewConfig: {
												plugins: {
													ptype: "gridviewdragdrop",
													ddGroup: "_I$RD_G_",
													enableDrop: true,
													enableDrag: false
												}
											},
											split: true,
											flex: 1,
											title: IRm$.r1("L_C_ITEMS"),
											tbar: [
												{
													text: IRm$.r1("L_ADD_NEW"),
													hidden: true,
													handler: function() {
														this._l7/*toolbar_content*/(0);
													},
													scope: this
												},
												{
													text: IRm$.r1("L_REMOVE_SELECTED"),
													handler: function() {
														this._l7/*toolbar_content*/(1);
													},
													scope: this
												},
												{
													text: IRm$.r1("B_MOVE_UP"),
													handler: function() {
														this._l7/*toolbar_content*/(2);
													},
													scope: this
												},
												{
													text: IRm$.r1("B_MOVE_DOWN"),
													handler: function() {
														this._l7/*toolbar_content*/(3);
													},
													scope: this
												}
											],
											store: {
												xtype: "store",
												fields: [
													"uid", "name", "nodepath", "type", "sid"
												]
											},
											columns: [
												{
													text: IRm$.r1("B_NAME"),
													dataIndex: "name"
												},
												{
													text: IRm$.r1("B_TYPE"),
													dataIndex: "type"
												},
												{
													text: IRm$.r1("B_DESC"),
													dataIndex: "description"
												},
												{
													text: IRm$.r1("B_LOC"),
													dataIndex: "nodepath",
													flex: 1
												}
											]
										}
									]
								},
								{
									xtype: "panel",
									region: "east",
									split: true,
									collapsible: true,
									collapsed: false, // true,
									flex: 1,
									title: IRm$.r1("L_NAVIGATOR"),
									layout: {
										type: "vbox",
										align: "stretch"
									},
									items: [
										new IG$.n$navTreeCom({
											name: "mtree",
											flex: 1,
											gridupdate: new IG$.callBackObj(me, me.updateGridContent)
										}),
										{
											xtype: "gridpanel",
											name: "mg",
											enabledrag: true,
											viewConfig: {
												plugins: [
													{
														ptype: "gridviewdragdrop",
														ddGroup: "_I$RD_G_",
														enableDrop: false,
														enableDrag: true
													}
												]
											},
											flex: 1,
											store: {
												xtype: "store",
												fields: [
													"uid", "name", "type", "nodepath"
												]
											},
											columns: [
												{
													text: IRm$.r1("B_NAME"),
													dataIndex: "name"
												},
												{
													text: IRm$.r1("B_TYPE"),
													dataIndex: "type"
												},
												{
													text: IRm$.r1("B_LOC"),
													dataIndex: "nodepath"
												}
											]
										}
									]
								}
							]
						}
					]
				}
			]
		});
		
		IG$.dashboardmenu.superclass.initComponent.call(this);
	},
	listeners: {
		afterrender: function(tobj) {
			this.load_content();
		}
	}
});
