IG$.nlpEdItT = $s.extend($s.window, {
	
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 600,
	height: 500,
	
	title: "Sentence",
	
	i_fields: [
		{
			n: "te_trim_type",
			m: "type"
		},
		{
			n: "te_left_words",
			m: "left_words",
			a: 1,
			required: false
		},
		{
			n: "te_right_words",
			m: "right_words",
			a: 1,
			required: false
		},
		{
			n: "te_words",
			m: "words",
			a: 1,
			required: false
		}
	],
	
	init_f: function() {
		var me = this,
			_mc;
		
		me._mc = _mc = me._mc || {};
		
		$.each(me.i_fields, function(i, t) {
			var c = me.down("[name=" + t.n + "]"),
				v = _mc[t.m];
			
			if (t.a)
			{
				c.setValue(v ? v.join("\n") : "");
			}
			else
			{
				c.setValue(v);
			}
		});
		
		if (_mc.options)
		{
			if (_mc.options.skip && _mc.options.skip.length)
			{
				me.down("[name=te_skip]").setValue(_mc.options.skip.join("\n"));
			}
		}
	},
	
	_confirm: function() {
		var me = this,
			err = 0,
			_mc = me._mc,
			te_skip = me.down("[name=te_skip]");
			
		$.each(me.i_fields, function(i, t) {
			var c = me.down("[name=" + t.n + "]"),
				v = c.getValue(),
				required = true;
			
			if (t.m != "type")
			{
				required = false;
				
				if (_mc.type == "between")
				{
					if (t.m == "words")
					{
						required = false;
					}
				}
				else
				{
					if (t.m == "left_words" || t.m == "right_words")
					{
						required = false;
					}
				}
			}
			
			if (!v && required)
			{
				c.markInvalid();
				err = 1;
			}
			else
			{
				c.clearInvalid();
				
				if (t.a)
				{
					_mc[t.m] = v ? v.split("\n") : null;
				}
				else
				{
					_mc[t.m] = v;
				}
			}
		});
			
		if (err)
			return;
		
		_mc.options = {};
		
		if (te_skip.getValue())
		{
			_mc.options.skip = te_skip.getValue().split("\n");
		}
		
		me.callback && me.callback.execute(me._mc);
		me.close();
	},
	
	initComponent : function() {
		var me = this;
		
		$s.apply(me, {
			padding: 5,
			border: 0,
			items: [
				{
					xtype: "panel",
					layout: "fit",
					padding: 10,
					autoScroll: true,
					items: [
						{
							xtype: "panel",
							layout: {
								type: "vbox",
								align: "stretch"
							},
							padding: 5,
							items: [
								{
									xtype: "container",
									layout: {
										type: "hbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "combobox",
											name: "te_trim_type",
											fieldLabel: IRm$.r1("L_TRIM_TYPE"), // "Trim Type",
											displayField: "name",
											valueField: "value",
											queryMode: 'local',
											editable: false,
											autoSelect: true,
											required: true,
											store: {
												field: ["name", "value"],
												data: [
													{name: "Between", value: "between"},
													{name: "After", value: "after"},
													{name: "AfterFirst", value: "afterfirst"},
													{name: "AfterLast", value: "afterlast"},
													{name: "Before", value: "before"},
													{name: "BeforeFirst", value: "beforefirst"},
													{name: "BeforeLast", value: "beforelast"}
												]
											},
											listeners: {
												change: function(cb, newValue, oldValue, eOpts) {
													var me = this;
													
													if (newValue == "between")
													{
														me.down("[name=te_words]").hide();
														me.down("[name=te_left_words]").show();
														me.down("[name=te_right_words]").show();
													}
													else
													{
														me.down("[name=te_words]").show();
														me.down("[name=te_left_words]").hide();
														me.down("[name=te_right_words]").hide();
													}
												},
												scope: this
											}
										},
										{
											xtype: "container",
											flex: 1
										}
									]
								},
								{
									xtype: "fieldset",
									title: "Options",
									layout: {
										type: "vbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "textarea",
											fieldLabel: IRm$.r1("L_SKIP_WORDS"), // "Skip words",
											name: "te_skip",
											labelPosition: "top",
											height: 120
										}
									]
								},
								{
									xtype: "textarea",
									name: "te_left_words",
									fieldLabel: IRm$.r1("L_LEFT_WORDS"), // "Left Words",
									labelPosition: "top",
									hidden: true,
									height: 200
								},
								{
									xtype: "textarea",
									name: "te_right_words",
									fieldLabel: IRm$.r1("L_RIGHT_WORDS"), // "Right Words",
									labelPosition: "top",
									hidden: true,
									height: 200
								},
								{
									xtype: "textarea",
									name: "te_words",
									fieldLabel: IRm$.r1("L_WORDS"), // "Words",
									labelPosition: "top",
									hidden: true,
									height: 200
								}
							]
						}
					]
				}
									
			],
			buttons:[
				"->",
				{
					text: IRm$.r1('B_CONFIRM'),
					handler: function() {
						this._confirm();
					},
					scope: this
				}, 
				{
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],

			listeners: {
				afterrender: function() {
					this.init_f();
				}
			}
		});
		
		IG$.nlpEdItT.superclass.initComponent.apply(this, arguments);
	}
});


