// mgr_trashbin

IG$.mgr_trashbin = $s.extend(IG$.v$pnl, {
	scroll: false,
	closable: true,
	layout: "border",
	
	iconCls: "icon-group",
	
	loadUID: function(uid) {
		var me = this,
			req = new IG$._rpc$();
		
		req.init(me, 
			{
				ack: "helpcontent",
				payload: {uid: uid},
				mbody: {option: "trashbin"}
			}, me, function(jdoc) {
				var me = this,
					f_3 = me.down("[name=f_3]");
					
				f_3.store.loadData(jdoc || []);
			}, false);
			
		req.send();
	},
	
	_recover: function(rec) {
		var me = this,
			tgt = me._tgt(),
			req = new IG$._rpc$(),
			payloads = [],
			i;
			
		if (tgt.length == 0)
		{
			return;
		}
		
		for (i=0; i < tgt.length; i++)
		{
			payloads.push({
				uid: tgt[i].get("uid")
			});
		}
		
		req.init(me, 
			{
				ack: "helpcontent",
				payload: payloads,
				mbody: {option: "recover_trash"}
			}, me, function(jdoc) {
				me.loadUID();
			}, false);
			
		req.send();
	},
	
	_purge: function(rec) {
		var me = this,
			tgt = me._tgt(),
			req = new IG$._rpc$(),
			payloads = [],
			i;
			
		if (tgt.length == 0)
		{
			return;
		}
		
		for (i=0; i < tgt.length; i++)
		{
			payloads.push({
				uid: tgt[i].get("uid")
			});
		}
		
		req.init(me, 
			{
				ack: "helpcontent",
				payload: payloads,
				mbody: {option: "remove_trash"}
			}, me, function(jdoc) {
				me.loadUID();
			}, false);
			
		req.send();
	},
	
	_tgt: function(rec) {
		var me = this,
			f_3 = me.down("[name=f_3]"),
			trows = [];
			
		if (rec)
		{
			trows.push(rec);
		}
		else
		{
			var store = f_3.getSelectionModel().selected,
				i, rec;
			
			for (i=0; i < store.length; i++)
			{
				trows.push(store.items[i]);
			}
		}
		
		return trows;
	},
	
	initComponent: function(){
		var me = this;
		
		me.title = IRm$.r1("L_TRASHBIN");

		ig$.$l_ = ig$.$l_ || 0;
		
		$s.apply(this, {
			border: 0,
			bodyPadding: 5,
			
			items: [
				
				{
					xtype: "panel",
					region: "center",
					title: IRm$.r1("L_TRASHBIN"), // "Revision Management",
					flex: 1,
					padding: 5,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					tbar: [
						{
							text: IRm$.r1("B_REFRESH"),
							handler: function() {
								this.loadUID();
							},
							scope: this
						},
						{
							text: IRm$.r1("B_PURGE"),
							handler: function() {
								this._purge();
							},
							scope: this
						},
						"->",
						{
							text: IRm$.r1("L_RECOVER"),
							handler: function() {
								this._recover();
							},
							scope: this
						}
					],
					items: [
						{
							xtype: "gridpanel",
							name: "f_3",
							selType: ig$.$l_ > 5 ? null : "checkboxmodel",
							selModel: {
								checkSelector: ".x-grid-cell"
							},
							store: {
								xtype: "store",
								fields: [
									"uid", "revision", "updatedate", "updatedatefm", "description", "username", "userid", "iscurrent", "writable"
								]
							},
							defaults: {
								menuDisabled: true
							},
							plugins: [
								{
									ptype: "cellediting",
									clicksToEdit: 1
								}
							],
							columns: [
								{
									text: IRm$.r1("B_REVISION"),
									dataIndex: "revision",
									width: 60
								},
								{
									text: IRm$.r1("B_TYPE"),
									dataIndex: "type",
									width: 60
								},
								{
									text: IRm$.r1("B_NAME"),
									dataIndex: "locale_name"
								},
								{
									text: IRm$.r1("B_LASTUPD"),
									dataIndex: "updatedatefm",
									width: 150
								},
								{
									text: IRm$.r1("B_MODI"),
									dataIndex: "username"
								},
								{
									text: IRm$.r1("B_DESC"),
									dataIndex: "description",
									flex: 1,
									hidden: ig$.$l_ > 5,
									editor: {
										xtype: "textfield",
										allowBlank: true
									}
								},
								{
									text: IRm$.r1("B_CURRENT"),
									dataIndex: "iscurrent"
								},
								{
									xtype: "actioncolumn",
									width: 50,
									menuDisabled: true,
									hidden: ig$.$l_ > 5,
									items: [
										{
											iconCls: "icon-grid-add",
											tooltip: IRm$.r1("B_PURGE"),
											handler: function (grid, rowIndex, colIndex) {
												var me = this,
													rec = grid.store.getAt(rowIndex);
												me._recover(rec);
											},
											scope: this
										},
										{
											iconCls: "icon-grid-delete",
											tooltip: IRm$.r1("L_RECOVER"),
											handler: function (grid, rowIndex, colIndex) {
												var me = this,
													rec = grid.store.getAt(rowIndex);
												me._purge(rec);
											},
											scope: this
										}
									]
								}
							]
						}
					]
				}
			]
		});
		
		IG$.mgr_trashbin.superclass.initComponent.call(this);
	},
	listeners: {
		afterrender: function(tobj) {
			var me = this;
			me.loadUID(me.uid);
		}
	}
});