	setViewMode: function(mview) {
		var mpanel = this.down("[name=mpanel]"),
			i;
		
		for (i=0; i < mpanel.items.items.length; i++)
		{
			if (i != mview)
			{
				mpanel.items.items[i].hide();
			}
		}
		
		this.down("[name=btn_exist_user]").setVisible(mview == 1);
		mpanel.items.items[mview].show();
	},
	
