IG$.classmodule = $s.extend($s.panel, {
	closable: true,
	
	layout: "border",
	bodyPadding: 0,
	
	_init: function() {
		var me = this;
		
		me.loadDataSource();
	},
	
	loadDataSource: function() {
		var me = this,
			req = new IG$._rpc$();
		
		req.init(me, 
			{
				ack: "userdb",
				payload: {address: "/Connections", option: "list"},
				mbody: {}
			}, me, function(jdoc) {
				var me = this,
					dp = [],
					i, cnode = jdoc.children, 
					p,
					t_ds = me.down("[name=t_ds]");				
				
				dp.push({name:"Select Database Instance", uid:""}); 
				
				if (cnode)
				{
					for (i=0; i < cnode.length; i++)
					{
						p = cnode[i];
						dp.push(p);
					}
				}
				
				t_ds.store.loadData(dp);
				t_ds.setValue("");
				
				me.loadItemContent();
			}, function() {
				me.loadItemContent();
			});
		req.send();
	},
	
	loadItemContent: function() {
		var me = this,
			req;
			
		req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "content",
				payload: {uid: me.uid},
				mbody: {option: "standard"}
			}, me, me.rs_loadItemContent, false);
		req.send();
	},
	
	rs_loadItemContent: function(jdoc) {
		var me = this,
			item = jdoc.content,
			jdom,
			jdomeditor,
			jview,
			w, h, i,
			dp = [],
			editor,
			t_ds = me.down("[name=t_ds]");
			
		me.item = item;
		item.modules = item.modules || [];
		
		t_ds.setValue(item.data_source || "");
		
		if (!window.ace)
		{
			IG$.getScriptCache(ig$.bootconfig.scmap.igcg, new IG$.callBackObj(me, function() {
				this.rs_loadItemContent(jdoc);
			}));
			
			return;
		}

		jdom = $(me.down("[name=javasrc]").el.dom);
		jdomeditor = $(".idv-jcl-edtr", jdom);
		jdomeditor.empty();
		
		w = IG$.j$ext._w(jdom);
		h = IG$.j$ext._h(jdom);
		
		jview = $("<div class='idv-jcl-view'></div>").appendTo(jdomeditor);
		IG$.j$ext._w(jview, w);
		IG$.j$ext._h(jview, h);
		
		editor = ace.edit(jview[0]);
		editor.getSession().setMode("ace/mode/java");
		
		me.scripteditor = editor;
		me.scriptview = jview;
		
		me.scriptview = jview;
		me.scripteditor = editor;
		
		jdom = $(me.down("[name=javasrc_all]").el.dom);
		jdomeditor = $(".idv-jcl-edtr", jdom);
		jdomeditor.empty();
		
		w = IG$.j$ext._w(jdom);
		h = IG$.j$ext._h(jdom);
		
		jview = $("<div class='idv-jcl-view'></div>").appendTo(jdomeditor);
		IG$.j$ext._w(jview, w);
		IG$.j$ext._h(jview, h);
		
		editor = ace.edit(jview[0]);
		editor.getSession().setMode("ace/mode/java");
		
		me.scriptview_all = jview;
		me.scripteditor_all = editor;
		
		dp = item.modules;
		
		me.down("[name=g_m]").store.loadData(dp);
		me.down("[name=m_s]").hide();
	},
	
	saveMetaContent: function(smode) {
		var me = this,
			req = new IG$._rpc$(),
			cnt = me.item,
			ack = smode == "source" ? "plugin" : "writecontent",
			t_ds = me.down("[name=t_ds]");
			
		me.commitModule();
		
		cnt.data_source = t_ds.getValue();
		
		req.init(me, 
			{
				ack: ack,
				payload: {uid: me.uid, option: (smode == "source" ? smode : null)},
				mbody: cnt,
				meta_type: "class_module"
			}, me, me.rs_saveMetaContent, null, smode);
		req.send();
	},
	
	rs_saveMetaContent: function(jdoc, smode) {
		var me = this,
			tnode,
			scode;
		
		if (smode == "source")
		{
			me.scripteditor_all.setValue(jdoc.source);
		}
		else if (smode == "compile")
		{
			me.doCompile();
		}
		else
		{
			IG$.alertmsg(ig$.appname, IRm$.r1("M_SAVED"), null, null, 0, "success");
		}
	},
	
	doCompile: function() {
		var me = this,
			req = new IG$._rpc$(),
			cnt;
		
		req.init(me, 
			{
				ack: "plugin",
				payload: {uid: me.uid, option: "compile"},
				mbody: me.item,
				meta_type: "class_module"
			}, me, me.rs_doCompile, null);
		req.send();
	},
	
	rs_doCompile: function(jdoc) {
		var me = this,
			tnode = jdoc && jdoc.length ? jdoc[0] : null,
			err,
			errmsg = [];
			
		if (tnode && tnode.status == "E")
		{
			err = tnode.results;
			err.error_details = err.error_details || [];
			for (i=0; i < err.error_details.length; i++)
			{
				errmsg.push("line : " + err.error_details[i].line_number + ", " + err.error_details[i].message);
			}
			me.down("[name=compileres]").setValue(errmsg.join("\r\n"));
			
			me.setViewMode(1);
		}
		else
		{
			me.down("[name=compileres]").setValue("");
			IG$.alertmsg(ig$.appname, IRm$.r1("M_COMP_SUCCESS"), null, null, 0, "success");
		}
	},
	
	toolbarHandler: function(cmd) {
		var me = this,
			g_m = me.down("[name=g_m]"),
			sel, rec, module,
			i;
		
		switch (cmd)
		{
		case "cmd_save":
			me.saveMetaContent();
			break;
		case "cmd_compile":
			me.saveMetaContent("compile");
			break;
		case "cmd_source":
			me.saveMetaContent("source");
			break;
		case "cmd_rename":
		case "cmd_delete":
			sel = g_m.getSelectionModel().selected;
			if (sel && sel.length > 0)
			{
				rec = sel.items[0];
			}
			
			if (rec && rec.get("name"))
			{
				for (i=0; i < me.item.modules.length; i++)
				{
					if (me.item.modules[i].name == rec.get("name"))
					{
						if (cmd == "cmd_delete")
						{
							me.item.modules.splice(i, 1);
						}
						else
						{
							module = me.item.modules[i];
						}
						break;
					}
				}
				
				if (cmd == "cmd_delete")
				{
					g_m.store.remove(rec);
					me._module = null;
					me.scripteditor.setValue("");
				}
				else
				{
					var dlg = new IG$.makeItemEditor({
						itemtype: "Module",
						itemname: module.name,
						description: module.description,
						commit_f: function() {
							var _m = this,
								citemname = _m.down("[name=fitemname]"),
								itemname = _m.down("[name=fitemname]").getValue(),
								desc = _m.down("[name=fdesc]").getValue(),
								fhelpuid = _m.down("[name=fhelpuid]").getValue() || null,
								selitemtype = _m.down("[name=selitemtype]"),
								r;
								
							citemname.clearInvalid();
							
							if (!itemname)
							{
								citemname.markInvalid([citemname.blankText]);
								return;
							}
							
							if (itemname.indexOf(" ") > -1)
							{
								citemname.markInvalid(["Space not allowed"]);
								return;
							}
							
							rec.set("name", itemname);
							rec.set("description", desc);
							module.name = itemname;
							module.description = desc;
							
							_m.close();
						}
					});
					IG$.checkLogin(this, dlg);
				}
			}
			break;
		}
	},
	
	commitModule: function() {
		var me = this;
		
		if (me._module)
		{
			me._module.script = me.scripteditor.getValue();
			me._module.iparams = [];
			me._module.oparams = [];
			
			$.each(["iparams", "oparams"], function(i, k) {
				var g = me.down("[name=g_" + k + "]"),
					i,
					rec,
					p = me._module[k];
					
				for (i=0; i < g.store.data.items.length; i++)
				{
					rec = g.store.data.items[i];
					if (rec.get("name"))
					{
						p.push({
							name: rec.get("name"),
							multiplevalues: rec.get("multiplevalues")
						});
					}
				}
			});
		}
	},
	
	editModule: function(rec) {
		var me = this,
			ename = rec.get("name"),
			i, item = me.item,
			module;
			
		if (me._module)
		{
			me.commitModule();
		}
			
		for (i=0; i < item.modules.length; i++)
		{
			if (item.modules[i].name == ename)
			{
				module = item.modules[i];
				break;
			}
		}
		
		if (module)
		{
			me.down("[name=m_s]").show();
			me.scripteditor.setValue(module.script || "");
			me._module = module;
			
			$.each(["iparams", "oparams"], function(i, k) {
				var g = me.down("[name=g_" + k + "]"),
					i,
					dp = [],
					p = me._module[k];
					
				if (p)
				{
					for (i=0; i < p.length; i++)
					{
						dp.push({
							name: p[i].name,
							multiplevalues: p[i].multiplevalues
						});
					}
				}
				
				g.store.loadData(dp);
			});
		}
	},
	
	addModule: function(itemname, desc) {
		var me = this,
			g_m = me.down("[name=g_m]"),
			r = true,
			i;
		
		if (me._module)
		{
			me.commitModule();
		}
		
		for (i=0; i < me.item.modules.length; i++)
		{
			if (me.item.modules[i].name == itemname)
			{
				r = false;
				break;
			}
		}
		
		if (r == true)
		{
			g_m.store.add({
				name: itemname,
				description: desc
			});
			
			me.item.modules.push({
				name: itemname,
				description: desc,
				script: null,
				iparams: [],
				oparams: []
			});
		}
		
		return r;
	},
	
	setViewMode: function(mode) {
		var me = this,
			mcard = me.down("[name=mcard]"),
			t_source = me.down("[name=t_source]");
		
		if (mode > -1)
		{
			me.vcard = mode;
		}
		else
		{
			me.vcard = (me.vcard == 0) ? 1 : 0;
		}
		
		if (me.vcard == 0)
		{
			mcard.getLayout().setActiveItem(0);
			t_source.setText("View Source");
		}
		else
		{
			mcard.getLayout().setActiveItem(1);
			t_source.setText("Edit Code");
			me.toolbarHandler("cmd_source"); 
		}
	},

	initComponent: function() {
		var me = this;
		
		me.vcard = 0;
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: "card",
					region: "center",
					flex: 1,
					name: "mcard",
					items: [
						{
							xtype: "panel",
							layout: "border",
							items: [
								{
									xtype: "panel",
									title: "Module Functions",
									region: "west",
									layout: "fit",
									split: true,
									collapsible: true,
									collapseMode: "mini",
									width: 300,
									tbar: [
										{
											text: IRm$.r1("B_NEW_ITEM"),
											handler: function() {
												var dlg = new IG$.makeItem({
													itemtype: "Module",
													commit_f: function() {
														var _m = this,
															citemname = _m.down("[name=fitemname]"),
															itemname = _m.down("[name=fitemname]").getValue(),
															desc = _m.down("[name=fdesc]").getValue(),
															fhelpuid = _m.down("[name=fhelpuid]").getValue() || null,
															selitemtype = _m.down("[name=selitemtype]"),
															r;
															
														citemname.clearInvalid();
														
														if (!itemname)
														{
															citemname.markInvalid([citemname.blankText]);
															return;
														}
														
														if (itemname.indexOf(" ") > -1)
														{
															citemname.markInvalid(["Space not allowed"]);
															return;
														}
														
														r = me.addModule.call(me, itemname, desc);
														
														if (r == true)
														{
															_m.close();
														}
													}
												});
												IG$.checkLogin(this, dlg);
											},
											scope: this
										},
										{
											text: IRm$.r1("B_RENAME"),
											handler: function() {
												this.toolbarHandler("cmd_rename");
											},
											scope: this
										},
										{
											text: IRm$.r1("B_DELETE"),
											handler: function() {
												this.toolbarHandler("cmd_delete");
											},
											scope: this
										}
									],
									items: [
										{
											xtype: "gridpanel",
											name: "g_m",
											store: {
												xtype: "store",
												fields: [
													"name", "description"
												]
											},
											columns: [
												{
													text: IRm$.r1("B_NAME"),
													flex: 1,
													dataIndex: "name"
												},
												{
													text: IRm$.r1("B_DESC"),
													flex: 1,
													dataIndex: "description"
												},
												{
													xtype: "actioncolumn",
													width: 30,
													menuDisabled: true,
													items: [
														{
															iconCls: "icon-grid-config",
															tooltip: IRm$.r1("L_EDIT"),
															handler: function (grid, rowIndex, colIndex) {
																var grd = grid,
																	store = grd.store,
																	rec = store.getAt(rowIndex);
																	
																this.editModule(rec);
															},
															scope: this
														}
													]
												}
											],
											listeners: {
												cellclick: function(grid, node, cellIndex, record, rowIndex, e) {
													if (cellIndex != 2)
													{
														this.editModule(record);
													}
												},
												scope: this
											}
										}
									]
								},
								{
									region: "center",
									xtype: "panel",
									flex: 1,
									title: "Module Sources",
									layout: "fit",
									items: [
										{
											xtype: "container",
											layout: {
												type: "vbox",
												align: "stretch"
											},
											items: [
												{
													xtype: "panel",
													border: 0,
													layout: "border",
													name: "m_s",
													items: [
														{
															name: "javasrc",
															region: "center",
															flex: 1,
															html: "<div class='idv-jcl-edtr'></div>",
															split: true,
															listeners: {
																resize: function(tobj, w, h, ow, oh, opts) {
																	if (me.scriptview && me.scripteditor)
																	{
																		IG$.j$ext._w(me.scriptview, w);
																		IG$.j$ext._h(me.scriptview, h);
																		me.scripteditor.resize(true);
																	}
																}
															}
														},
														{
															xtype: "panel",
															region: "east",
															width: 200,
															layout: {
																type: "vbox",
																align: "stretch"
															},
															items: [
																{
																	xtype: "gridpanel",
																	name: "g_iparams",
																	title: "Input Parameters",
																	flex: 1,
																	store: {
																		xtype: "store",
																		fields: [
																			"name"
																		]
																	},
																	selType: "checkboxmodel",
																	selModel: {
																		checkSelector: ".x-grid-cell",
																		mode: "MULTI"
																	},
																	plugins: [
																		{
																			ptype: "cellediting",
																			clicksToEdit: 1
																		}
																	],
																	columns: [
																		{
																			text: IRm$.r1("B_NAME"),
																			flex: 1,
																			dataIndex: "name",
																			editor: {
																				xtype: "textfield",
																				allowBlank: false
																			}
																		}
																	],
																	tbar: [
																		{
																			xtype: "button",
																			text: IRm$.r1("B_ADD"),
																			handler: function() {
																				var me = this,
																					g_iparams;
																				
																				g_iparams = me.down("[name=g_iparams]");
																				g_iparams.store.add({
																					name: "",
																					value: ""
																				});
																			},
																			scope: this
																		},
																		{
																			xtype: "button",
																			text: IRm$.r1("B_REMOVE"),
																			handler: function() {
																				var me = this,
																					g_iparams = me.down("[name=g_iparams]"),
																					g_iparams_st = g_iparams.store,
																					sel = g_iparams.getSelectionModel().selected,
																					i;
																				
																				for (i=sel.length-1; i >= 0; i--)
																				{
																					g_iparams_st.remove(sel.items[i]);
																				}
																			},
																			scope: this
																		}
																	]
																},
																{
																	xtype: "gridpanel",
																	title: "Output Parameters",
																	name: "g_oparams",
																	flex: 1,
																	store: {
																		xtype: "store",
																		fields: [
																			"name"
																		]
																	},
																	selType: "checkboxmodel",
																	selModel: {
																		checkSelector: ".x-grid-cell",
																		mode: "MULTI"
																	},
																	plugins: [
																		{
																			ptype: "cellediting",
																			clicksToEdit: 1
																		}
																	],
																	columns: [
																		{
																			text: IRm$.r1("B_NAME"),
																			flex: 1,
																			dataIndex: "name",
																			editor: {
																				xtype: "textfield",
																				allowBlank: false
																			}
																		},
																		{
																			xtype: "checkcolumn",
																			width: 50,
																			text: "Multi",
																			dataIndex: "multiplevalues"
																		}
																	],
																	tbar: [
																		{
																			xtype: "button",
																			text: IRm$.r1("B_ADD"),
																			handler: function() {
																				var me = this,
																					g_oparams;
																				
																				g_oparams = me.down("[name=g_oparams]");
																				g_oparams.store.add({
																					name: "",
																					value: "",
																					multiplevalues: false
																				});
																			},
																			scope: this
																		},
																		{
																			xtype: "button",
																			text: IRm$.r1("B_REMOVE"),
																			handler: function() {
																				var me = this,
																					g_oparams = me.down("[name=g_oparams]"),
																					g_oparams_st = g_oparams.store,
																					sel = g_oparams.getSelectionModel().selected,
																					i;
																				
																				for (i=sel.length-1; i >= 0; i--)
																				{
																					g_oparams_st.remove(sel.items[i]);
																				}
																			},
																			scope: this
																		}
																	]
																}
															]
														}
													]
												}
											]
										}
									]
								}
							]
						},
						{
							xtype: "panel",
							title: "Source View (ReadOnly)",
							layout: "border",
							items: [
								{
									name: "javasrc_all",
									flex: 1,
									region: "center",
									html: "<div class='idv-jcl-edtr'></div>",
									split: true,
									listeners: {
										resize: function(tobj, w, h, ow, oh, opts) {
											if (me.scriptview_all && me.scripteditor_all)
											{
												IG$.j$ext._w(me.scriptview_all, w);
												IG$.j$ext._h(me.scriptview_all, h);
												me.scripteditor_all.resize(true);
											}
										}
									}
								},
								{
									xtype: "panel",
									title: "Compile Messages",
									height: 200,
									region: "south",
									layout: "fit",
									items: [
										{
											xtype: "textarea",
											popup_editor: true,
											name: "compileres"
										}
									]
								}
							]
						}
					]
				}
			],
			tbar: [
				{
					iconCls: "icon-toolbar-save",
					name: "t_save",
					tooltip: IRm$.r1("L_SAVE_CONTENT"),
					handler: function() {
						this.toolbarHandler("cmd_save"); 
					},
					scope: this
				},
				{
					text: "Compile",
					name: "t_save_as",
					tooltip: "Compile",
					handler: function() {
						this.toolbarHandler("cmd_compile"); 
					},
					scope: this
				},
				{
					xtype: "combobox",
					name: "t_ds",
					tooltip: IRm$.r1("L_DATA_SOURCE"),
					fieldLabel: IRm$.r1("L_DATA_SOURCE"),
					labelPosition: "right",
					queryMode: "local",
					displayField: "name",
					valueField: "uid",
					editable: false,
					autoSelect: true,
					store: {
						xtype: "store",
						fields: []
					}
				},
				{
					text: "Package Lists",
					handler: function() {
						var me = this,
							dlg = new IG$.classModuleClsImport({
								litem: me.item
							});
						dlg.show();
					},
					scope: this
				},
				"->",
				{
					name: "t_source",
					text: "View Source",
					handler: function() {
						this.setViewMode(-1);
					},
					scope: this
				}
			]
		});
			  
		IG$.classmodule.superclass.initComponent.call(this);
	},
	
	listeners: {
		afterrender: function(tobj) {
			var me = this;
			me._init();
		}
	}
});

IG$.classModuleClsImport = $s.extend($s.window, {
	width: 400,
	height: 500,
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	
	_init: function() {
		var me = this,
			ltx = me.down("[name=ltx]"),
			litem = me.litem;
		if (litem)
		{
			ltx.setValue(litem.cls || "");
		}
	},
	_confirm: function() {
		var me = this,
			ltx = me.down("[name=ltx]"),
			litem = me.litem;
			
		if (litem)
		{
			litem.cls = ltx.getValue();
		}
		
		me.callback && me.callback.execute(litem);
		
		me.close();
	},
	items: [
		{
			xtype: "panel",
			layout: {
				type: "vbox",
				align: "stretch"
			},
			items: [
				{
					xtype: "textarea",
					name: "ltx",
					flex: 1
				},
				{
					xtype: "displayfield",
					value: "* Add class lists with Java : import java.util.*;"
				}
			]
		}
	],
	initComponent: function() {
		$s.apply(this, {
			title: IRm$.r1("L_PKG_LST"),
			buttons:[
				"->",
				{
					text: IRm$.r1("B_CONFIRM"),
					handler: function() {
						this._confirm();
					},
					scope: this
				}, 
				{
					text: IRm$.r1("B_CANCEL"),
					handler:function() {
						this.close();
					},
					scope: this
				}
			]
		});
		IG$.classModuleClsImport.superclass.initComponent.call(this);
	},
	
	listeners: {
		afterrender: function(tobj) {
			tobj._init();
		}
	}
});