IG$.mgrUserDuty = $s.extend($s.window, {
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 500,
	autoHeight: true,
	bodyPadding: 10,
	bodyStyle: {
		background: "#ffffff"
	},
	
	_confirm: function() {
		var me = this;
		
		if (me.callback)
		{
			var gridduty_sel = me.down("[name=gridduty_sel]"),
				i,
				sel = []; // gridduty.getSelectionModel().getSelection();
				
			for (i=0; i < gridduty_sel.store.data.length; i++)
			{
				sel.push(gridduty_sel.store.data.items[i]);
			}
			me.callback.execute(sel);
		}
		me.close();
	},
	
	loadDutyList: function() {
		var panel = this,
			req = new IG$._rpc$();
		req.init(panel, 
			{
				ack: "usercontent",
				payload: {address: "/Auth"},
				mbody: {option: 'select'}
			}, panel, panel.rs_loadDutyList, false);
		req.send();
	},
	
	rs_loadDutyList: function(jdoc) {
		var i,
			cnodes,
			duties = [], 
			duties_sel = [],
			rec,
			duty,
			gridduty = this.down("[name=gridduty]"),
			gridduty_sel = this.down("[name=gridduty_sel]"),
			sel = {};
			// smodel = gridduty.getSelectionModel();
		
		if (this.sel)
		{
			for (i=0; i < this.sel.length; i++)
			{
				sel[this.sel[i].sid] = this.sel[i];
			}
		}
		
		if (jdoc)
		{
			for (i=0; i < jdoc.length; i++)
			{
				duty = jdoc[i];
				if (sel[duty.sid])
				{
					duties_sel.push(duty);
				}
				else
				{
					duties.push(duty);
				}
			}
		}
		
		gridduty.store.loadData(duties);
		gridduty_sel.store.loadData(duties_sel);
//		
//		for (i=0; i < gridduty.store.data.items.length; i++)
//		{
//			rec = gridduty.store.data.items[i];
//			
//			if (sel[rec.get("sid")])
//			{
//				smodel.select(rec, true, true);
//			}
//		}
	},
	
//	createDuty: function() {
//		var panel = this,
//			dlg = new IG$.makeItem({
//			itemtype: "Auth",
//			callback: new IG$.callBackObj(panel, panel.rs_createDuty)
//		});
//		
//		dlg.show(panel);
//	},
//	
//	rs_createDuty: function(xdoc) {
//		this.loadDutyList();
//	},


	moveDuty: function(select) {
		var me = this,
			i,
			grd_source = me.down("[name=gridduty" + (select == false ? "_sel" : "") + "]"),
			grd_target = me.down("[name=gridduty" + (select == true ? "_sel" : "") + "]"),
			sel = grd_source.getSelectionModel().getSelection();
			
		if (sel.length > 0)
		{
			for (i=0; i < sel.length; i++)
			{
				grd_source.store.remove(sel[i]);
				grd_target.store.add(sel[i]);
			}
		}
	},
	
	initComponent : function() {
		this.title = IRm$.r1("L_MGR_UD");
		
		var items = [
			{
				xtype: "form",
				layout: "anchor",
				defaults: {
					anchor: "100%"
				},
				border: 0,
				bodyPadding: 5,
				items: [
					{
						xtype: "displayfield",
						value: IRm$.r1("L_UD_DESC")
					},
					{
						xtype: "panel",
						border: 0,
						layout: {
							type: "hbox",
							align: "stretch"
						},
						items: [
							{
								xtype: "gridpanel",
								name: "gridduty",
								border: 1,
								flex: 1,
								height: 300,
								selType: "checkboxmodel",
								selModel: {
									checkSelector: ".x-grid-cell",
									mode: "MULTI"
								},
								multiSelect: true,
								store: {
									fields: [
										"name", "dutytype", "sid", "status", "type"
									]
								},
								columns: [
									{
										header: IRm$.r1("B_NAME"),
										dataIndex: "name",
										flex: 1,
										sortable: true,
										hideable: false							
									},
									{
										header: IRm$.r1("B_TYPE"),
										dataIndex: "type",
										flex: 1,
										sortable: true,
										hideable: false							
									}
								]
							},
							{
								xtype: "container",
								bodyPadding: "0 4 0 4",
								border: 0,
								layout: {
									type: "vbox",
									align: "stretch"
								},
								items: [
									{
										xtype: "container",
										flex: 1
									},
									{
										xtype: "button",
										text: ">>",
										handler: function() {
											this.moveDuty(true);
										},
										scope: this
									},
									{
										xtype: "button",
										text: "<<",
										handler: function() {
											this.moveDuty(false);
										},
										scope: this
									},
									{
										xtype: "container",
										flex: 1
									}
								]
							},
							{
								xtype: "gridpanel",
								name: "gridduty_sel",
								flex: 1,
								border: 1,
								height: 300,
								selType: "checkboxmodel",
								selModel: {
									checkSelector: ".x-grid-cell",
									mode: "MULTI"
								},
								multiSelect: true,
								store: {
									fields: [
										"name", "dutytype", "sid", "status", "type"
									]
								},
								columns: [
									{
										header: IRm$.r1("B_NAME"),
										dataIndex: "name",
										flex: 1,
										sortable: true,
										hideable: false							
									},
									{
										header: IRm$.r1("B_TYPE"),
										dataIndex: "type",
										flex: 1,
										sortable: true,
										hideable: false							
									}
								]
							}
						   ]
					   }
				]
			}
		];
		
		var buttons = [
			{
				text: IRm$.r1('B_CONFIRM'),
				handler: function() {
					this._confirm();
				},
				scope: this
			}, 
			{
				text: IRm$.r1('B_CANCEL'),
				handler:function() {
					this.close();
				},
				scope: this
			}
		];
		
		$s.apply(this, {
			items: items,
			buttons: buttons
		});
		
		IG$.mgrUserDuty.superclass.initComponent.apply(this, arguments);
	},
	
	listeners: {
		afterrender: function(ui) {
			this.loadDutyList();
		}
	}
});
