IG$.sync_user_param = $s.extend($s.window, {
	
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 600,
	height: 500,
	
	_init: function() {
		var me = this,
			params = me.down("[name=params]"),
			dmodule = me.dmodule;
		
		if (dmodule && dmodule.parameters)
		{
			$.each(dmodule.parameters, function(n, p) {
				var ctrl = {
					xtype: p.type,
					name: p.name,
					fieldLabel: p.label || p.name,
					required: p.isnecessary
				}, isadmin, i, ctrldesc;
				
				if (ctrl.xtype == "datasource")
				{
					ctrl.xtype = "combobox";
					ctrl.valueField = "uid";
					ctrl.displayField = "name";
					ctrl.queryMode = "local";
					ctrl.editable = false;
					ctrl.store = {
						data: [{name:"Select Database Instance", uid:""}]
					};
					
					isadmin = IG$.dlgLogin.jS2;
					
					for (i=0; i < me.dsource.length; i++)
					{
						p = me.dsource[i];
						if (isadmin == true || (isadmin == false && p.name.toUpperCase() != "IGCBASE"))
						{
							ctrl.store.data.push({
								uid: p.uid,
								name: p.name
							});
						}
					}
				}
				else if (ctrl.xtype == "textarea")
				{
					ctrl.height = 150;
				}
				
				params.add(ctrl);
				
				if (p.description)
				{
					ctrldesc = {
						xtype: "textarea",
						fieldLabel: " ",
						readOnly: true,
						required: false,
						value: p.description,
						height: 150
					};
					params.add(ctrldesc);
				}
			});
			
			setTimeout(function() {
				$.each(dmodule.parameters, function(n, p) {
					var ctrl = me.down("[name=" + p.name + "]");
					if (ctrl)
					{
						if (p.initial_values && p.initial_values.length)
						{
							ctrl.setValue(p.initial_values[0].value);
						}
						else
						{
							ctrl.setValue("");
						}
					}
				});
			}, 10);
		}
	},
	
	initComponent: function() {
		var me = this;

		$s.apply(this, {
			defaults: {padding: 10},
			title: "SSO Parameters",
			items: [
				{
					xtype: "container",
					defaults: {padding: 10},
					padding: 10,
					autoScroll: true,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: [
						{
							xtype: "fieldset",
							title: "Parameters",
							name: "params",
							flex: 1,
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: []
						}
					]
				}
			],
			buttons:[
				{
					text: IRm$.r1('B_CONFIRM'),
					handler: function() {
						var me = this,
							params = me.down("[name=params]"),
							model = {},
							has_error;
						
						has_error = params.getAllValues(model);
						
						if (has_error)
							return;
						
						me.callback && me.callback.execute(model);
						me.close();
					},
					scope: this
				}, 
				{
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(tobj) {
					tobj._init();
				}
			}
		});

		IG$.sync_user_param.superclass.initComponent.apply(me, arguments);
	}
});