IG$.MetaEditorPanel = $s.extend($s.panel, {
	autoScroll:false,
	layout: "fit",
	
	closable: true,
	
	loadMetaContent: function() {
		var me = this;
		
		if (!me.uid)
		{
			IG$.alertmsg(ig$.appname, "Select item object!", null, null, 0, "error");
			return;
		}
		
		var req = new IG$._rpc$();

		req.init(me, 
			{
				ack: "content",
				payload: {uid: me.uid},
				mbody: {option: "diagnostics"}
			}, me, function(jdoc) {
				var panel = this
					content = JSON.stringify(jdoc, null, 2),
					tb_info = panel.down("[name=tb_info]"),
					metaview = panel.down("[name=metaview]");
				tb_info.setValue(jdoc.nodepath);
				metaview.setValue(content);
			}, null);				
		req.send();
	},
	
	saveContent: function() {
		var me = this,
			metaview = me.down("[name=metaview]"),
			req = new IG$._rpc$(),
			cnt,
			metatype;

		try
		{
			cnt = JSON.parse(metaview.getValue());
		}
		catch (e)
		{
			IG$.alertmsg(ig$.appname, "Error on json format! " + e, null, null, 0, "error");
			return;
		}
		
		if (cnt && cnt.content)
		{
			metatype = cnt.content.meta_type;
			
			if (!metatype)
			{
				IG$.alertmsg(ig$.appname, "Item " + cnt.type + " can not be imported! " + e, null, null, 0, "error");
				return;
			}
			
			req.init(me, 
				{
					ack: "writecontent",
					payload: {uid: me.uid},
					mbody: cnt.content,
					meta_type: metatype
				}, me, function(jdoc) {
					IG$.alertmsg(ig$.appname, IRm$.r1('M_SAVED'), null, null, 0, "success");
				}, null);				
			req.send();
		}
	},
	
	initComponent : function(){
		$s.apply(this,{
			padding: 6,
			items: [
				{
					xtype: "panel",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					tbar: [
						{
							text: IRm$.r1("L_SAVE_CONTENT"),
							handler: function() {
								this.saveContent();
							},
							scope: this
						},
						{
							text: IRm$.r1("L_REFRESH"),
							handler: function() {
								this.loadMetaContent();
							},
							scope: this
						},
						{
							xtype: "textfield",
							width: 400,
							name: "tb_info",
							readOnly: true
						}
					],
					padding: 6,
					items: [
						{
							xtype: "textarea",
							name: "metaview",
							flex: 1
						}
					]
				}
			],
			listeners: {
				afterrender: function(ui) {
					ui.loadMetaContent();
				}
			}
		});
		
		IG$.MetaEditorPanel.superclass.initComponent.call(this);
	}
});

