IG$.chart_dsgn = $s.extend(IG$.v$pnl, {
	padding: 10,
	closable: true,
	iconCls: "icon-schedule",
	layout: "fit",
	
	init_f: function() {
		var me = this;
		me.load_cnt();		
	},
	
	load_cnt: function() {
		var me = this,
			req = new IG$._rpc$(),
			params;
			
		params = {
			ack: "content",
			payload: {address: "/SYS_Config/chart_dsgn"},
			mbody: {subfolder: "T"}
		};
		
		req.init(me, 
			params, me, function(jdoc) {
				var me = this,
					grdprog = me.down("[name=grdprog]");
					
				grdprog.store.loadData(jdoc.result);
			}, null);
		req.send();
	},
	
	_editProg: function(rec) {
		var me = this,
			grdprog = me.down("[name=grdprog]"),
			mcard = me.down("[name=mcard]"),
			w, h;
		
		mcard.getLayout().setActiveItem(1);
		
		me._editor_rec = rec ? rec : null;
		me.mdata = rec ? rec.data : {};
		
		if (!me.__tabinit_)
		{
			var jdom = $(me.down("[name=script_0]").el.dom),
				jdomeditor = $(".idv-jcl-edtr", jdom),
				jview,
				editor;
			
			jdomeditor.empty();
			
			w = Math.max(IG$.j$ext._w(jdom) - 4, w || 380);
			h = Math.max(IG$.j$ext._h(jdom) - 4, h || 380);
			
			jview = $("<div class='idv-jcl-view'></div>").appendTo(jdomeditor);
			IG$.j$ext._w(jview, w);
			IG$.j$ext._h(jview, h);
			
			editor = ace.edit(jview[0]);
			editor.getSession().setMode("ace/mode/java");
			
			me.scriptview_0 = jview;
			me.scripteditor_0 = editor;
			
			me.__tabinit_ = 1;
			
			setTimeout(function() {
				me._editor_init_f();
			}, 500);
		}
		else
		{
			me._editor_init_f();
		}
	},
	
	initComponent: function() {
		var me = this;
		
		$s.apply(this, {
			items: [
				{
					xtype: "panel",
					layout: "card",
					name: "mcard",
					items: [
						// card1 -- list of schedule
						{
							xtype: "gridpanel",
							flex: 1,
							name: "grdprog",
							store: {
								xtype: "store"
							},
							columns: [
								{
									text: IRm$.r1("B_NAME"),
									width: 200,
									dataIndex: "name"
								},
								{
									text: IRm$.r1("L_PKG_NAME"), // "Package name",
									flex: 1,
									dataIndex: "java_class"
								},
								{
									xtype: "actioncolumn",
									width: 60,
									items: [
										{
											iconCls: "icon-grid-delete",
											tooltip: IRm$.r1('L_REMOVE'),
											handler: function (grid, rowIndex, colIndex) {
												var me = this,
													grd = grid,
													store = grd.store,
													rec = store.getAt(rowIndex);
												
												IG$.confirmMessages(ig$.appname, IRm$.r1("M_CONFIRM"), function(e) {
													store.removeAt(rowIndex);
												});
											},
											scope: this
										}
									]
								}
							],
							listeners: {
								itemclick: function(tobj, record, item, index, e, eopts) {
									this._editProg(record);
								},
								scope: this
							},
							tbar: [
								{
									text: IRm$.r1("B_REFRESH"),
									handler: function() {
										this.init_f();
									},
									scope: this
								},
								{
									text: IRm$.r1("L_ADD_NEW_CHART"),
									handler: function() {
										this._editProg();
									},
									scope: this
								},
								"->",
								{
									text: IRm$.r1("L_EXPORT_META"),
									tooltip: IRm$.r1("L_EXPORT"),
									handler: function() {
										this._exp();
									},
									scope: this
								}
							]
						},
						// end of card1 -- list of schedule
						// card2 -- program editor
						{
							xtype: "panel",
							layout: "fit",
							tbar: [
								{
									text: IRm$.r1('B_CONFIRM'),
									handler: function() {
										this._apply_prog_part();
										this._editor_commit_f();
									},
									scope: this
								},
								{
									text: IRm$.r1('B_CANCEL'),
									handler:function() {
										var me = this,
											mcard = me.down("[name=mcard]");
										me.__prog_start = false;
										mcard.getLayout().setActiveItem(0);
									},
									scope: this
								}
							],
							items: [
								{
									xtype: "panel",
									padding: 10,
									layout: {
										type: "vbox",
										align: "stretch"
									},
									items: [
										
										{
											xtype: "panel",
											padding: 10,
											layout: {
												type: "vbox",
												align: "stretch"
											},
											autoScroll: true,
											title: IRm$.r1("B_GENERAL"),
											items: [
												
												{
													xtype: "container",
													layout: {
														type: "hbox",
														align: "stretch"
													},
													items: [
														{
															xtype: "container",
															layout: {
																type: "vbox",
																align: "stretch"
															},
															items: [
																{
																	xtype: "textfield",
																	fieldLabel: IRm$.r1("B_NAME"),
																	name: "m_name"
																},
																{
																	xtype: "textfield",
																	fieldLabel: IRm$.r1("B_LABEL"),
																	name: "m_label"
																},
																{
																	xtype: "textfield",
																	fieldLabel: IRm$.r1("B_CHART_TYPE"),
																	name: "m_charttype"
																},
																{
																	xtype: "textfield",
																	fieldLabel: IRm$.r1("B_CHART_SUBTYPE"),
																	name: "m_subtype"
																},
																{
																	xtype: "textfield",
																	fieldLabel: IRm$.r1("B_CHART_IMG"),
																	name: "m_img"
																},
																{
																	xtype: "combobox",
																	fieldLabel: IRm$.r1("B_GRP"),
																	name: "m_grp",
																	displayField: "name",
																	valueField: "value",
																	store: {
																		data: IG$.__chartoption.chartcateg
																	}
																}
															]
														},
														{
															xtype: "container",
															flex: 1
														}
													]
												},
												{
													xtype: "fieldset",
													paddingY: 20,
													title: "Codes",
													layout: {
														type: "vbox",
														align: "stretch"
													},
													items: [
														{
															xtype: "container",
															layout: {
																type: "hbox",
																align: "stretch"
															},
															items: [
																{
																	xtype: "textfield",
																	fieldLabel: IRm$.r1("L_JAVA_CLASS"), // "JavaClass",
																	name: "m_java_class"
																},
																{
																	xtype: "container",
																	flex: 1
																}
															]
														},
														{
															xtype: "panel",
															layout: {
																type: "vbox",
																align: "stretch"
															},
															height: 600,
															border: 0,
															items: [
																{
																	xtype: "panel",
																	layout: {
																		type: "hbox",
																		align: "stretch"
																	},
																	border: 0,
																	items: [
																		{
																			xtype: "combobox",
																			name: "prog_part",
																			fieldLabel: IRm$.r1("L_PROGRAM_PARTS"), // "Program Parts",
																			required: true,
																			displayField: "name",
																			valueField: "value",
																			store: {
																			},
																			listeners: {
																				change: function(tobj, nv, ov) {
																					var me = this;
																					if (ov)
																					{
																						me._apply_prog_part(ov);
																					}
																					me._update_prog_part();
																				},
																				scope: this
																			}
																		},
																		{
																			xtype: "container",
																			flex: 1
																		},
																		{
																			xtype: "button",
																			text: IRm$.r1("L_APPLY_CODES"),
																			handler: function() {
																				this._apply_prog_part();
																				this._editor_commit_f(true);
																			},
																			scope: this
																		}
																	]
																},
																{
																	name: "script_0",
																	flex: 1,
																	region: "center",
																	html: "<div class='idv-jcl-edtr'></div>",
																	split: true,
																	border: 1,
																	listeners: {
																		resize: function(tobj, w, h, ow, oh, opts) {
																			if (me.scriptview_0 && me.scripteditor_0)
																			{
																				IG$.j$ext._w(me.scriptview_0, w - 4);
																				IG$.j$ext._h(me.scriptview_0, h - 4);
																				me.scripteditor_0.resize(true);
																			}
																		}
																	}
																}
															]
														}
													]
												}
											]
										}
									]
								}
							]
						}
					]
				}
			]
		});
		
		IG$.chart_dsgn.superclass.initComponent.call(this);
	},
	listeners: {
		afterrender: function(tobj) {
			tobj.init_f();
		}
	}
});