/********************* user audit detail ***************************/
IG$.usrAudDet = $s.extend($s.window, {
	modal: true,
	region:'center',
	layout: "fit",
	closable: false,
	resizable:false,
	width: 650,
	height: 520,
	
	init_f: function() {
		var me = this,
			req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "helpcontent",
				payload: {
					tuid: me.tuid,
					sub_option: 'get_audit_user_detail'
				},
				mbody: {option: "audit_user"}
			}, me, function(jdoc) {
				var me = this,
					grd_hist = me.down("[name=grd_hist]");
					
				if (jdoc)
				{
					$.each(jdoc, function(i, d) {
						if (d.status_desc)
						{
							d.status_desc = IRm$.r1(d.status_desc);
						}
					});
				}
					
				grd_hist.store.loadData(jdoc);
			}, null);
		req.send();
	},
	
	initComponent : function() {
		var me = this,
			rec = me.rec;
		
		if (rec)
		{
			me.title = IRm$.r1("L_USR_AUD_TITLE", rec.get("uname") + "(" + rec.get("cdate_user") + ")");
		}
				
		$s.apply(this, {
			defaults: {
				padding: 10
			},
			
			items: [
				{
					xtype: "panel",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					padding: 10,
					defaults: {
						anchor: "100%"
					},
					items: [
						{
							xtype: "gridpanel",
							name: "grd_hist",
							flex: 1,
							store: {},
							columns: [
								{
									text: IRm$.r1("B_TYPE"),
									dataIndex: "status_desc",
									width: 120
								},
								{
									text: IRm$.r1("B_DESC"),
									dataIndex: "description",
									flex: 1
								},
								{
									text: IRm$.r1("L_LAST_ACC_DT"),
									dataIndex: "cdate_user",
									width: 140
								}
							]
						}
					]
				}
			],
			buttons:[
				"->",
				{
					text: IRm$.r1('B_CLOSE'),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],
			listeners: {
				afterrender: function(ui) {
					this.init_f();
				}
			}
		});
		
		IG$.usrAudDet.superclass.initComponent.apply(this, arguments);
	}
});