IG$.groupPrivSetting = $s.extend($s.window, {
	// title: "Group Privilege Setting",
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 400,
	autoHeight: true,
	plain: true,
	bodyPadding: 10,
	bodyStyle: {
		background: "#ffffff"
	},
	
	_confirm: function() {
		var me = this;
		me.callback && me.callback.execute();
		me.close();
	},
	
	loadPrivilege: function(gid) {
		var me = this,
			req = new IG$._rpc$();
		req.init(me, 
			{
				ack: "usercontent",
				payload: {address: "/usergroup", gid: gid, action: "privilege"},
				mbody: {option: 'standard'}
			}, me, me.rs_loadPrivilege, false);
		req.send();
	},
	
	rs_loadPrivilege: function(jdoc) {
	},
	
	setGroupActive: function(status) {
		var me = this,
			req = new IG$._rpc$(),
			content,
			groupname = me.groupname,
			gid = me.gid;
		
		content = {
			gid: gid
		};
		
		req.init(me, 
			{
				ack: "usercontent",
				payload: {address: "/usergroup", action: status},
				mbody: content
			}, me, me.rs_setGroupActive, false, [status]);
		req.send();
	},
	
	rs_setGroupActive: function(xdoc, param) {
		var me = this,
			status = param[0],
			ugroup = me.ugroup,
			btn_erase = me.down("[name=btn_erase]");
		
		switch (status)
		{
		case "activate":
			ugroup.set("status", "Active");
			ugroup.set("active", "1");
			btn_erase.setVisible(false);
			break;
		case "delete":
			ugroup.set("status", "Blocked");
			ugroup.set("active", "0");
			btn_erase.setVisible(true);
			break;
		case "erase":
			ugroup.parentNode.removeChild(ugroup);
			me.callback && me.callback.execute();
			me.close();
			break;
		}
	},
	
	initComponent : function() {
		var me = this;
		
		$s.apply(me, {
			title: IRm$.r1("L_GROUP_PRIV_SETTINGS"), 
			items: [
				{
					xtype: "form",
					layout: "anchor",
					defaults: {
						anchor: "100%"
					},
					border: 0,
					items: [
						{
							xtype: "displayfield",
							value: "Check to allow each priviege."
						},
						{
							xtype: "textfield",
							readOnly: true,
							fieldLabel: IRm$.r1("L_GROUP_NAME"), // Group name",
							name: "groupname"
						},
						{
							xtype: "fieldset",
							title: IRm$.r1("L_ENABLE_DISABLE"), // "Enable/Disable",
							name: "p_activate",
							hidden: false,
							layout: {
								type: "hbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "button",
									iconCls: 'icon-toolbar-activate',
									flex: 1,
									text: IRm$.r1("B_ACTIVATE"),
									handler: function() {
										this.setGroupActive("activate");
									},
									scope: me
								},
								{
									xtype: "container",
									width: 10
								},
								{
									xtype: "button",
									iconCls: 'icon-toolbar-deprivation',
									flex: 1,
									text: IRm$.r1("B_DEPRIVATE"),
									handler: function() {
										this.setGroupActive("delete");
									},
									scope: me
								}
							]
						}
					]
				}
			],
			buttons: [
				{
					text: IRm$.r1("L_ERASE_GROUP"),
					name: "btn_erase",
					hidden: me.ugroup.get("active") == "1" ? true : false,
					handler: function() {
						this.setGroupActive("erase");
					},
					scope: me
				},
				"->",
				{
					text: IRm$.r1('B_CONFIRM'),
					handler: function() {
						this._confirm();
					},
					scope: me
				}, 
				{
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: me
				}
			],
			listeners: {
				afterrender: function(ui) {
					var me = this;
					if (me.gid)
					{
						me.loadPrivilege(me.gid);
						var groupname = me.down("[name=groupname]");
						groupname.setValue(me.groupname || "");
					}
				}
			}
		});
		
		IG$.groupPrivSetting.superclass.initComponent.apply(me, arguments);
	}
});


IG$.TenantSetting = $s.extend($s.window, {
	// title: "Tenant Setting",
	modal: true,
	layout: "fit",
	closable: false,
	resizable:false,
	width: 400,
	autoHeight: true,
	plain: true,
	bodyPadding: 10,
	bodyStyle: {
		background: "#ffffff"
	},

	_confirm: function() {
		this.close();
	},
		
	setTenantActive: function(status) {
		var me = this,
			req = new IG$._rpc$(),
			content = {},
			tenantname = me.down("[name=tenantname]"),
			gid = me.gid,
			mts = me.mts;
		
		var berror = status == "rename" ? me.getAllValues(content) : 0;
		
		if (berror)
			return;
		
		content.mts = mts.get("uid");
		
		req.init(me, 
			{
				ack: "usercontent",
				payload: {address: "/tenants/" + mts.get("name"), action: status},
				mbody: content
			}, me, function(xdoc, param) {
				if (status == "rename")
				{
					for (var k in content)
					{
						mts.set(k, content[k]);
					}
				}
				me.rs_setTenantActive(xdoc, param);
			}, false, [status]);
		req.send();
	},
	
	rs_setTenantActive: function(xdoc, param) {
		var me = this,
			status = param[0],
			ugroup = me.mts,
			btn_erase = me.down("[name=btn_erase]");
		
		switch (status)
		{
		case "activate":
			ugroup.set("pstatname", "Active");
			ugroup.set("pstatus", "1");
			btn_erase.setVisible(false);
			break;
		case "delete":
			ugroup.set("pstatname", "Blocked");
			ugroup.set("pstatus", "0");
			btn_erase.setVisible(true);
			break;
		case "erase":
			ugroup.remove();
			me.close();
			break;
		case "rename":
			me.close();
			break;
		}
	},
	
	loadTemplates: function() {
		var me = this,
			req = new IG$._rpc$();
			
		req.init(me, 
			{
				ack: "helpcontent",
				payload: {},
				mbody: {option: "timezone"}
			}, me, function(jdoc) {
				var me = this,
					tnode,
					tnodes,
					i, dp_tmpl = [], dp_theme = [],
					p,
					a1 = me.down("[name=a1]"),
					a2 = me.down("[name=a2]");
					
				dp_tmpl.push({name: IRm$.r1("B_SELECT"), value: ""});
				if (jdoc.app_tmpl)
				{
					for (i=0; i < jdoc.app_tmpl.length; i++)
					{
						p = jdoc.app_tmpl[i];
						dp_tmpl.push(p);
					}
				}
				
				a1.store.loadData(dp_tmpl);
				
				dp_theme.push({name: IRm$.r1("B_SELECT"), value: ""});
				if (jdoc.themes)
				{
					tnodes = jdoc.themes;
					
					for (i=0; i < tnodes.length; i++)
					{
						p = tnodes[i];
						p.name = p.value;
						dp_theme.push(p);
					}
				}
				
				a2.store.loadData(dp_theme);
				
				if (me.mts)
				{
					me.setAllValues(me.mts.data);
				}
			});
		req.send();
	},
	
	initComponent : function() {
		var me = this;
		
		$s.apply(me, {
			title: IRm$.r1("L_TENANT_SETTINGS"),
			items: [
				{
					xtype: "form",
					layout: "anchor",
					defaults: {
						anchor: "100%"
					},
					border: 0,
					padding: 5,
					items: [
						{
							xtype: "fieldset",
							title: IRm$.r1("L_GENERAL_INFO"), // "General Information",
							layout: "anchor",
							items: [
								{
									xtype: "textfield",
									readOnly: me.mts && me.mts.get("pstatus") == "A",
									fieldLabel: IRm$.r1("B_NAME"),
									required: true,
									name: "name",
									mapname: "name"
								},
								{
									xtype: "textfield",
									fieldLabel: IRm$.r1("L_TENANT_NAME"),
									name: "tenantname",
									required: true
								},
								{
									xtype: "textfield",
									fieldLabel: IRm$.r1("L_LOGO_IMAGE"), // Logo image",
									name: "logoimage"
								}
							]
						},
						{
							xtype: "fieldset",
							title: IRm$.r1("B_OPTIONS"), // "Options",
							layout: "anchor",
							items: [
								{
									xtype: "combobox",
									fieldLabel: IRm$.r1("B_TEMPLATES"),
									name: "a1",
									editable: false,
									queryMode: "local",
									valueField: "value",
									displayField: "name",
									store: {
										xtype: "store",
										fields: [
											"name", "value"
										]
									}
								},
								{
									xtype: "combobox",
									fieldLabel: IRm$.r1("L_THEMS"), // Themes",
									name: "a2",
									editable: false,
									queryMode: "local",
									valueField: "value",
									displayField: "name",
									store: {
										xtype: "store",
										fields: [
											"name", "value"
										]
									}
								}
							]
						},
						{
							xtype: "fieldset",
							title: IRm$.r1("L_LICENSE_INFO"),
							layout: "anchor",
							items: [
								{
									xtype: "textfield",
									name: "turl",
									labelPosition: "top",
									fieldLabel: IRm$.r1("B_URL"), // "URL",
									readOnly: true
								},
								{
									xtype: "numberfield",
									name: "userlimit",
									fieldLabel: IRm$.r1("L_ALLOWED_USERS"),
									value: 5
								},
								{
									xtype: "textfield",
									name: "reg_date",
									fieldLabel: IRm$.r1("L_REG_DATE"),
									editable: false
								},
								{
									xtype: "datefield",
									fieldLabel: IRm$.r1("L_EXPIRE_ON"), // Expire on",
									name: "expire_date",
									required: true
								}
							]
						},
						{
							xtype: "container",
							height: 10
						}
					]
				}
			],
			buttons: [
				{
					xtype: "button",
					iconCls: 'icon-toolbar-activate',
					flex: 1,
					tooltip: IRm$.r1("L_ACTIVATE"),
					hidden: me.mts && me.mts.get("pstatus") == "A",
					handler: function() {
						this.setTenantActive("activate");
					},
					scope: me
				},
				{
					xtype: "button",
					iconCls: 'icon-toolbar-deprivation',
					flex: 1,
					tooltip: IRm$.r1("L_DEPRIVATE"),
					hidden: me.mts && me.mts.get("pstatus") == "A",
					handler: function() {
						this.setTenantActive("delete");
					},
					scope: me
				},
				{
					text: IRm$.r1("L_ERASE_TENANT"),
					name: "btn_erase",
					hidden: me.mts.get("pstatus") == "1" || me.mts.get("pstatus") == "A" ? true : false,
					handler: function() {
						this.setTenantActive("erase");
					},
					scope: me
				},
				"->",
				{
					text: IRm$.r1('B_CONFIRM'),
					handler: function() {
						this.setTenantActive("rename");
					},
					scope: me
				}, 
				{
					text: IRm$.r1('B_CANCEL'),
					handler:function() {
						this.close();
					},
					scope: me
				}
			],
			listeners: {
				afterrender: function(ui) {
					var me = this;
					if (me.mts)
					{
						var turl = me.down("[name=turl]"),
							uname = me.mts.get("name");
						
						me.setAllValues(me.mts.data);
						
						uname = uname.split(" ");
						uname = uname.join("%20");
						
						turl.setValue("http://hostname.com/amplix/?lang=en_US&mts=" + uname);
						
						me.loadTemplates();
					}
				}
			}
		});
		
		IG$.TenantSetting.superclass.initComponent.apply(me, arguments);
	}
});
