IG$.mgrRevision = $s.extend(IG$.v$pnl, {
	scroll: false,
	closable: true,
	layout: "border",
	
	iconCls: "icon-group",
	
	loadUID: function(uid) {
		var me = this,
			req = new IG$._rpc$();
		
		req.init(me, 
			{
				ack: "helpcontent",
				payload: {uid: uid},
				mbody: {option: 'translate'}
			}, me, function(jdoc) {
				if (jdoc && jdoc.length)
				{
					me.metaobj = jdoc[0];
					me.applyMetaObj.call(me);
				}
			}, false);
			
		req.send();
	},
	
	applyMetaObj: function() {
		var me = this,
			metaobj = me.metaobj,
			typename = metaobj.type.toLowerCase(),
			f_1 = me.down("[name=f_1]"),
			f_2 = me.down("[name=f_2]"),
			_f1 = me.down("[name=_f1]"),
			isfolder;
		
		isfolder = me.checkFolderType(typename);
		me.folderhierarchy = [];
		
		f_1.setVisible(isfolder != 0);
		
		if (isfolder != 0)
		{
			me.folderhierarchy.push(metaobj);
			_f1.setValue(metaobj.name);
			f_2.store.loadData([]);
			isfolder == 2 && me.down("[name=_f3]").show();
			
			this.loadFolderContent();
		}
		else
		{
			me.loadRevision(metaobj);
		}
	},
	
	loadRevision: function(metaobj) {
		var me = this,
			req = new IG$._rpc$();
		
		me._loaded_obj = metaobj;
			
		req.init(me, 
			{
				ack: "revision",
				payload: {uid: metaobj.uid, type: metaobj.type},
				mbody: {option: 'get'}
			}, me, function(jdoc) {
				var tnodes,
					minfo,
					f_3 = me.down("[name=f_3]"),
					f_4 = me.down("[name=f_4]"),
					f_5 = me.down("[name=f_5]"),
					f_6 = me.down("[name=f_6]"),
					f_7 = me.down("[name=f_7]"),
					dt = [], d,
					i;
				
				if (jdoc)
				{
					minfo = jdoc;
					f_4.setValue(minfo.name);
					f_5.setValue(minfo.nodepath);
					f_6.setValue(minfo.description);
					f_7.setValue(minfo.revision);
					
					tnode = jdoc.revisions;
					
					if (tnode)
					{
						for (i=0; i < tnode.length; i++)
						{
							d = tnode[i];
							d.uid = minfo.uid;
							d.iscurrent = (minfo.revision == d.revision) ? "YES" : "";
							d.writable = minfo.writable;
							dt.push(d);
						}
					}
					minfo._history = dt;
					me.minfo = minfo;
					f_3.store.loadData(dt);
				}
			}, false);
			
		req.send();
	},
	
	updateRevision: function() {
		var me = this,
			minfo = me.minfo,
			f_7 = me.down("[name=f_7]"),
			nrev,
			bf = false,
			i,
			req;
		
		f_7.clearInvalid();
		
		if (minfo)
		{
			nrev = f_7.getValue();
			for (i=0; i < minfo._history.length; i++)
			{
				if (minfo._history[i].revision == nrev)
				{
					bf = true;
					break;
				}
			}
			
			if (bf == false)
			{
				f_7.markInvalid("Not valid revision");
				IG$.ShowError(IRm$.r1("E_INVALID_REV"), me);
				return;
			}
			
			req = new IG$._rpc$();
		
			req.init(me, 
				{
					ack: "revision",
					payload: {uid: minfo.uid, revision: "" + nrev, type: minfo.type},
					mbody: {option: 'setrevision'}
				}, me, function(jdoc) {
					me.loadRevision(minfo);
					IG$.alertmsg(ig$.appname, IRm$.r1("M_SAVED"), null, null, 0, "success");
				}, false);
				
			req.send();
		}
	},
	
	loadFolderContent: function() {
		var me = this,
			metaobj = me.metaobj,
			req = new IG$._rpc$(),
			_f1 = me.down("[name=_f1]");
		
		_f1.setValue(metaobj.name);
		
		req.init(me, 
			{
				ack: "content",
				payload: {uid: metaobj.uid},
				mbody: {subfolder: "T"}
			}, me, function(jdoc) {
				var tnodes = jdoc ? jdoc.children : null,
					folderhierarchy = me.folderhierarchy,
					i,
					dp = [], p,
					f_2 = me.down("[name=f_2]");

				if (tnodes)
				{
					for (i=0; i < tnodes.length; i++)
					{
						p = tnodes[i];
						dp.push(p);
					}
				}
				
				f_2.store.loadData(dp);
			}, false);
			
		req.send();
	},
	
	checkFolderType: function(typename) {
		var r = 0;
		
		if (typename == "cubemodel")
		{
			r = 0;
		}
		else if ((/folder|javapackage/).test(typename))
		{
			r = 1;
		}
		else if ((/datacube|cube|metrics|mcube|nosql|sqlcube|mdbcube/).test(typename))
		{
			r = 2;
		}
		
		return r;
	},
	
	updateRevisionDesc: function(rec) {
		var me = this,
			req = new IG$._rpc$();
			
		req.init(me, 
			{
				ack: "revision",
				payload: {uid: rec.get("uid"), revision: "" + rec.get("revision")},
				mbody: {option: "updatedesc", name: "desc", value:rec.get("description")}
			}, me, function(xdoc) {
			}, false);
			
		req.send();
	},
	
	openRevision: function(row, col) {
		var me = this,
			f_3 = me.down("[name=f_3]"),
			minfo = me.minfo,
			rec = f_3.store.data.items[row],
			mp = IG$.mainPanel;
			
		if (mp && minfo && rec && rec.get("uid"))
		{
			mp.navigateApp.call(mp, minfo.uid, minfo.type.toLowerCase(), 
				minfo.name, minfo.nodepath, true, minfo.writable, 
				{revision: "" + rec.get("revision")}
			);
		}
	},
	
	removeItem: function(row, col) {
		var me = this,
			f_3 = me.down("[name=f_3]"),
			rec = f_3.store.data.items[row],
			req;
			
		if (rec && rec.get("uid"))
		{
			req = new IG$._rpc$();
			
			req.init(me, 
				{
					ack: "revision",
					payload: {uid: rec.get("uid"), revision: "" + rec.get("revision")},
					mbody: {option: "clear_rev"}
				}, me, function(xdoc) {
					if (me._loaded_obj)
					{
						me.loadRevision(me._loaded_obj);
					}
				}, false);
				
			req.send();
		}
	},
	
	initComponent: function(){
		var me = this;
		
		me.title = IRm$.r1('T_REVISION');

		ig$.$l_ = ig$.$l_ || 0;
		
		$s.apply(this, {
			border: 0,
			bodyPadding: 5,
			
			items: [
				{
					xtype: "panel",
					title: IRm$.r1("L_LIST_OF_ITEMS"), // "List of Items",
					region: "west",
					width: 420,
					
					name: "f_1",
					layout: {
						type: "vbox",
						align: "stretch"
					},
					hidden: true,
					split: true,
					collapseMode: "mini",
					collapsible: true,
					padding: 5,
					items: [
						{
							xtype: "fieldset",
							title: IRm$.r1("L_FOLDER_INFO"), // "Folder Info",
							layout: {
								type: "hbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "textfield",
									flex: 1,
									fieldLabel: null,
									readOnly: true,
									name: "_f1"
								},
								{
									xtype: "button",
									text: IRm$.r1("B_UP"),
									hidden: true,
									name: "_f2",
									handler: function() {
										var me = this,
											folderhierarchy = me.folderhierarchy,
											_f2 = me.down("[name=_f2]"),
											_f3 = me.down("[name=_f3]"),
											isfolder;
											
										folderhierarchy.splice(folderhierarchy.length - 1, 1);
										me.metaobj = folderhierarchy[folderhierarchy.length - 1];
										
										isfolder = me.checkFolderType(me.metaobj.type.toLowerCase());
										
										_f2.setVisible(folderhierarchy.length > 1);
										_f3.setVisible(isfolder == 2);
										
										me.loadFolderContent();
										(isfolder == 2) && me.loadRevision(me.metaobj);
									},
									scope: this
								},
								{
									xtype: "button",
									text: "->",
									hidden: true,
									name: "_f3",
									handler: function() {
										var me = this,
											minfo = me.folderhierarchy[me.folderhierarchy.length - 1];
											
										minfo && me.loadRevision(minfo);
									},
									scope: this
								}
							]
						},
						{
							xtype: "fieldset",
							title: IRm$.r1("B_SEARCH"),
							hidden: true,
							layout: {
								type: "vbox",
								align: "stretch"
							},
							defaults: {
								labelWidth: 70
							},
							items: [
								{
									xtype: "textfield",
									fieldLabel: IRm$.r1("B_KEYWORD") // "Keyword"
								},
								{
									xtype: "combobox",
									queryMode: "local",
									displayField: "name",
									valueField: "value",
									editable: false,
									fieldLabel: IRm$.r1("L_LAST_MODIFIED"), // "Last Modified",
									value: "",
									store: {
										xtype: "store",
										fields: ["name", "value"],
										data: [
											{name: IRm$.r1("B_SELECT"), value: ""},
											{name: "1 Day", value: "1d"},
											{name: "2 Day", value: "2d"},
											{name: "3 Day", value: "3d"},
											{name: "1 Week", value: "1w"}
										]
									}
								},
								{
									xtype: "fieldcontainer",
									layout: "hbox",
									items: [
										{
											xtype: "container",
											flex: 1
										},
										{
											xtype: "button",
											text: IRm$.r1("L_RUN"),
											handler: function() {
												this.loadFolderContent();
											},
											scope: this
										}
									]
								}
							]
						},
						{
							xtype: "gridpanel",
							name: "f_2",
							store: {
								xtype: "store",
								fields: [
									"name", "nodepath", "revision", "updatedate", "updatedatefm", "uid", "type"
								]
							},
							flex: 1,
							selType: "checkboxmodel",
							selModel: {
								checkSelector: ".x-grid-cell",
								mode: "SINGLE"
							},
							$v: {
								scrollX: true
							},
							columns: [
								{
									text: IRm$.r1("B_NAME"),
									dataIndex: "name",
									minWidth: 120
								},
								{
									text: IRm$.r1("B_REVISON"),
									dataIndex: "revision",
									minWidth: 80
								},
								{
									text: IRm$.r1("B_PATH"),
									dataIndex: "nodepath",
									flex: 1,
									minWidth: 300
								},
								{
									text: IRm$.r1("L_LAST_UPD"),
									dataIndex: "updatedatefm",
									minWidth: 160
								}
							],
							listeners: {
								cellclick: function(tobj, td, cellindex, record, rowindex, e) {
									var me = this,
										uid = record.get("uid"),
										typename = record.get("type"),
										isfolder,
										_f2 = me.down("[name=_f2]"),
										_f3 = me.down("[name=_f3]");
									
									isfolder = me.checkFolderType(typename.toLowerCase());
									
									if (isfolder == 0)
									{
										me.loadRevision(record.data);
									}
									else
									{
										me.metaobj = {
											uid: uid,
											name: record.get("name"),
											type: typename,
											nodepath: record.get("nodepath")
										};
										me.folderhierarchy.push(me.metaobj);
										_f2.setVisible(me.folderhierarchy.length > 1);
										_f3.setVisible(isfolder == 2);
										me.loadFolderContent();
										(isfolder == 2) && me.loadRevision(me.metaobj);
									}
								},
								scope: this
							}
						}
					]
				},
				{
					xtype: "panel",
					region: "center",
					title: IRm$.r1("L_REVISION_MGR"), // "Revision Management",
					flex: 1,
					padding: 5,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					bbar: [
						"->",
						{
							xtype: "button",
							text: IRm$.r1("L_TRASHBIN"),
							hidden: ig$.$l_ > 5,
							handler: function() {
								IG$.procMenuCommand("CMD_TRASHBIN");
							},
							scope: this
						}
					],
					items: [
						{
							xtype: "fieldset",
							title: IRm$.r1("L_REVISION_INFO"), // "Information",
							layout: {
								type: "hbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "panel",
									layout: "anchor",
									flex: 1,
									border: 1,
									padding: "0 5 0",
									bodyPadding: 4,
									defaults: {
										labelAlign: "right"
									},
									items: [
										{
											xtype: "textfield",
											fieldLabel: IRm$.r1("B_NAME"),
											readOnly: true,
											name: "f_4"
										},
										{
											xtype: "textfield",
											anchor: "100%",
											fieldLabel: IRm$.r1("B_LOCATION"), // "Location",
											readOnly: true,
											name: "f_5"
										},
										{
											xtype: "textarea",
											anchor: "100%",
											height: 120,
											fieldLabel: IRm$.r1("B_DESC"),
											readOnly: true,
											name: "f_6"
										},
										{
											xtype: "fieldset",
											title: IRm$.r1("L_CUR_REVISION"), // "Current Revision",
											layout: {
												type: "hbox",
												align: "stretch",	
											},
											items: [
												{
													xtype: "numberfield",
													minValue: 0,
													name: "f_7"
												},
												{
													xtype: "button",
													text: IRm$.r1("L_CHANGE_REV"),
													handler: function() {
														this.updateRevision();
													},
													scope: this
												}
											]
										}
									]
								}
							]
						},
						{
							xtype: "fieldcontainer",
							layout: {
								type: "hbox"
							},
							items: [
								{
									xtype: "button",
									text: IRm$.r1("B_REFRESH"),
									width: 80,
									margin: "0 4 0",
									handler: function() {
										var me = this;
										if (me._loaded_obj)
										{
											me.loadRevision(me._loaded_obj);
										}
									},
									scope: this
								},
								{
									xtype: "button",
									text: IRm$.r1("B_CLEAR"),
									width: 80,
									hidden: ig$.$l_ > 5,
									handler: function() {
										var me = this,
											req;
										
										if (me._loaded_obj)
										{
											req = new IG$._rpc$();
			
											req.init(me, 
												{
													ack: "revision",
													payload: {uid: me._loaded_obj.uid, type: me._loaded_obj.type},
													mbody: {option: "clear_hist"}
												}, me, function(xdoc) {
													if (me._loaded_obj)
													{
														me.loadRevision(me._loaded_obj);
													}
												}, false);
												
											req.send();
										}
									},
									scope: this
								},
								{
									xtype: "displayfield",
									flex: 1,
									hidden: ig$.$l_ > 5,
									value: "Clear previous revision history",
									padding: "0 5 0"
								},
								{
									xtype: "container",
									flex: 1
								}
							]
						},
						{
							xtype: "gridpanel",
							name: "f_3",
							selType: ig$.$l_ > 5 ? null : "checkboxmodel",
							selModel: {
								checkSelector: ".x-grid-cell"
							},
							store: {
								xtype: "store",
								fields: [
									"uid", "revision", "updatedate", "updatedatefm", "description", "username", "userid", "iscurrent", "writable"
								]
							},
							defaults: {
								menuDisabled: true
							},
							plugins: [
								{
									ptype: "cellediting",
									clicksToEdit: 1
								}
							],
							columns: [
								{
									text: IRm$.r1("B_REVISION"),
									dataIndex: "revision"
								},
								{
									text: IRm$.r1("B_LASTUPD"),
									dataIndex: "updatedatefm",
									width: 150
								},
								{
									text: IRm$.r1("B_MODI"),
									dataIndex: "username"
								},
								{
									text: IRm$.r1("B_DESC"),
									dataIndex: "description",
									flex: 1,
									hidden: ig$.$l_ > 5,
									editor: {
										xtype: "textfield",
										allowBlank: true
									}
								},
								{
									text: IRm$.r1("B_CURRENT"),
									dataIndex: "iscurrent"
								},
								{
									xtype: "actioncolumn",
									width: 50,
									menuDisabled: true,
									hidden: ig$.$l_ > 5,
									items: [
										{
											iconCls: "icon-grid-add",
											tooltip: "Open This Revision",
											handler: function (grid, rowIndex, colIndex) {
												me.openRevision.call(me, rowIndex, colIndex);
											}
										},
										{
											iconCls: "icon-grid-delete",
											tooltip: "Remove History",
											handler: function (grid, rowIndex, colIndex) {
												me.removeItem.call(me, rowIndex, colIndex);
											}
										}
									]
								}
							],
							listeners: {
								edit: function(editor, e, eopts) {
									var me = this,
										rec = e.record;
									
									me.updateRevisionDesc(rec);
								},
								scope: this
							}
						}
					]
				}
			]
		});
		
		IG$.mgrRevision.superclass.initComponent.call(this);
	},
	listeners: {
		afterrender: function(tobj) {
			var me = this;
			me.loadUID(me.uid);
		}
	}
});