/* file: codemapping.js */

IG$.cmwzd = $s.extend($s.window, {
	
	modal: true,
	layout: "fit",
	
	closable: false,
	resizable:false,
	width: 540,
	height: 480,
	
	_confirm: function() {
		var me = this,
			g1 = me.down("[name=g1]"),
			sel = g1.getSelectionModel().selected,
			rec,
			seq = 0,
			slen = sel.items.length;
			
		$.each(sel.items, function(i, item) {
			var req = new IG$._rpc$(),
				dt = item.data,
				exitem = dt.exitem,
				payload,
				mbody;
			
			if (exitem)
			{
				payload = {uid: exitem.uid, type: "CodeMap"};
			}
			else
			{
				payload = {pid: me.s1.get("uid"), name: dt.name, type: "CodeMap"};
			}
			
			mbody = {
				meta_type: "code_mapping",
				datasource: dt.dsource,
				attributes: {
					attrb1: dt.name
				},
				dtype: "sql",
				sql_data: dt.sql,
				uid: exitem ? exitem.uid : null,
				type: "CodeMap"
			};
			
			req.init(me, 
				{
					ack: "writecontent",
					payload: payload,
					mbody: mbody,
					meta_type: "code_mapping"
				}, me, function(jdoc) {
					seq++;
					
					if (seq == slen)
					{
						me.close();
					}
				}, null);
			req.send();
		});
	},
	
	_init: function() {
		this._getDsource();
	},
	
	execq: function() {
		var me = this,
			lt_0 = me.down("[name=lt_0]"),
			value = {},
			req = new IG$._rpc$(),
			mbody = {};
		
		if (lt_0.getAllValues(value, false))
		{
			return;
		}
		
		mbody.dbpool = value.cbdatasource;
		mbody.sql = value.cbsql;
		
		req.init(me, 
			{
				ack: "olapservice",
				payload: {option: "sqlcubemeta"},
				mbody: mbody,
				meta_type: "inlineview"
			}, me, function(jdoc) {
				var me = this,
					tb,
					col,
					i,
					names = [],
					cbflds = me.down("[name=cbflds]");
					
				if (jdoc.length)
				{
					tb = jdoc[0];
					col = tb.columns;
					for (i=0; i < col.length; i++)
					{
						names.push(col[i].name);
					}
					
					cbflds.setValue(names.join(","));
					
					mbody.columns = col;
					
					me._execdata(mbody, value.cbgroupfield);
				}
			}, false);
		req.send();
	},
	
	_execdata: function(mbody, gfield) {
		var me = this,
			lt_0 = me.down("[name=lt_0]"),
			value = {},
			req = new IG$._rpc$(),
			dmbody = {dbpool: mbody.dbpool},
			mheaders = [],
			i;
			
		dmbody.sql = "SELECT DISTINCT " + gfield + " FROM (" + mbody.sql + ") t01";
		dmbody.columns = [];
		
		gfield = gfield.toLowerCase();
		
		for (i=0; i < mbody.columns.length; i++)
		{
			if (mbody.columns[i].name.toLowerCase() == gfield)
			{
				dmbody.columns.push(mbody.columns[i]);
			}
			
			if (i < 4)
			{
				mheaders.push(mbody.columns[i].name);
			}
		}
				
		req.init(me, 
			{
				ack: "tablecontent",
				payload: {option: "preview"},
				mbody: dmbody,
				meta_type: "inlineview"
			}, me, function(jdoc) {
				var me = this,
					g1 = me.down("[name=g1]"),
					res = jdoc, 
					rs, hd, gcol = -1,
					data = [], m,
					i,
					_scontent = me._scontent,
					smap = {},
					attrb;
					
				if (res.headers && res.headers.length)
				{
					for (i=0; i < _scontent.length; i++)
					{
						attrb = _scontent[i].attributes.attrb1;
						
						if (attrb)
						{
							smap[attrb] = _scontent[i];
						}
					}
					hd = res.headers;
					
					for (i=0; i < hd.length; i++)
					{
						if (hd[i].name.toLowerCase() == gfield)
						{
							gcol = i;
						}
					}
					
					if (gcol > -1)
					{
						rs = res.inline_data;
						for (i=0; i < rs.length; i++)
						{
							m = {
								name: rs[i][gcol],
								dsource: mbody.dbpool
							};
							
							m.sql = "SELECT DISTINCT " + mheaders.join(", ") + "\n\n FROM (\n"
								  + mbody.sql + "\n\n"
								  + ") t01 WHERE \n"
								  + gfield + "='" + m.name + "'";
								  
							m.exitem = smap[m.name];
								  
							data.push(m);
						}
					}
				}
				
				g1.store.loadData(data);
			}, false);
		req.send();
	},
	
	_getDsource: function() {
		var me = this,
			req = new IG$._rpc$();
		
		req.init(me, 
			{
				ack: "tablecontent",
				payload: {address: "/"},
				mbody: {option: "standard"}
			}, me, function(jdoc) {
				var me = this,
					d,
					dp = [],
					isadmin = IG$.dlgLogin.jS2,
					cbdatasource = me.down("[name=cbdatasource]"),
					i, tnodes, tnode;
				
				if (jdoc && jdoc.length)
				{
					tnodes = jdoc;
					
					dp.push({dispname:"-- Select Pool --", uid:""}); 
					
					for (i=0; i < tnodes.length; i++)
					{
						d = tnodes[i];
						d.dispname = d.name;
						dp.push(d);
					}
					
					cbdatasource.store.loadData(dp);
					cbdatasource.setValue("");
				}
				
				me._getMeta();
			}, false);
		req.send();
	},
	
	_getMeta: function() {
		var me = this,
			req = new IG$._rpc$(),
			s1 = me.s1;
		
		req.init(me, 
			{
				ack: "content",
				payload: {uid: s1.get("uid")},
				mbody: {option: "folder"}
			}, me, function(jdoc) {
				var me = this;
				me._scontent = jdoc.children || [];
			}, false);
		req.send();
	},
	
	initComponent : function() {
		var me = this;
				
		$s.apply(me, {
			title: IRm$.r1("T_CODE_MAPPING"),
			
			bodyStyle: "padding:5px; background: #ffffff",
			
			items: [
				{
					xtype: "panel",
					padding: "5 5",
					border: 0,
					autoScroll: true,
					
					layout: {
						type: "vbox",
						align: "stretch"
					},
					
					items: [
						{
							xtype: "fieldset",
							title: IRm$.r1("L_STATIC_DATA_VALUES"), // "Static Data Values",
							name: "lt_0",
							flex: 1,
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "combobox",
									name: "cbdatasource",
									required: true,
									queryMode: "local",
									fieldLabel: IRm$.r1("L_DATASOURCE"),
									displayField: "dispname",
									valueField: "uid",
									editable: false,
									autoSelect: false,
									store: {
										xtype: "store",
										fields: []
									}
								},
								{
									xtype: "textfield",
									fieldLabel: "Group Field",
									required: true,
									name: "cbgroupfield",
									value: "GROUP"
								},
								{
									xtype: "textarea",
									name: "cbsql",
									required: true,
									flex: 1,
									fieldLabel: "SQL",
									value: "SELECT CODE, VALUE, SORT, GROUP FROM TABLE"
								},
								{
									xtype: "container",
									layout: {
										type: "hbox",
										align: "stretch"
									},
									items: [
										{
											xtype: "container",
											flex: 1
										},
										{
											xtype: "button",
											text: "Execute",
											handler: function() {
												this.execq();
											},
											scope: this
										}
									]
								},
								{
									xtype: "textfield",
									name: "cbflds",
									value: "",
									readOnly: true,
									flex: 1,
									fieldLabel: "Columns"
								},
								{
									xtype: "gridpanel",
									name: "g1",
									height: 400,
									store: {
										xtype: "store",
										fields: []
									},
									selType: "checkboxmodel",
									selModel: {
										checkSelector: ".x-grid-cell",
										mode: "MULTI"
									},
									columns: [
										{
											text: IRm$.r1("B_NAME"),
											dataIndex: "name",
											tdCls: "igc-td-link"
										},
										{
											text: IRm$.r1("B_DESC"),
											dataIndex: "sql",
											flex: 1
										}
									],
									tbar: [
										"->",
										{
											xtype: "button",
											text: "Register Selected",
											handler: function() {
												this._confirm();
											},
											scope: this
										}
									]
								}
							]
						}
					]
				}
			],
			
			buttons:[
				
				{
					text: IRm$.r1("B_CANCEL"),
					handler:function() {
						this.close();
					},
					scope: this
				}
			],

			listeners: {
				afterrender: function() {
					this._init();
				}
			}
		});
		
		IG$.cmwzd.superclass.initComponent.apply(this, arguments);
	}
});

IG$.systemlookup = $s.extend(IG$.v$pnl, {
	scroll: false,
	initialized: false,
	closable: true,
	layout: "fit",
	bodyPadding: 5,
	iconCls: "icon-ing-docdef",
	
	init_f: function() {
		var me = this,
			mtree,
			m_navi = this.down("[name=m_navi]"),
			req = new IG$._rpc$();
		
		req.init(me, 
			{
				ack: "helpcontent",
				payload: {uid: "/SYS_Lookup", name: "SYS_Lookup", type: "Folder", post: "create", duty: "everyone", mode: "s"},
				mbody: {option: "translate"}
			}, me, function(jdoc) {
				var tuid = jdoc && jdoc.length ? jdoc[0].uid : null;
					
				if (tuid)
				{
					me.tuid = tuid;
					
					mtree = new IG$.n$navTreeCom({
						name: "mtree",
						rootuid: tuid,
						btype: "codemap",
						rootname: "Code Mapping",
						visibleItems: "folder;",
						showCustomMenu: function(rec) {
							var me = this,
								menu = this.customMenu,
								typename = rec.get("type").toLowerCase(),
								itemname = rec.get("name"),
								itemaddr = rec.get("nodepath"),
								itemuid = rec.get("uid"),
								itemtype = rec.get("type"),
								writable = (rec.get("writable") || rec.get("manage")) ? true : false,
								manageable = (rec.get("manage")) ? true : false,
								r = false;
								
							menu.removeAll();
															
							if (writable || manageable)
							{
								r = true;
								menu.add({
									text: IRm$.r1("B_FOLDER"),
									hidden: ig$.features["ig_n_f_f"] !== false,
									handler: function() {
										this._createMetaObject.call(this, "Folder", itemuid, itemaddr, rec);
										  },
										  scope: this
								});
								
								menu.add({
									text: IRm$.r1("L_RENAME_ITEM", IG$._gitemname(itemname)),
									hidden: ig$.features["ig_n_ren"] !== false,
									handler: function() {
										this._renameMetaObject.call(this, itemname, itemuid, itemaddr, record);
										  },
										  scope: this
								});
								menu.add({
									text: IRm$.r1("L_DELETE_ITEM", IG$._gitemname(itemname)),
									hidden: ig$.features["ig_n_del"] !== false,
									handler: function() {
										this.deleteMetaObject.call(this, itemuid, record);
										  },
										  scope: this
								});
							}
							
							return r;
						},
						custom_expand_func: new IG$.callBackObj(me, me.rs_custom_click_func),
						custom_click_func: new IG$.callBackObj(me, me.rs_custom_click_func)
					});
				
					m_navi.add(mtree);
				}
				
			});
		req.send();
	},
	
	rs_custom_click_func: function(node) {
		var me = this,
			t_btn = me.down("[name=t_btn]"),
			b_n_lk = me.down("[name=b_n_lk]"),
			b_mv = me.down("[name=b_mv]"),
			b_wzd = me.down("[name=b_wzd]");
					
		t_btn.setDisabled(true);
		b_n_lk.setDisabled(true);
		b_mv.setDisabled(true);
		b_wzd.setDisabled(true);
		
		me._s1 = node;
		
		if (node.get("writable") || node.get("manageable"))
		{
			t_btn.setDisabled(false);
			if (!node.isRoot())
			{
				b_n_lk.setDisabled(false);
				b_mv.setDisabled(false);
				b_wzd.setDisabled(false);
			}
		}
		
		me.load_content(node);
	},
	
	load_content: function(node) {
		var panel = this,
			req = new IG$._rpc$();
			
		req.init(panel, 
			{
				ack: "content",
				payload: {uid: node.get("uid")},
				mbody: {}
			}, panel, function(jdoc) {
				var tnode = jdoc,
					tnodes = (tnode) ? tnode.children : null,
					dp = [],
					g1 = this.down("[name=g1]"),
					i, p;
				
				if (tnodes)
				{
					for (i=0; i < tnodes.length; i++)
					{
						p = tnodes[i];
						
						if (p.type == "CodeMap")
						{
							dp.push(p);
						}
					}
				}
				g1.store.loadData(dp);
			}, false);
		req.send();
	},
	
	createItem: function(itemtype) {
		var me = this,
			mtree = me.down("[name=mtree]");
		
		if (me._s1)
		{
			if (itemtype == "CodeMap" && me._s1.isRoot())
			{
				IG$.alertmsg(ig$.appname, IRm$.r1("M_SELECT_FOLDER"), null, null, 1, "warning");
				return;
			}
			mtree._createMetaObject.call(mtree, itemtype, me._s1.get("uid"), me._s1.get("nodepath"), me._s1);
		}
		else
		{
			IG$.alertmsg(ig$.appname, IRm$.r1("M_SELECT_FOLDER"), null, null, 1, "warning");
		}
	},
	
	toolbarHandler: function(cmd) {
		var me = this,
			g1,
			sel, rec,
			itemcnt = 0,
			iscopy = false;
			
		switch (cmd)
		{
		
		case "cmd_move":
			g1 = me.down("[name=g1]");
			sel = g1.getSelectionModel().selected;
			
			if (sel && sel.length == 1)
			{
				var me = this,
					dlgitemsel = new IG$.metaSelectDlg({
						visibleItems: "folder",
						targetobj: "folder",
						tree_options: {
							cubebrowse: false,
							rootuid: me.tuid
						},
						callback: new IG$.callBackObj(me, function(item) {
							if (item)
							{
								IG$.confirmMessages(IRm$.r1("B_CONFIRM"), IRm$.r1("B_CD_MHS", item.name), function(dlg) {
									if (dlg == "yes")
									{
										address = [];
										itemcnt = 0;
										
										for (i=0; i < sel.length; i++)
										{
											rec = sel.items[i];
											if (rec.get("writable") || rec.get("manage"))
											{
												address.push({uid: rec.get("uid")});
												itemcnt++;
											}
										}
										
										if (itemcnt > 0)
										{
											req = new IG$._rpc$();
											req.init(me, 
												{
													ack: "helpcontent",
													payload: address,
													mbody: {option: (iscopy == true ? "copy" : "move"), target: item.uid}
												}, me, function(xdoc) {
													var me = this,
														mtree = me.down("[name=mtree]"),
														tnode = mtree.getRootNode();
														
													mtree.refreshNode.call(mtree, tnode);
												}, null, null);
											req.send();
										}
									}
								});
							}
						})
					});
				IG$.checkLogin(this, dlgitemsel);
			}
			else
			{
				IG$.alertmsg(ig$.appname, IRm$.r1("M_SEL_SINGLE"), null, null, 1, "warning");
			}
			break;
		case "cmd_export_meta":
			IG$.confirmMessages(IRm$.r1("T_EXP_MC"), IRm$.r1("T_EXP_MC_Q"), function(dlg) {
				var panel = me,
					meta,
					req = new IG$._rpc$(),
					i, rec,
					payload;
				
				meta = {
					uid: panel.tuid,
					name: "SYS_Lookup",
					type: "Folder",
					nodepath: "/SYS_Lookup"
				};
					
				if (meta && meta.uid && dlg == "yes")
				{
					payload = {
						uid: meta.uid,
						nodepath: meta.nodepath,
						name: meta.name,
						type: meta.type
					};
					req.init(panel, 
						{
							ack: "content",
							payload: payload,
							mbody: {output: "file"}
						}, panel, function(jdoc) {
							var fpath, filename;
							
							if (jdoc)
							{
								fpath = jdoc.luid;
								filename = jdoc.filename;
								
								if (fpath && filename)
								{
									$.download(ig$.servlet, [
										{name: "ack", value: "download"},
										{name: "payload", value: fpath},
										{name: "mbody", value: filename},
										{name: "_mts_", value: (IG$._g$a || "")}
									], "POST");
								}
							}
						}, null);
					req.send();
				}
			}, me, me);
			break;
		case "cmd_wzd":
			var me = this,
				dlg;
			dlg = new IG$.cmwzd({
				s1: me._s1
			});
			IG$.checkLogin(me, dlg);
			break;
		}
	},
	
	initComponent: function(){
		var me = this;
		
		me.items = [
			{
				xtype: "panel",
				layout: "border",
				border: false,
				items: [
					{
						xtype: "panel",
						layout: "fit",
						region: "west",
						collapsed: false,
						width: 200,
						title: IRm$.r1("L_NAVIGATOR"), // "Navigator",
						name: "m_navi",
						tbar: [
							{
								xtype: "button",
								name: "t_btn",
								text: IRm$.r1("B_CR"),
								disabled: true,
								menu: {
									xtype: "menu",
									items: [
										{
											text: IRm$.r1("B_FOLDER"),
											hidden: ig$.features["ig_n_f_f"] !== false, 
											handler: function() {
												this.createItem("Folder");
											},
											scope: this
										},
										"-",
										{
											text: IRm$.r1("L_CODEMAPPING"),
											hidden: ig$.features["ig_n_lk"] !== false, 
											handler: function() {
												this.createItem("CodeMap");
											},
											scope: this
										}
									]
								}
							},
							{
								xtype: "button",
								iconCls: "icon-refresh",
								tooltip: IRm$.r1("L_REFRESH"),
								handler: function() {
									var me = this,
										mtree = me.down("[name=mtree]"),
										tnode = mtree.getRootNode();
										
									mtree.refreshNode.call(mtree, tnode);
								},
								scope: this
							}
						],
						items: [
							
						]
					},
					{
						xtype: "panel",
						region: "center",
						flex: 1,
						layout: "fit",
						title: IRm$.r1("L_S_LK"),
						tbar: [
							{
								text: IRm$.r1("L_CODEMAPPING"),
								hidden: ig$.features["ig_n_lk"],
								name: "b_n_lk",
								disabled: true,
								handler: function() {
									this.createItem("CodeMap");
								},
								scope: this
							},
							{
								xtype: "button",
								text: IRm$.r1("L_MOVE"),
								name: "b_mv",
								hidden: ig$.$l_ > 5,
								disabled: true,
								handler: function() {
									this.toolbarHandler("cmd_move");
								},
								scope: this
							},
							{
								xtype: "button",
								text: IRm$.r1("L_WIZARD"),
								name: "b_wzd",
								disabled: true,
								handler: function() {
									this.toolbarHandler("cmd_wzd");
								},
								scope: this
							},
							"->",
							{
								text: IRm$.r1("L_EXPORT_META"),
								hidden: ig$.$l_ > 5,
								handler: function() {
									this.toolbarHandler("cmd_export_meta");
								},
								scope: this
							}
						],
						items: [
							{
								xtype: "gridpanel",
								name: "g1",
								store: {
									xtype: "store",
									fields: [
										"uid", "name", "description", "nodepath", "type", "writable"
									]
								},
								selType: "checkboxmodel",
								selModel: {
									checkSelector: ".x-grid-cell",
									mode: "SINGLE"
								},
								columns: [
									{
										text: IRm$.r1("B_NAME"),
										dataIndex: "name",
										tdCls: "igc-td-link"
									},
									{
										text: IRm$.r1("B_DESC"),
										dataIndex: "description",
										flex: 1
									},
									{
										xtype: "actioncolumn",
										width: 60,
										menuDisabled: true,
										items: [
											{
												iconCls: "icon-grid-config",
												tooltip: IRm$.r1("L_EDIT"),
												handler: function (grid, rowIndex, colIndex) {
													var grd = grid,
														store = grd.store,
														rec = store.getAt(rowIndex),
														mp = IG$.mainPanel;
														
													if (mp)
													{
														mp.navigateApp.call(mp, rec.get("uid"), rec.get("type").toLowerCase(), rec.get("name"), rec.get("nodepath"), false, rec.get("writable"));
													}
												},
												scope: this
											},
											{
												iconCls: "icon-grid-delete",
												tooltip: IRm$.r1("L_REMOVE"),
												handler: function(grid, rowIndex, colIndex) {
													var g1 = grid,
														me = this,
														store = grid.store,
														rec = store.getAt(rowIndex);
														
													IG$.confirmMessages(ig$.appname, IRm$.r1("M_CONFIRM_DELETE"), function(e) {
														if (e == "yes")
														{
															var req = new IG$._rpc$();
															req.init(me, 
																{
																	ack: "deletecontent",
																	payload: {uid: rec.get("uid")},
																	mbody: {}
																}, me, function(xdoc) {
																	g1.store.remove(rec);
																}, null);
															req.send();
														}
													});
												},
												scope: this
											}
										]
									}
								],
								listeners: {
									cellclick: function(tobj, td, cellIndex, rec, rowIndex, e) {
										var me = this;
										
										if (cellIndex == 1)
										{
											var mp = IG$.mainPanel;
												
											if (mp)
											{
												mp.navigateApp.call(mp, rec.get("uid"), rec.get("type").toLowerCase(), rec.get("name"), rec.get("nodepath"), false, rec.get("writable"), {
													callback: new IG$.callBackObj(me, function() {
														if (me._s1)
														{
															me.rs_custom_click_func(me._s1);
														}
													})
												});
											}
										}
									},
									scope: this
								}
							}
						]
					}
				]
			}
		];
		
		this.listeners = {
			afterrender: function(tobj) {
				tobj.init_f.call(tobj);
			}
		}

		IG$.systemlookup.superclass.initComponent.call(this);
	}
});
