IG$.relationMgr = $s.extend($s.window, {
	modal: true,
	closable: false,
	resizable:true,
	layout: "fit",
	width: 600,
	autoHeight: true,
	
	layout: {
		type: 'fit'
	},
	
	bodyPadding: 0,
	
	loadRelation: function(uid, type, grd) {
		var me = this,
			lreq = new IG$._rpc$(),
			isdown = true,
			itype = type ? type.toLowerCase() : "";
			
		if (itype == "report")
		{
			isdown = false;
		}
		
		lreq.init(me, 
			{
				ack: "helpcontent",
				payload: {uid: uid},
				mbody: {option: "relations", isdown: (isdown ? "T" : "F"), detail: "info"}
			}, me, function(jdoc) {
				var me = this,
					grd_rel = me.down("[name=" + (grd || "grd_rel") + "]"),
					tnodes = jdoc && jdoc.children ? jdoc.children : [],
					i, d, dm,
					txtrel = me.down("[name=txtrel]"),
					dp = [];
				
				if (!uid)
				{
					txtrel.setValue("Relations : " + jdoc.name + "(" + jdoc.nodepath + ")");
				}
				for (i=0; i < tnodes.length; i++)
				{
					d = tnodes[i];
					d.iconcls = IG$.get_metatype_icon(d.type.toLowerCase(), d);
					dp.push(d);
				}
				
				grd_rel.store.loadData(dp);
			}, false);
			
		lreq.send();
	},
	
	initComponent: function(){
		var me = this;
		
		$s.apply(this, {
			title: IRm$.r1("T_Resources"),
			items: [
				{
					xtype: "panel",
					bodyPadding: 10,
					minHeight: 500,
					layout: {
						type: "vbox",
						align: "stretch"
					},
					items: [
						{
							xtype: "displayfield",
							name: "txtrel",
							value: "Relations"
						},
						{
							xtype: "gridpanel",
							name: "grd_rel",
							flex: 1,
							store: {
								xtype: "store",
								fields: [
									"name", "nodepath", "uid", "type", "iconcls"
								]
							},
							columns: [
								{
									xtype: "templatecolumn",
									text: IRm$.r1("B_NAME"),
									menuDisabled: true,
									flex: 1,
									minWidth: 160,
									tdCls: "ig-navi-namecol",
									renderer: function(value, metadata, record) {
										var r = [
											"<div class='ig-navi-itemicon " + (record.get("iconcls") || "") + "'></div>",
											"<span title='" + record.get("name") + "(" + record.get("type") + ")'>" + record.get("name") + "</span>"
										];
										
										return r.join("");
									}
									// tpl: "<div class='ig-navi-itemicon {iconcls}'></div><span title='{name} ({type})'>{name}</span>"
								},
								{
									text: IRm$.r1("B_PATH"),
									flex: 1,
									minWidth: 200,
									dataIndex: "nodepath"
								}
							],
							listeners: {
								cellclick: function(tobj, td, cellIndex, record, rowIndex, e) {
									var me = this,
										png_rel_sub = me.down("[name=png_rel_sub]"),
										uid = record.get("uid");
									
									if (cellIndex == 0)
									{
										png_rel_sub.show();
										me.loadRelation(uid, record.get("type"), "grd_rel_sub");
									}
								},
								scope: this
							}
						},
						{
							xtype: "panel",
							name: "png_rel_sub",
							flex: 1,
							hidden: true,
							layout: {
								type: "vbox",
								align: "stretch"
							},
							items: [
								{
									xtype: "displayfield",
									value: "Sub relations"
								},
								{
									xtype: "gridpanel",
									flex: 1,
									name: "grd_rel_sub",
									store: {
										xtype: "store",
										fields: [
											"name", "nodepath", "uid", "type", "iconcls"
										]
									},
									columns: [
										{
											xtype: "templatecolumn",
											text: IRm$.r1("B_NAME"),
											menuDisabled: true,
											flex: 1,
											minWidth: 160,
											tdCls: "ig-navi-namecol",
											renderer: function(value, metadata, record) {
												var r = [
													"<div class='ig-navi-itemicon " + (record.get("iconcls") || "") + "'></div>",
													"<span title='" + record.get("name") + "(" + record.get("type") + ")'>" + record.get("name") + "</span>"
												];
												
												return r.join("");
											}
											// tpl: "<div class='ig-navi-itemicon {iconcls}'></div><span title='{name} ({type})'>{name}</span>"
										},
										{
											text: IRm$.r1("B_PATH"),
											flex: 1,
											minWidth: 200,
											dataIndex: "nodepath"
										}
									]
								}
							]
						}
					]
				}
			],
			buttons: [
				{
					text: IRm$.r1("B_CONFIRM"),
					handler:function() {
						this.close();
					},
					scope: this
				},
				{
					text: IRm$.r1("B_CLOSE"),
					handler:function() {
						this.close();
					},
					scope: this
				}
			]
		});
		
		IG$.relationMgr.superclass.initComponent.call(this);
	},
	
	listeners: {
		afterrender: function(tobj) {
			var me = this;
			tobj.loadRelation(me.uid, me.type);
		}
	}
});